import QuasiElements, { Constructor as QuasiConstructor, Template as QuasiTemplate } from 'modules/nerdy-elements/core/quasi-elements';
// import { Constructor as QuasiConstructor, Template as QuasiTemplate } from 'modules/nerdy-elements/core/quasi-elements';

export * from './app-elements';
export * from './iron-elements';
export * from './paper-elements';

export type DomRepeat = PolymerElements.DomRepeat;
export let DomRepeat = QuasiConstructor<DomRepeat>('dom-repeat');

export class Repeat implements Partial<DomRepeat> { // extends DomRepeat { // (Object as any as DomRepeat)
    constructor() {
        const element = new DomRepeat() as DomRepeat & Repeat;
        const template = new QuasiTemplate();
        element.appendChild(template);
        element.appendChild = <T extends Node>(node: T): T => (QuasiTemplate.isTemplate(node) ? template.replace(node) : template.append([node]), node);
        return element as any;
    }
}

export class RepeatTemplate implements Partial<HTMLTemplateElement & DomRepeat> { // extends DomRepeat { // (Object as any as DomRepeat)
    constructor() {
        // const element = document.createElement('template'); // as DomRepeat & Repeat;
        const element = new QuasiTemplate();
        element.setAttribute('is', 'dom-repeat');
        element.appendChild = <T extends Node>(node: T): T => (QuasiTemplate.isTemplate(node) ? element.replace(node) : element.append([node]), node);
        return element as any;
    }
}

export type DomIf = PolymerElements.DomIf;
export let DomIf = QuasiConstructor<DomIf>('dom-if');

export class If implements Partial<DomIf> { // extends DomIf { // (Object as any as DomIf)
    constructor() {
        const element = new DomIf() as DomIf & If;
        const template = new QuasiTemplate();
        element.appendChild(template);
        element.appendChild = <T extends Node>(node: T): T => ((node: Node) => QuasiTemplate.isTemplate(node) ? template.replace(node) : template.append([node]), node);
        return element as any;
    }
}

export class IfTemplate implements Partial<HTMLTemplateElement & DomIf> { // extends DomIf { // (Object as any as DomIf)
    constructor() {
        // const element = document.createElement('template'); // as DomIf & Repeat;
        const element = new QuasiTemplate();
        element.setAttribute('is', 'dom-if');
        element.appendChild = <T extends Node>(node: T): T => ((node: Node) => QuasiTemplate.isTemplate(node) ? element.replace(node) : element.append([node]), node);
        return element as any;
    }
}

