import { Components, PolymerElement, StyleElement as Style, style } from '~/framework';
import { AppStyle } from '.';

export class NerdyAppStatus extends PolymerElement {
    static tagName = 'nerdy-app-status';

    static style: style = {
        ':host': {
            position: 'fixed', left: 0, right: 0, bottom: 0, padding: 0, margin: 0,
            backgroundColor: 'rgba(254, 254, 254, 0.5)',
            contain: 'content', fontSize: '90%',
        } as any,
        'label': { fontWeight: '200' }, 'label::after': { content: ':', paddingRight: '0.25em' },
        '.status': {
            display: 'flex', justifyContent: 'space-around', alignContent: 'center',
            padding: '0 0.5em',
            color: AppStyle.$Primary, textShadow: '0 1.5px 0 rgba(255, 255, 255, 1)',
            backdropFilter: 'blur(5px)', WebkitBackdropFilter: 'blur(5px)',
        },
        '.status ::slotted(*)': { padding: '0 1em', minWidth: '10%' },
    };

    static template = ({ }: NerdyAppStatus) =>
        <template>
            <Style>{NerdyAppStatus.style}</Style>
            <div class='status'>
                <slot id="status-slot"></slot>
            </div>
        </template>

    // attached() {
    //     const statusSlot = this.root.querySelector('#status-slot'); // console.log(statusSlot);
    //     statusSlot.addEventListener('slotchange', (event) => console.log('slotchange', { event }));
    // }
}

Components.define(NerdyAppStatus);

export default NerdyAppStatus;
