
export const map = <T, U>(object: {[name: string]: T} | T[], mapper: (value: T) => U): U[] => (
    (Array.isArray(object) ? object : Object.values(object) as T[]).map<U>(mapper)
);

export const reduce = <T, U>(object: {[name: string]: T} | T[], reducer: ReducerFunction<T, U, typeof object, string|number>, initialValue: U ): U => (
    reducer['keys'] = !Array.isArray(object) && Object.keys(object) || [],
    (Array.isArray(object) ? object : Object.values(object) as T[]).reduce<U>((result, value, index) => reducer(result, value, reducer['keys'][index] || index, object), initialValue)
);

export declare type ReducerFunction<T, U, P extends ({[name: string]: T} | T[]), K extends string | number> = (previousValue: U, currentValue: T, currentIndex: K, object: P) => U;
