const stopEvent = <E extends Event>(event: E): typeof event => (event.stopPropagation(), event.preventDefault(), event);
const notify = true, reflectToAttribute = true;
const bits = (...args) => args.reduceRight((a, b, i) => a | (+b << i), 0); // tslint:disable-line:no-bitwise
const modifierKeys = ['', 'ctrl', 'alt+', 'ctrl+alt', 'shift', 'ctrl+shift', 'alt+shift', 'ctrl+alt+shift', 'cmd', 'ctrl+cmd', 'alt+cmd', 'ctrl+alt+cmd', 'cmd', 'ctrl+shift+cmd', 'alt+shift+cmd', 'ctrl+alt+shift+cmd'];

