    $: $ & { imageInputCard: Card }; // toolbar: Toolbar; canvases: Canvases;
    displayTitle: string = 'IQ:FFT Demo';
    filterMode?: boolean | string = (this._filterMode() || 'pass').toLocaleLowerCase();
    timeouts?: Partial<Record<keyof this | string, any>> = {};
    processes?: { [name: string]: Process } = {};
    _droptargets = new Set<HTMLElement>();
    _body?: HTMLBodyElement; _dropHandler?: EventListener; _dragHandler?: EventListener; _zoomHandler?: EventListener;
