// tslint:disable:align

export declare interface IBandPassFilter {
    low?: number; high?: number;
    band?: number; size?: number;
    min?: 0 | number; max?: number;
    basis?: number;
    [name: string]: any;
}

export const numerify = (value, fallback = NaN) =>
    value === 0 ? 0 : !value || isNaN(value) ? (value || fallback) : value; // Number(value);

export class BandPassFilter implements IBandPassFilter {

    state: IBandPassFilter;
    low?: number; high?: number; band?: number; size?: number;
    lowValue?: number; highValue?: number; bandValue?: number; sizeValue?: number;
    min?: number = 0; max?: number = Infinity; basis?: number;

    constructor({ low = NaN, high = NaN, band = NaN, size = NaN, min = 0, max = Infinity }: IBandPassFilter = {}) { this.state = { low, high, band, size }; }

    setBasis(basis: number) {
        // basis = Number(basis);
        const { basis: currentBasis } = this.state;
        // if (isNaN(basis) || basis < 0) return this.state.basis = NaN;
        basis = this.state.basis = isNaN(basis) || basis < 0 ? NaN : Number(basis);
        const change = basis != currentBasis; // tslint:disable-line:triple-equals
        if (change && !isNaN(basis) && !isNaN(currentBasis)) {
            const { low: currentLow, high: currentHeight, band: currentBand, size: currentSize } = this.state;
            const low = isNaN(currentLow) ? NaN : currentLow / currentBasis * basis;
            const high = isNaN(currentHeight) ? NaN : currentHeight / currentBasis * basis;
            const band = isNaN(currentBand) ? NaN : currentBand / currentBasis * basis;
            const size = isNaN(currentSize) ? NaN : currentSize / currentBasis * basis;
            this.state.basis = basis, this.state.low = low, this.state.high = high;
            // console.log('setBasis', { low, currentLow, high, currentHeight, basis, currentBasis });
            this.set({ low, high, band, size }); // return
            // return true;
        }
        return change;
    }

    // tslint:disable-next-line:cyclomatic-complexity
    set(change: IBandPassFilter) {
        if ('basis' in change) this.setBasis(change.basis);
        const numerify = (value) => !value && value !== 0 ? NaN : isNaN(value) ? value : Number(value);
        let { low, high, band, size }: IBandPassFilter = { ...this.state, ...change }; // const previous = { ... this.state };
        const { min, max } = (({ min, max, basis } = this.state) => ({
            max: (!isNaN(max) ? max : basis > 0 ? Math.hypot(basis, basis) : Infinity) || Infinity,
            min: (!isNaN(min) ? min : 0) || 0,
        }))(); // max = (!isNaN(max) ? max : basis > 0 ? Math.hypot(basis, basis) : Infinity) || Infinity; // min = (!isNaN(min) ? min : 0) || 0;
        if ('low' in change || 'high' in change) {
            low = numerify(low), high = numerify(high);
            if (isNaN(low) && isNaN(high)) { }
            else if ((isNaN(low) || low <= min) && high > min) NaN, size = Math.abs(high - min), band = min + size / 2;
            else if ((isNaN(high) || high >= max) && low < max) size = Math.abs(max - low), band = min + size / 2;
            else if (!isNaN(low) && !isNaN(high)) size = Math.abs(low - high), band = Math.min(low, high) + size / 2;
        } else if ('band' in change || 'size' in change) {
            band = numerify(band), size = numerify(size);
            if (isNaN(size) && (low === min || high === max)) size = Math.abs(high - low);
            if (!isNaN(band) && !isNaN(size)) low = band - size / 2, high = band + size / 2;
        } else return false;
        // tslint:disable-next-line:prefer-object-spread
        // return Object.assign(this.state, {
        //     low: Math.round(low * 4) / 4, high: Math.round(high * 4) / 4,
        //     band: Math.round(band * 4) / 4, size: Math.round(size * 4) / 4,
        // }), true;
        return Object.assign(this.state, { low, high, band, size }), true; // { ...low > high ? { low: high, high: low } : { low, high }
        // } else if ('band' in change || 'size' in change) {

        // } else return;

        // band = numerify(band), size = numerify(size);

        // if (!isNaN(band + size)) low = band - size / 2, high = band + size / 2;
    }

    get(key?: string) {
        if (key) return numerify(this.state[key], null);
        const { lowValue: low, highValue: high, bandValue: band, sizeValue: size } = this;
        return { low, high, band, size }; // { low: Math.round(low * 4) / 4, high: Math.round(high * 4) / 4, band: Math.round(band * 4) / 4, size: Math.round(size * 4) / 4 };
    }

    getValue(key: string) {
        const { [key]: value, low, high } = this.state;
        return Math.max(0, key === 'lowValue' ? low === high ? NaN : Math.min(low, high) : key === 'highValue' ? low === high ? NaN : Math.max(low, high) : value);
    }

}

Object.defineProperties(BandPassFilter.prototype, ['low', 'high', 'band', 'size', 'basis'].reduce((map, key) => Object.assign(map, {
    [key]: { get(this: BandPassFilter) { return this.get(key); }, set(this: BandPassFilter, value) { return this.set({ [key]: value }); } },
    [`${key}Value`]: { get(this: BandPassFilter) { return this.getValue(key); } },
}), {} as PropertyDescriptorMap));

export default BandPassFilter;
