import { Components, PolymerElement, StyleElement as Style, style, styling } from '~/framework';
import appStyle from './app-style';
import Styles, { View, Flex, Paper } from '../components/nerdy-styles';
// import { Selector as $$, Selectors, Var } from 'modules/nerdy-utils/styles';
import globalStyle from './global-styles';
// export * from '../components/nerdy-styles';

export const mixins: Record<'View' | 'Paper' | 'Flex', style> = { View, Paper, Flex };

export const styleObject: style = { // & typeof mixins
    ...Flex, ':root': {
        ...View[':root'] as styling || {},
        ...appStyle.variables as styling || {},
    },
    ...globalStyle,
    //     border: '2px solid cyan !important',
    //     display: 'flex', justifyContent: 'center', alignItems: 'center',
    // },
    // '[layer-group] > *': {
    //     border: '2px dotted red !important',
    //     position: 'absolute', flex: 1, opacity: 0.75,
    // },
};
Object.setPrototypeOf(styleObject, mixins);

const styleText: string = Style.generateStyleText(styleObject);
// debugger;
const styleBlob = new Blob([styleText], { type: 'text/css' });
export const styleURL = URL.createObjectURL(styleBlob);
export const sharedStyles = `@import "${styleURL}"`;

export class NerdyAppStyles extends PolymerElement {
    // static templateElement = <template><Style>{sharedStyles}</Style></template> as HTMLTemplateElement;
    static template = () => <template><Style>{sharedStyles}</Style></template>;
}

export const stylesheet = <Style>{sharedStyles}</Style>;
document.head.appendChild(stylesheet);
export const { sheet } = stylesheet as any;

// console.log({ stylesheet, sheet, templateElement: NerdyAppStyles.templateElement });

export default NerdyAppStyles;
