export { Complex, GrayData, FFTData, FilterData }; // Data,
export * from './iq-band-pass-filter';
const { max, min, clz32: lead } = Math;

// http://localhost:8080/samples/conres-19tv/vector/ConRes19tv%20-%20Vector-08.svg
const baseURL = document.baseURI.replace(/\/[^\/]*$/, ''); // location.origin;
const conresPatchSize: [number, string] = [7.06, 'mm'];
const checkerboardCycles = 8, checkerboardResolution = hypot(checkerboardCycles) / conresPatchSize[0]; // hypot(8) / conresPatchSize[0];
// const checkerboardPatch = `${baseURL}/samples/checkerboard.png?contrast=100.0&cycles=${checkerboardCycles}`;
const checkerboardPatch = `${baseURL}/samples/checkerboard.png?contrast=100.0&resolution=${checkerboardResolution}`;
const conresPatch = `${baseURL}/samples/conres-19tv/vector/ConRes19tv - Vector-08.svg?contrast=100.0&resolution=3.759`;
const ernestJung = `${baseURL}/samples/ernst-jung-2048.png`;
const defaults: Partial<State> = { // & { bandFilter: Partial<BandPassFilter> } = {
    // bandFilter: { low: 80, high: 110 }, sourceURI: ernestJung, bandPassMode: false, canvasSize: 256, autoContrast: false, symmetricFilter: false,
    // bandFilter: { low: 375, high: 775 }, sourceURI: ernestJung, bandPassMode: false, canvasSize: 2048, autoContrast: false, symmetricFilter: false,
    // bandFilter: { band: 4.5, size: 4 }, sourceURI: conresPatch, bandPassMode: true, autoContrast: true, dynamicFilter: true,
    bandFilter: { band: 11.3137, size: 1 }, sourceURI: checkerboardPatch, bandPassMode: true, autoContrast: true, dynamicFilter: true,
    bandScaling: false, filterScaling: true, symmetricFilter: false,
    useWorker: true,
};
const view: Partial<Window> = typeof window !== 'undefined' && window || {};
const viewWidth = view.innerWidth || 1280, viewHeight = view.innerHeight || 800;
const viewMin = min(viewWidth, viewHeight), viewMax = max(viewWidth, viewHeight);
const viewArea = viewWidth * viewHeight;
// const currentURL = new URL(location.href), { searchParams } = currentURL;
// console.log([... (searchParams as any)]);
const forceSize = false;
const defaultSize = forceSize || max(128, min(512, 1 << 31 - lead(viewMin / 1.75) as number)); // tslint:disable-line:no-bitwise
const defaultZoom = (canvasSize = defaultSize, canvasMargin = 10) => {
    const viewRows = floor(viewHeight / (canvasSize + (canvasMargin || 0))) + 0.5;
    const viewColumns = floor(viewWidth / (canvasSize + (canvasMargin || 0))) + 0.5;
    const viewCells = floor(viewRows) * floor(viewColumns) + 0.75;
    // const viewFit = floor(viewRows) / 2.25 * floor(viewColumns) / 3.25;
    // return max(0.1, min(2 * 128 / canvasSize, viewCells / 4.5)); // floor(); // , viewFit
    // return min(canvasSize < 512 ? 1.5 : 3, max(min(viewRows / 2, viewColumns / 3), min(viewColumns / 2, viewRows / 3), 0.1)); // floor(); // , viewFit
    return min(canvasSize < 512 ? 1.5 : 3, max(min(viewColumns / 2, viewRows / 3, viewCells / 4), 0.1)); // floor(); // , viewFit
    // return floor(max(0.25, min(1.5, (viewWidth - 100) / (canvasSize * 2))) * 8) / 8;
};
// const defaultZoom = (canvasSize = defaultSize) => floor(max(0.25, min(1.5, (viewWidth - 100) / (canvasSize * 2))) * 8) / 8;
