export declare class State {
    canvasSize?: 128 | 256 | 512 | 1024 | 2048 | 4096 | number; canvasZoom?: number;
    autoContrast?: boolean; bandScaling?: boolean; filterScaling?: boolean;
    symmetricFilter?: boolean; dynamicFilter?: boolean;
    sourceImage?: HTMLImageElement; sourceURI?: State['sourceImage']['src']; sourceFile?: File;
    patchContrast?: number; patchResolution?: number; patchCycles?: number;
    bandFilter?: IBandPassFilter; bandPassMode?: boolean | 'pass' | 'stop';
    grayData?: GrayData; fftData?: FFTData;
    filterData?: FilterData; filteredData?: FFTData;
    useWorker?: boolean; // number[] | boolean;
}

export class AppState extends (Object as typeof State) {
    static keys: Array<keyof AppState> = ['sourceURI', 'sourceFile', 'sourceImage', 'grayData', 'fftData', 'filterData', 'filteredData', 'bandFilter'];
    static defaultZoom = defaultZoom;
    static bandFilter = () => new BandPassFilter(defaults.bandFilter);
    static sourceURI = () => defaults.sourceURI;
    static bandPassMode = () => defaults.bandPassMode;
    static canvasSize = () => defaults.canvasSize || defaultSize;
    static canvasZoom = () => defaults.canvasZoom || defaultZoom(AppState.canvasSize());
    static autoContrast = () => defaults.autoContrast;
    static bandScaling = () => defaults.bandScaling;
    static filterScaling = () => defaults.filterScaling;
    static symmetricFilter = () => defaults.symmetricFilter;
    static dynamicFilter = () => defaults.dynamicFilter;
    static useWorker = () => defaults.useWorker;
}

export declare interface ClassConstructor<T extends {} = {}> { new(...args: any[]): T; }
export declare interface Model<T extends {} = {}> {
    keys: Array<keyof T>;
    new(): T;
    [name: string]: any;
}

export declare interface ModelDecoratedClass<M, T = {}> extends ClassConstructor<T & Partial<M>> { }
export declare type TypedPropertyDescriptors<T> = Partial<PropertyDescriptorMap & {[P in keyof T]: TypedPropertyDescriptor<T[P]>}>;

export function model<M>(model: Model<M>) {
    const descriptors: TypedPropertyDescriptors<M> = model.keys.reduce(<K extends keyof M>(descriptors, key: K) => (
        descriptors[key] = {
            configurable: true,
            get(this: M): M[K] {
                // if (!this['[model]']) this['[model]'] = new model(this);
                return this[`_${key}`] || (
                    !(`_${key}` in this) && typeof model[key] === 'function' && (this[key] = this[`_${key}`] = model[key](this)),
                    this[`_${key}`]
                );
            },
            set(this: M, value: M[K]) { this[`_${key}`] = value; }, // this: M,
        } as PropertyDescriptor,
        descriptors
    ), {} as TypedPropertyDescriptors<M>);
    let mixin;
    // return <T extends {}>(constructor: Constructor): typeof constructor & Constructor<T & M> => ( // { new(...args: any[]): T & {[P in keyof M]: M[P]}; } & S => (
    return <T = {}, S = {}>(constructor: { new(...args: any[]): T; } & S): { new(...args: any[]): T & {[P in keyof M]: M[P]}; } & S => (
        mixin = constructor,
        // mixin = class extends constructor {
        //     constructor(... args: any[]) {
        //         super(... args);
        //         for (const key of model.keys) {
        //             if (this[key] !== undefined || `_${key}` in this || !(key in model)) continue;
        //             this[key] = typeof model[key] === 'function' ? model[key](this) : model[key];
        //         }
        //     }
        // },
        // Object.assign(mixin.prototype)
        Object.defineProperties(mixin.prototype, descriptors),
        mixin
    ); //  as { new(...args: any[]): T & M; } & S;
    // } as { new(...args: any[]): T & M; } & S; // ClassConstructor<T & M>; // as C & { new(): ModelDecoratedClass<M, T> };
}

export const StatefulElement = <M extends {} = {}>(constructor: Constructor<any> = PolymerElement) => constructor as any as Constructor<PolymerElement & M>;

export default AppState;

