/// <reference path="types.d.ts" />

import { bind } from './decorators';
import { isCustomTagName, isElement, isFunction, isString, isTemplateElement, call, typeCheck } from './helpers';
import { classNames, isNativeConstructor, getTagName, NativeTag, HTMLElement } from './native-elements';

// tslint :disable: label-position ban-types unified-signatures interface-over-type-literal callable-types align no-empty-interface prefer-const

export const commonAttributesNames = ['id', 'name', 'class', 'hidden', 'value'];
export const propertyAttributeMap = { className: 'class', hide: 'hidden' };
export const standardAttributeProperties = Object.keys(propertyAttributeMap);
export const standardAttributeNames = [...commonAttributesNames, ...standardAttributeProperties.map(name => standardAttributeProperties[name])].filter(name => name && isString(name));
export const standardAttributeExpression = new RegExp(`/^(${standardAttributeNames.join('|')})$/`);

export const supportsDocumentFactory = !!(typeof document === 'object' && document.createElement); // && ((document.createElement('p') || {})['constructor'] || {})['name'] === 'HTMLParagraphElement');
export const supportsCustomElements = !!(typeof window === 'object' && window.customElements && window.customElements.define && window.customElements.whenDefined);

const textify = (object: any, string = String(object)): string => !object ? '' :
    isString(object) ? object :
        !isNaN(object) ? string :
            object[Symbol.toPrimitive] && (object[Symbol.toPrimitive] !== Object[Symbol.toPrimitive]) ? object[Symbol.toPrimitive]('string') :
                object[Symbol.toStringTag] && (object[Symbol.toStringTag] !== Object[Symbol.toStringTag]) ? object[Symbol.toStringTag]() :
                    object['toString'] && (object['toString'] !== Object['toString']) ? object['toString']() :
                        '';

/**
 * ElementsFactory handles all the element creation logic for various types of
 * custom elements.
 *
 * @export
 * @class ElementsFactory
 */
@bind // Bind delegate methods to this factory instance
export class ElementsFactory {

    /**
     * Determines how constructElement behaves when handling function entities
     * including custom element classes. By default when the custom elements API
     * is available on the window object is to defer construction to the owner
     * document through document.createElement(tag).
     *
     * @static
     * @type {boolean}
     * @memberof ElementsFactory
     */
    static useCustomElementsRegistry: boolean = supportsCustomElements;
    static keyMap = new Map<string, { property: string, attribute: string }>();

    static getKeys(name: string, keys = ElementsFactory.keyMap.get(name), { property = '', attribute = '' } = keys || {} as typeof keys) {
        // let { property = '', attribute = '' }: any = ElementsFactory.keyMap.get(name) || {};
        return name && (property && attribute || (keys = {
            property: property = property || name.replace(/-([a-z]|$)/ig, (a) => (a[1] || '').toUpperCase()),
            attribute: attribute = attribute || property.replace(/([a-z](?=[A-Z])|\D(?=\d)|[A-Z](?=[A-Z][a-z]))/g, '$1-').toLowerCase(),
        }, ElementsFactory.keyMap.set(name, keys).set(property, keys).set(attribute, keys))), { property, attribute };
    }

    get create(): ElementsFactory['createElement'] { return this.createElement; }

    constructor(public registrar: object & any) { }

    /**
     * Isomorphic-capable method for creating elements from tags, functions or
     * classes with an interface compatible with runtime document.createElement
     * and various compile- and post-compile-time JSX factory implementations.
     *
     * @template N
     * @template P
     * @template C
     * @template Properties
     * @param {(N | C)} entity
     * @param {Properties} [properties]
     * @param {...element[]} contents
     * @returns
     *
     * @memberof ElementsFactory
     */
    @bind public createElement<N extends TagName, P extends element, C extends ElementConstructor<P>, Properties extends Partial<P> | ElementProperties>(entity: N | C, properties?: Properties, ...contents: element[]) {

        // Create element from entity
        const element = isString(entity) ? this.constructElement(entity as TagName) : this.constructElement(entity as any as ElementConstructor);

        // TODO: Figure out how to make elements transiently-ambient until they are inserted at their destination
        // element.onerror = (event) => console.log(event); // (event['preventDefault'] && event['preventDefault'](), false);

        // Create pseudo-typed element
        const pseudo = element as Pseudo<typeof element>;

        // Apply properties to element
        this.applyProperties(pseudo, properties);

        // Append contents
        contents.length && this.appendContents(pseudo, ...[].concat(...contents));

        return element;
    }

    /**
     * Delegate method for creating elements from tags, functions or classes
     * using the most appropriate strategy depending on the execution context
     * and entity type. String entities
     *
     * @template Tag
     * @param {Tag} tagName
     * @returns {TagMap[Tag]}
     *
     * @memberof ElementsFactory
     */
    public constructElement<N extends keyof TagMap>(tagName: N): TagMap[N];
    // public constructElement<G extends keyof HTMLElementTagNameMap>(tagName: G): HTMLElementTagNameMap[G];
    public constructElement<P extends element, C extends ElementConstructor<P>>(constructor: ElementConstructor<P>): P;
    @bind public constructElement<N extends TagName, P extends element, C extends ElementConstructor<P>>(entity: TagName | C) {
        const ensureDefined = () => { }; // async () => typeof entity['ensureDefined'] === 'function' && entity['ensureDefined']();

        if (supportsDocumentFactory && isString(entity)) {
            const element = document.createElement(entity as TagName);
            return element as typeof element & HTMLElement & TagMap[N];
        } else if (isFunction(entity)) {
            if (ElementsFactory.useCustomElementsRegistry && isCustomTagName(entity.tagName)) //  && entity.template
                return ensureDefined(), this.constructElement(entity.tagName as N) as typeof entity.prototype & (CustomElement | HTMLElement) & P;

            if (isNativeConstructor(entity as any))
                return this.constructElement(getTagName(entity as any) as N);

            try {
                return new (entity as C)() as typeof entity.prototype & (CustomElement | HTMLElement) & P;
            } catch (exception) {
                // console.warn(exception);
                if (/constructor/.test(exception.message) && exception.name === 'TypeError') {
                    // try {
                    return (entity as any)() as typeof entity.prototype & (CustomElement | HTMLElement) & P;
                    // }
                }
                console.error(exception);
            }
        } else {
            throw TypeError(`Cannot construct element from entity "${entity}" which should either be a valid tag name or a Custom Element constructor.`);
        }
    }

    /**
     * Convenience method for creating text nodes.
     *
     * @template text
     * @param {text} text
     * @returns {Pseudo<Text>}
     *
     * @memberof ElementsFactory
     */
    @bind public createTextNode<text extends string>(content: text): Pseudo<Text> {
        return document.createTextNode(textify(content)) as Pseudo<Text>;
    }

    /**
     * Convenience method for creating comment nodes.
     *
     * @template text
     * @param {(text | any)} data
     * @returns {Pseudo<Comment>}
     *
     * @memberof ElementsFactory
     */
    @bind public createComment<text extends string>(data: text | any): Pseudo<Comment> {
        return document.createComment(`${data}`) as Pseudo<Comment>;
    }

    /**
     * Delegate method for applying properties to elements from JSX conforming
     * properties objects, which will automatically determine when a property
     * should be applied as an attribute on the element or a set property value
     * on the element's instance based on execution context, property name,
     * and value type.
     *
     * @template P
     * @template A
     * @param {P} element
     * @param {A} properties
     * @returns {void}
     *
     * @memberof ElementsFactory
     */
    applyProperties<P extends HTMLElement, A extends ElementProperties | Partial<P>>(element: P, properties: A): void {
        if (typeof properties !== 'object') return;
        const keys = Object.keys(properties || {});
        // if (keys.includes('drawerless')) console.log(properties, { drawerless: properties['drawerless'] });
        if (keys.length) {
            for (const property of keys) {
                // const propertyKey = property.replace(/-([a-z]?)/ig, (a) => (a[1] || '').toUpperCase()), // property
                //     attributeKey = propertyKey.replace(/([a-z](?=[A-Z])|\D(?=\d)|[A-Z](?=[A-Z][a-z]))/g, '$1-').toLowerCase(), // property.replace(/-$/, '')
                const { property: propertyKey, attribute: attributeKey } = ElementsFactory.getKeys(property),
                    value = (properties as any)[property],
                    valueType = typeof value; // propertyType = typeof element[property];
                // if (/value/.test(property)) console.log({ value, valueType, property, element });
                ( // !/-$/.test(property) ||
                    (!(propertyKey in element) && !element[propertyKey]) ||
                    valueType === 'undefined' || value === null || // value !== undefined ||
                    typeof element[propertyKey] === valueType
                ) && (element[propertyKey] = value);
                (value === false || value === null || value === undefined)
                    ? element['removeAttribute'](attributeKey) // element.getAttribute(property) &&
                    : (value !== true && (valueType === 'function' || valueType === 'object' || valueType === 'symbol'))
                    || element['setAttribute'](attributeKey, value === true ? '' : value);
            }
        }
    }

    /**
     * Delegate method for inserting children into elements passed as rest
     * parameters using JSX conforming format, which will automatically
     * determine how a child should be appended to a parent based on the type of
     * the child and if either the parent or the child implement the custom
     * appendObject or appendTo methods to deal with non-element objects, with
     * additional logic for properly appending content to HTMLTemplateElements.
     *
     * @template P
     * @param {P} element
     * @param {...element[]} contents
     * @returns
     *
     * @memberof ElementsFactory
     */
    appendContents<P extends HTMLElement>(element: P, ...contents: element[]) {
        if ((!contents || !contents.length) || (typeof element !== 'object' || !(element instanceof HTMLElement))) return; // if (element instanceof HTMLStyleElement) element.textContent = contents.join('\n');

        const template: HTMLTemplateElement = isTemplateElement(element) && element as any;
        const parent: Node = template ? template.content : element;

        if (!parent || (typeof parent.appendChild)[0] !== 'f') return;

        for (const child of contents) if (child) {
            if (isElement(child) || child instanceof Text) {
                parent.appendChild(child as Node);
            } else {
                if (typeof parent['appendObject'] === 'function') parent['appendObject'](child);
                // else if (typeof child['appendTo'] === 'function') child['appendTo'](parent);
                else parent.appendChild(this.createTextNode(child as any)); // `${child}`
            }
        }

        if (template) this.initializeTemplateContents(template);
    }

    /**
     * Convenience method that will attempt to call attachedToTemplate(template)
     * on all descendants of a template element as a non-obtrusive way for
     * custom elements to perform ambient bindings and initialization functions
     * outside of the scope of the main document. Elements are called in reverse
     * sequence to favour a depth-first calling order for any nested elements
     * resulting in a last-first calling order for any sbilings.
     *
     * @param {HTMLTemplateElement} template
     *
     * @memberof ElementsFactory
     */
    initializeTemplateContents(template: HTMLTemplateElement) {
        if (!isTemplateElement(template)) return;
        const descendants = (template.content as any).querySelectorAll('*');
        for (const decendant of Array.from(descendants).reverse())
            call(decendant, 'attachedToTemplate', template);
    }
}

export namespace ElementsFactory {
    export type createElement = ElementsFactory['createElement'];
    export type createComment = ElementsFactory['createComment'];
    export type createTextNode = ElementsFactory['createTextNode'];
}

export interface ElementFactory { createElement: ElementsFactory['createElement']; }
export type ElementFactoryMethod = ElementsFactory['createElement'];
export interface CommentFactory { createComment: ElementsFactory['createComment']; }
export type CommentFactoryMethod = ElementsFactory['createComment'];

export default ElementsFactory;
