export interface Indexable<T = any> { [index: number]: T; length?: number; } // T[] | ArrayLike<T>

export function* subinterval<T = any>(this: any, data: Indexable<T>, interval: number = 1, start: number = 0, end: number = data && data.length) {
    if (!data || typeof data.length !== 'number' || data.length < 1 || data.length < end) return; //  || !(start in data && end in data)) return;
    for (let i = start; i < end; i += interval) yield data[i];
}
