// import 'modules/nerdy-elements';
import { style } from '~/framework';

export const flexStyles: style = {
    '[flex], [flex-pad], [flex-wrap], [flex-space] [flex-column], [flex-row], [flex-fill]': { display: 'flex' },
    '[flex] > *': { margin: 'auto', marginTop: 'auto', marginBottom: 'auto', marginLeft: 'auto', marginRight: 'auto' },
    '[flex-row]': { flexDirection: 'row', flex: '1 1 auto' },
    '[flex-row] > *': { marginTop: 'auto', marginBottom: 'auto' },
    '[flex-column]': { flexDirection: 'column', flex: '1 1 auto' },
    '[flex-column] > *': { marginLeft: 'auto', marginRight: 'auto' },
    '[flex-wrap], [flex][auto-wrap]': { flexWrap: 'wrap' },
    '[no-wrap]': { flexWrap: 'nowrap' },
    '[flex-inline], [flex][inline]': { display: 'inline-flex' },
    '[flex-fill], [flex] > [fill]': { flexGrow: 1, flexBasis: '100%', alignSelf: 'stretch', justifySelf: 'stretch' },
    '[space*="around along"], [space=around], [space-around=""], [space-around~=along], [space-around-along]': { justifyContent: 'space-around' },
    '[space*="around across"], [space=around], [space-around=""], [space-around~=across], [space-around-across]': { alignContent: 'space-around' },
    '[space*="between along"], [space=between], [space-between=""], [space-between~=along], [space-between-along]': { justifyContent: 'space-between' },
    '[space*="between across"], [space=between], [space-between=""], [space-between~=across], [space-between-across]': { alignContent: 'space-between' },
    '[space*="evenly along"], [space=evenly], [space-evenly=""], [space-evenly~=along], [space-evenly-along]': { justifyContent: 'space-evenly' },
    '[space*="evenly across"], [space=evenly], [space-evenly=""], [space-evenly~=across], [space-evenly-across]': { alignContent: 'space-evenly' },
    '[center=""], [center~=along]:not([center*="self along"]), [center*="items along"], [center-items], [center-items~=along]': { justifyItems: 'center' },
    '[center=""], [center~=across]:not([center*="self across"]), [center*="items across"], [center-items], [center-items~=across]': { alignItems: 'center' },
    '[center=""], [center~=along]:not([center*="self along"]), [center*="content along"], [center-content], [center-content~=along]': { justifyContent: 'center' },
    '[center=""], [center~=across]:not([center*="self across"]), [center*="content across"], [center-content], [center-content~=across]': { alignContent: 'center' },
    '[center-self=""], [center-self~=along], [center*="self along"]': { justifySelf: 'center' },
    '[center-self=""], [center-self~=across], [center*="self across"]': { alignSelf: 'center' },
    '[stretch=""], [stretch~=along]:not([stretch*="self along"]), [stretch*="items along"], [stretch-items], [stretch-items~=along]': { justifyItems: 'stretch' },
    '[stretch=""], [stretch~=across]:not([stretch*="self across"]), [stretch*="items across"], [stretch-items], [stretch-items~=across]': { alignItems: 'stretch' },
    '[stretch=""], [stretch~=along]:not([stretch*="self along"]), [stretch*="content along"], [stretch-content], [stretch-content~=along]': { justifyContent: 'stretch' },
    '[stretch=""], [stretch~=across]:not([stretch*="self across"]), [stretch*="content across"], [stretch-content], [stretch-content~=across]': { alignContent: 'stretch' },
    '[stretch-self=""], [stretch-self~=along], [stretch*="self along"]': { justifySelf: 'stretch' },
    '[stretch-self=""], [stretch-self~=across], [stretch*="self across"]': { alignSelf: 'stretch' },
};

export const globalStyle: style = {
    '*::selection': { backgroundColor: 'Highlight', color: 'HighlighText' },
    '*:not(:defined), *:not(:defined) *, [not-defined], [not-defined] *': {
        opacity: '0 !important',
        color: 'rgba(125, 127, 127, 0)',
        background: 'rgba(125, 127, 127, 0)',
        // transition: 'opacity 5000ms ease-in-out',
    },
    ':defined, :defined *, ::slotted(*), *:not(:defined), *:not(:defined) *, [not-defined], [not-defined] *': {
        transitionProperty: 'opacity, color, background',
        transitionTimingFunction: 'ease-in-out',
        transitionDuration: '100ms',
    },    // '[layer-group]': {
    '@keyframes fadeout': `from { opacity: 1; } to { opacity: 0; }` as any,
    '[hidden]': { opacity: 0 }, // display: 'none',
    '[pixelated]': { imageRendering: 'pixelated' },
    ...flexStyles,
};

export { globalStyle as style };

export default globalStyle;
