import Components, { CustomElement, TemplateFactory, BindingHandler, NerdyBindingHandler, BindingProxy, utils } from '../core';
import createStyledLogger from 'modules/nerdy-utils/logger'; // , {InstanceLogger, logger}

// tslint:disable: unified-signatures ban-types

import { $entry } from 'modules/nerdy-utils'; $entry(__filename || 'nerdy-elements/components/nerdy-element.tsx');

const { defineProperty, defineProperties } = Object;

export class NerdyElement extends CustomElement {
    static root: 'open' | 'closed' | 'light' = 'open';
    static _instanceCount: number = 0; // public
    static template?: HTMLTemplateElement | TemplateFactory<NerdyElement>; // public
    static templateBinder?: { new(...args: any[]): BindingHandler } = NerdyBindingHandler; // public
    static define: typeof Components['define'] = (...args: any[]) => (Components as any).define(...args); // public

    static get localName(): string { return this.tagName || this.name || (this as any).displayName; } // public

    static createLogger<M extends logger>(instance: NerdyElement, options: LoggerOptions<M> | M | string = 'log'): Logger<M> { // } StyleLogger<M> & Partial<Record<keyof typeof instance, StyleLogger<M>>> {
        const { localName = '', _instanceNumber: index, constructor: { tagName, name: functionName } } = instance;
        const type = /(log|info|warn|error)/.exec<M>([options, options['type'], 'log'])[0];
        const name = [localName, tagName, functionName].find(name => name && typeof name === 'string');
        const prefix = /\w/.test(options['prefix'] || '') ? options['prefix'] : index > - 1 ? `${name}-${index}` : name;
        options = { ... typeof options === 'object' ? options : {}, type, prefix, source: instance };
        return createStyledLogger<M>({ ...options });
    }

    static async canRegister() { return await this; } // public

    static stampTemplate(element: NerdyElement, template: TemplateFactory<NerdyElement> | HTMLTemplateElement) { // public
        if (!template) return;
        // TODO: Implement resolve instance values using NerdyBindingHandler
        const constructor = element.constructor as any as typeof NerdyElement;
        const BindingHandler = constructor.templateBinder || NerdyBindingHandler;
        const binder = new BindingProxy(element, new BindingHandler(element));
        const factory = typeof template === 'function' && template;
        if (factory) template = Components.defineTemplate(element.tagName, factory, binder) as HTMLTemplateElement;
        // console.log(`${this.tagName}#stampTemplate`, { template });
        if (!(template instanceof HTMLTemplateElement)) throw TypeError(`Cannot stampTemplate without a valid HTMLTemplateElement or TemplateFactory.`);
        element.template = template;
        // console.log('constructor.root', constructor.root, constructor); // if (constructor.root === 'light') debugger;
        const container = constructor.root === 'light' ? element : element.attachShadow({ mode: constructor.root === 'closed' ? 'closed' : 'open' });
        const content = template.content.cloneNode(true);
        container.appendChild(content);
    }

    template?: HTMLTemplateElement; // public
    _instanceNumber = -1; // public

    get log(): Logger<'log', this> { return Object.defineProperty(this, 'log', { value: NerdyElement.createLogger(this, 'log') }).log; } // log(method: string, ...args) { return console.log(`${this.localName}:${this._instanceNumber}#${method}`, ...args), true; }
    get info(): Logger<'info', this> { return Object.defineProperty(this, 'info', { value: NerdyElement.createLogger(this, 'info') }).info; }
    get warn(): Logger<'warn', this> { return Object.defineProperty(this, 'warn', { value: NerdyElement.createLogger(this, 'warn') }).warn; } // warn(method: string, ...args) {  return console.warn(`${this.localName}:${this._instanceNumber}#${method}`, ...args), true; }
    get error(): Logger<'error', this> { return Object.defineProperty(this, 'error', { value: NerdyElement.createLogger(this, 'error') }).error; } // error(error: Error); error(method: string, ...args: any[]); error(arg, ...args: any[]) { return args.length === 0 ? console.error(arg) : console.error(`${this.localName}:${this._instanceNumber}#${arg}`, ...args), true; }

    constructor() {
        super(); const constructor: typeof NerdyElement & { [name: string]: any } & Partial<Object> = this.constructor as any;

        // Count instance
        constructor.hasOwnProperty('_instanceCount') || defineProperty(constructor, '_instanceCount', { value: 0, writable: true });
        this['_instanceNumber'] = constructor['_instanceCount'] = (constructor['_instanceCount'] || 0) + 1;

        // Stamp template
        const { template, templateBinder } = constructor;
        if (typeof template === 'function' && (!templateBinder || templateBinder === NerdyBindingHandler)) NerdyElement.stampTemplate(this, template);
        // else if (template) this.log('constructor', { template, html: (template as HTMLTemplateElement).outerHTML });

        // /* Loggers */ this.log(10), this.info(this), this.warn(`You're all good`), this.error(Error('Nothing wrong here!')); this.log.attached(10), this.info.attached(this), this.warn.attached(`You're all good`), this.error.attached(Error('Nothing wrong here!'));
    }

    /** Called every time the element is inserted into the DOM */
    connectedCallback() { utils.call(this, 'attached'); }

    /** Called every time the element is removed from the DOM. */
    disconnectedCallback() { utils.call(this, 'detatched'); }

    /** Called if the element has been moved into a new document */
    adoptedCallback() { utils.call(this, 'adopted'); }

    /** Called when an attribute was added, removed, or updated */
    attributeChangedCallback<name extends AttributeName, value extends AttributeValue>(attribute: name, previousValue: value, value: value) { utils.call(this, 'modified', attribute, previousValue, value); }

    /** Called every time the element is inserted into the DOM */
    attached?(); attached?(...args: any[]);

    /** Called every time the element is removed from the DOM. */
    detatched?(); detatched?(...args: any[]);

    /** Called if the element has been moved into a new document */
    adopted?(); adopted?(...args: any[]);

    /** Called when an attribute was added, removed, or updated */
    modified?<name extends AttributeName, value extends AttributeValue>(attribute: name, previousValue: value, value: value); modified?(...args: any[]);

    /** Called when the element is inserted inside an HTMLTemplateElement (which is a document-fragment isolated from it's ownerDocument) */
    attachedToTemplate?(template: HTMLTemplateElement);

    protected asNumber(value: any, fallback?: any) { if (arguments.length > 1) return !isNaN(value) ? value : fallback; else if (!isNaN(value)) return value; }
    protected asFixed(value: any, rounding = 2, fallback?: any) { return this.asNumber(value).toFixed(rounding); }
    protected asPrecise(value: any, rounding = 3, fallback?: any) { return this.asNumber(value).toPrecision(rounding); }
    protected asString(value: any) { return typeof value === 'string' || typeof value === 'number' ? value === 0 ? '0' : !value ? '' : `${value}` : ''; }
    // protected asLower(value: any) { return typeof value === 'string' || typeof value === 'number' ? value === 0 ? '0' : !value ? '' : `${value}`.toLowerCase() : ''; }
    protected asLower(value: any) { return this.asString(value).toLowerCase(); }
    // protected asUpper(value: any) { return typeof value === 'string' || typeof value === 'number' ? value === 0 ? '0' : !value ? '' : `${value}`.toUpperCase() : ''; }
    protected asUpper(value: any) { return this.asString(value).toUpperCase(); }
    protected asJoinedString(...values: any[]) { return values.map(this.asString).join(''); }
    protected asJoinedWords(...values: any[]) { return values.map(this.asString).join(' '); }
    protected asJoinedList(...values: any[]) { return values.map(this.asString).join(', '); }
    // protected asProper(value: any) { return this.asString(value).replace(/(\W|^)([a-z])/g, (a, b, c) => `${b}${c}.toUpperCase()`); }
    protected asStringTag(value: any) { return value && value[Symbol.toStringTag] && value[Symbol.toStringTag]() || Object.prototype[Symbol.toStringTag].call(value); }
    // protected asHTML(value: string) {  }

}

export default NerdyElement;
