// tslint:disable: ban-types no-magic-numbers interface-over-type-literal

export const BindMethods = Symbol('[[BindMethods]]');

// export declare type Constructor<T extends object = object> = { new (...args: any[]): T } | { new(): T; };
export declare type Constructor<T extends object = object> = new (...args: any[]) => T;

/** Decorate method for binding when bind is applied to the constructor or object */
export function bind(target: any, method: string | symbol, descriptor: TypedPropertyDescriptor<Function>): never;
/** Decorate class constructor to bind prototype methods after constructor is executes */
export function bind<Constructor extends { new (...args: any[]): {} }>(target: Constructor): Constructor;
/** Bind prototype methods to instantiated object */
export function bind(target: object): never;
export function bind<Constructor extends { new (...args: any[]): {} }>(target: Constructor | object | any, method: string | symbol | Array<string | symbol> = target[BindMethods], descriptor: TypedPropertyDescriptor<Function> = null) {
    const scope: any = {};
    if (arguments.length === 3) {
        // Decorate method for binding when bind is applied to the constructor or object
        (target[BindMethods] = [... (target[BindMethods] || []), method], undefined);
    } else if (arguments.length < 3 && typeof target === 'object' && Array.isArray(method)) {
        // Bind prototype methods to instantiated object
        const prototype = scope.prototype = Object.getPrototypeOf(target),
            properties: PropertyDescriptorMap = scope.properties = {},
            boundMethods = scope.boundMethods = method || [];
        for (const boundMethod of boundMethods) {
            if (typeof boundMethod === 'string' && typeof prototype[boundMethod] === 'function') {
                target[boundMethod] = prototype[boundMethod].bind(target);
            }
        }
        // console.log('bind(object)', scope);
    } else if (arguments.length === 1 && typeof target === 'function') {
        // Decorate class constructor to bind prototype methods after constructor is executes
        return class extends (target as { new (...args: any[]): {} }) {
            constructor(...args: any[]) {
                super(...args);
                bind(this);
            }
        };
    }
}

