import QuasiElements, { Canvas as QuasiCanvas, Constructor as QuasiConstructor, QuasiElementConstructor } from 'modules/nerdy-elements/core/quasi-elements';
import Fourier, { Complex, ComplexArray, parseFilterRange as parseFilter, initializeData, outdated, workers, processInverseData, processForwardData } from 'modules/nerdy-fft/turbomaze';
import { subinterval, round, floor, ceil, diagonal, Bound, Reflection, imul, min, max } from 'modules/nerdy-utils';
import Process, { ProcessOptions } from '../modules/process'; // import { draw } from './iq-canvas';

// tslint:disable:no-bitwise

export { Complex, subinterval, Process };
export const shouldAbort = (process: Partial<Process>, i = 0) => !(i % 100) ? !process || (process.aborted === true) || outdated(process.output) || outdated(process.input) : false;
export const render = (process: Process, { render, aborted } = process) => (typeof render !== 'function' || shouldAbort(process) || process.render.call(process, process), process);
export const getImageData = (context: CanvasRenderingContext2D, { canvas = context && context.canvas, top = 0, left = 0, width = canvas && (canvas.width || 0 - left), height = canvas && (canvas.height - top) || 0 } = {}) => context && context.getImageData(top, left, width, height);
export const createImageData = (context: CanvasRenderingContext2D, { canvas = context && context.canvas, top = 0, left = 0, width = canvas && (canvas.width || 0 - left), height = canvas && (canvas.height - top) || 0 } = {}) => context && context.createImageData(width, height);
export const prepareImageData = getImageData;

export class ImageProcess extends Process {
    parameters: (RenderParameters | CanvasParameters) & { useWorker?: boolean; };
    data: { initialImageData?: ImageData } = {};
    imageData?: any; pixelData?: null; grayData?: null;
    worker?: Worker;
    postrender?(process: this);
}

export class ConvertToGrayProcess extends ImageProcess {
    static id = 'ConverToGray'; static instances = 0;
    input: HTMLImageElement; output: GrayData; parameters: ImageProcess['Parameters'];

    draw(source: HTMLImageElement | HTMLCanvasElement, canvas: HTMLCanvasElement, { context = canvas && canvas.getContext('2d'), scale = NaN, left = NaN, top = NaN, width = NaN, height = NaN, sourceFit = max, canvasFit = min } = {}) {
        if (!source) return; else source['crossOrigin'] = '';
        const { save, restore, drawImage, scale: setScale, setTransform, ['backingStoreRatio' as any]: backingStoreRatio } = Bound(context);
        const { naturalWidth: sourceWidth, naturalHeight: sourceHeight } = source || {} as any;
        const sourceSize = sourceFit(sourceWidth, sourceHeight);
        const canvasSize = canvasFit(canvas.width, canvas.height);
        const devicePixelRatio = window.devicePixelRatio || 1; // ratio = devicePixelRatio / (backingStoreRatio || webkitBackingStorePixelRatio || mozBackingStorePixelRatio || msBackingStorePixelRatio || oBackingStorePixelRatio || backingStorePixelRatio || 1)
        !(scale > 0) && (scale = canvasSize / sourceSize);
        isNaN(width) && (width = sourceWidth); isNaN(height) && (height = sourceHeight);
        left = (left || 0) + imul(sourceWidth - width, 0.5); top = (top || 0) + imul(sourceHeight - height, 0.5);
        save(), setTransform(scale, 0, 0, scale, 0, 0);
        drawImage(source, left, top, width, height), restore(); // drawImage(source, 0, 0, sourceSize, sourceSize, 1, 1, sourceSize, sourceSize);
        return source; // console.log({ source, canvas, sourceWidth, sourceHeight, width, height, scale, left, top, devicePixelRatio, backingStoreRatio, context, parameters: arguments[2] });
    }

    async process({ input, parameters, output, data, log, parameters: { canvas, top = 0, left = 0, width = canvas.width - left, height = canvas.height - top, context } }: this) {
        canvas && (canvas['processes'] = (canvas['processes'] || 0) + 1);
        context.restore(), context.clearRect(top, left, width, height), this.draw(input, canvas, { context, top, left, sourceFit: min });
        if (shouldAbort(this)) return output;
        const imageData = getImageData(context, { top, left, width, height }); // data.initialImageData =
        const toGray = (data) => toGrayBits(data);
        const pixelData = imageData.data;
        const grayData: GrayData = output = initializeData(toGray(imageData.data)); // new Uint8ClampedArray(width * height);
        if (this.aborted && canvas) canvas['processes'] = (canvas['processes'] || 1) - 1;
        return this.output = await output, render(this), output;
    }

    async render({ input, output, data, postrender, parameters: { top = 0, left = 0, width, height, context, canvas = context && context.canvas } }: this = this) {
        if (!output && !output.length) return;
        const length = width * height;
        const fromGray = (data) => fromGrayBits(data);
        const pixelData = fromGray(output);
        if (!shouldAbort(this)) {
            (context && context.putImageData(new ImageData(pixelData, width, height), left > 0 ? left : 0, top > 0 ? top : 0));
            canvas && (canvas['processes'] = (canvas['processes'] || 1) - 1);
            if (typeof postrender === 'function') postrender.call(this, this);
        }
    }
}

export const fftDefaults = { contrastConstant: 9e-3, maxMagnitude: Math.pow(8, 8) / 4, fallbackValue: new Complex(0, 0) };
const { contrastConstant: _contrastConstant, maxMagnitude: _maxMagnitude, fallbackValue: _fallbackValue } = fftDefaults;

export const initializeFFTData = (fftData: Partial<FFTData>, options: Partial<FFTData & FFTParameters & typeof fftDefaults> = {}): FFTData => {
    const { contrastConstant, maxMagnitude, fallbackValue } = options = { ...fftDefaults, ...options } as typeof options;
    const { length = fftData.length, width = ceil(length ** 0.5 * 0.5), height = ceil(length ** 0.5 * 0.5) } = options;
    const output: Partial<FFTData> = { contrastConstant, maxMagnitude };
    const curve = output.curve = (magnitude: number) => Math.log1p(contrastConstant * magnitude);
    const curvedMagnitude = output.curvedMagnitude = output.curve(maxMagnitude);
    const denominator = (data: Partial<FFTData>, { curvedMagnitude, maxMagnitude, max } = data) => curvedMagnitude || (data.curvedMagnitude = data.curve(maxMagnitude || max));
    const shifted = output.shifted = (i) => i;
    const getShifted = output.getShifted = (i) => fftData.get(fftData.shifted(i)) || fallbackValue;
    const rgb = output.rgb = (i) => round(255 * fftData.curve(fftData.getShifted(i)['getMagnitude']()) / denominator(fftData));
    // const rgb = output.rgb = (i) => round(255 * 2 * fftData.getShifted(i)['getMagnitude']() / fftData.length);
    // tslint:disable-next-line:prefer-object-spread
    return Object.assign(fftData, output) as FFTData;
};

export class ForwardFFTProcess extends ImageProcess {
    static id = 'ForwardFFT'; static instances = 0; static fallbackValue = _fallbackValue;
    input: GrayDataLike; output: FFTData & ComplexArray; parameters: FFTParameters & ImageProcess['parameters'];

    pre({ output, parameters: { width, height }, input: { length = width * height } }: this) {
        if (!output) this.output = new SharedComplexArray(length) as any;
        Object.defineProperty(this.output, 'progress', { get: () => this['progress'], set: (progress) => this['progress'] = progress });
    }

    async process({ input, parameters, data, output, log, worker, parameters: { useWorker = false, contrastConstant = _contrastConstant, maxMagnitude = _maxMagnitude, width, height, fallbackValue = _fallbackValue, context, canvas = context && context.canvas } }: this) {
        canvas && (canvas['processes'] = (canvas['processes'] || 0) + 1);
        if (!useWorker || !(worker = workers && workers[width])) {
            Fourier.forward(input, output);
        } else {
            // console.log(this.id, { useWorker, worker });
            await new Promise((resolve, reject) => {
                const uid = this.id; let listener, dispose = () => (clearInterval(interval), worker.removeEventListener('message', listener), dispose = () => { });
                const abort = () => worker.postMessage({ action: 'abort', uid });
                worker.addEventListener('message', listener = ({ data = {}, data: { uid: _uid, output: _data } }: MessageEvent) => {
                    if (_uid === uid && !shouldAbort(this)) dispose(), output.data = _data, processForwardData(output), resolve();
                });
                const interval = setInterval(({ aborted } = this) => aborted && (reject('aborted'), abort(), dispose()), 100);
                const { data: $out } = output as any as { data: Float32Array }, $in = input;
                worker.postMessage({ input: $in, output: $out, uid, action: 'forward' }, transferables($in.buffer, $out.buffer));
            });
        }
        const length = width * height, dx = floor(width / 2), dl = floor(length / 2);
        initializeFFTData(output, { contrastConstant, maxMagnitude, fallbackValue });
        output.shifted = (i, x = i % width, l = i - x) => (l + ((dx - x) % width) + dl) % length;
        if (this.aborted && canvas) canvas['processes'] = (canvas['processes'] || 1) - 1;
        return this.output = await output, render(this), output;
    }

    async render({ input, output, data, postrender, parameters: { top = 0, left = 0, width, height, context, canvas = context && context.canvas } }: this) {
        if (!output && !output.length) return;
        const length = width * height; top = top > 0 ? top : 0; left = left > 0 ? left : 0;

        // Direct Method
        const pixelData = new Uint8ClampedArray(length * 4);
        pixelData.fill(255);
        for (let i = 0, v; i < length && (i % 100 || !shouldAbort(this)); i++) {
            pixelData.fill(output.rgb(i), i * 4, i * 4 + 3);
        }

        if (!shouldAbort(this)) {

            const { beginPath, moveTo, lineTo, stroke, fill, save, restore, setTransform, clearRect, putImageData, setLineDash, set, get, globalAlpha } = Bound(context); // ['beginPath', 'moveTo', 'lineTo'].reduce((proxy, method) => (proxy[method] = (context[method] as any).bind(context), proxy), {});

            // Reset
            save();
            setTransform(1, 0, 0, 1, 0, 0); clearRect(top, left, width, height);
            const offset = 0.5, alpha = 1;
            setTransform(1, 0, 0, 1, offset, offset); set('globalAlpha', alpha); // context.translate(dt, dt);

            const x1 = (left > 0 ? left : 0), y1 = (top > 0 ? top : 0);
            putImageData(new ImageData(pixelData, width, height), x1, y1);

            const line = (x1: number, y1: number, x2: number, y2: number, style: string = 'white') => {
                beginPath(); moveTo(x1, y1); lineTo(x2, y2); set('strokeStyle', style); stroke();
            };

            const x0 = 0, xn = width, xm = xn / 2, dx = 8; // Math.round(xn / 100);
            const y0 = 9, yn = height, ym = yn / 2, dy = 8; // Math.round(yn / 100);
            const d1 = Math.min(dx, dy), d2 = dx * 2;
            set('lineWidth', 1); setLineDash([2, d2 - 2]);
            line(xm - d1, ym, x0, ym, 'rgb(255, 191, 127)'); line(xm - d2, ym, x0, ym, 'rgb(31, 15, 0)');
            line(xm, ym - d1, xm, y0, 'rgb(255, 191, 127)'); line(xm, ym - d2, xm, y0, 'rgb(31, 15, 0)');
            line(xm + d1, ym, xn, ym, 'rgb(255, 191, 127)'); line(xm + d2, ym, xn, ym, 'rgb(31, 15, 0)');
            line(xm, ym + d1, xm, yn, 'rgb(255, 191, 127)'); line(xm, ym + d2, xm, yn, 'rgb(31, 15, 0)');

            canvas && (canvas['processes'] = (canvas['processes'] || 1) - 1);
            restore(); set('globalAlpha', globalAlpha); // context.translate(dt, dt);
            if (typeof postrender === 'function') postrender.call(this, this);
        }
    }
}

export class InverseFFTProcess extends ImageProcess {
    static id = 'InverseFFT'; static instances = 0;
    input: FFTData; output: GrayData; // ExtendedData<number> | GrayData;
    parameters: { filterData?: FilterData, min?: number, max?: number, autoContrast?: boolean } & ImageProcess['parameters'];

    pre({ output, parameters: { width, height }, input: { length = width * height } }: this) {
        // if (!output) this.output = initializeData(new Array<number>(length)) as typeof output;
        if (!output) this.output = initializeData(new SharedFloat32Array(length)) as typeof output;
        Object.defineProperty(this.output, 'progress', { get: () => this['progress'], set: (progress) => this['progress'] = progress });
    }

    async process({ input, parameters, output, log, worker, input: { shifted, rgb }, parameters: { useWorker = false, width, height, filterData, context, canvas = context && context.canvas } }: this) {
        canvas && (canvas['processes'] = (canvas['processes'] || 0) + 1);
        const length = width * height;
        if (!useWorker || !(worker = workers && workers[width])) {
            Fourier.inverse(input, output);
        } else {
            // console.log(this.id, { useWorker, worker });
            await new Promise((resolve, reject) => {
                const uid = this.id; let listener, dispose = () => (clearInterval(interval), worker.removeEventListener('message', listener), dispose = () => { });
                const abort = () => worker.postMessage({ action: 'abort', uid });
                worker.addEventListener('message', listener = ({ data = {}, data: { uid: _uid, output: _data } }: MessageEvent) => {
                    if (_uid === uid && !shouldAbort(this)) dispose(), processInverseData(new ComplexArray(_data), output as any), resolve();
                });
                const interval = setInterval(({ aborted } = this) => aborted && (reject('aborted'), abort(), dispose()), 100);
                const $in = input instanceof ComplexArray ? input.data : input;
                const $out = new SharedFloat32Array($in.length);
                worker.postMessage({ input: $in, output: $out, uid, action: 'inverse' }, transferables($in.buffer, $out.buffer));
            });
        }
        if (this.aborted && canvas) canvas['processes'] = (canvas['processes'] || 1) - 1;
        return this.output = await output, render(this), output;
    }

    // tslint:disable-next-line:cyclomatic-complexity
    async render({ input, output, postrender, parameters: { top = 0, left = 0, width, height, context, canvas = context && context.canvas, min = NaN, max = NaN, autoContrast = true } }: this) {
        if (!output || !output.length) return; // if (!output.max || !output.min) console.warn('InverseFFTProcess#render: output does not define a min or max property', this); // if (!output || !output.length) return;
        const length = width * height; top = top > 0 ? top : 0; left = left > 0 ? left : 0;
        const imageData = this.imageData = prepareImageData(context, { top, left, width, height });
        // if (!shouldAbort(this) && context) context.fillStyle = 'gray', context.fillRect(left, top, width, height);
        const pixelData = imageData ? imageData.data : new Uint8ClampedArray(length * 4);
        const grayData = output as GrayData;

        const traverse = (data: TypedArray, callback: (chunk: number, start: number, length: number) => any, { view = new DataView(data.buffer), length = data.length, start = 0, end = length - 1, getter = DataView.prototype.getFloat64, step = 64 / data.BYTES_PER_ELEMENT } = {} as any) => {
            for (let i = start; i <= end; i += step) callback(getter.call(view, i), i, step);
        };
        const cmax = (data: TypedArray, cmax = data[0]): number => (traverse(data, (chunk: number, start, length) => {
            chunk > cmax && (cmax = Math.max(cmax, ...data.slice(start, start + length)) || cmax); // .forEach(((v: number) => {}) as any); // .forEach((v) => v > max && (max = v))
        }), cmax);

        min = isNaN(min) ? 'cmin' in output ? output['cmin'] : output.min : min;
        max = isNaN(max) ? 'cmax' in output ? output['cmax'] : output.max : max;
        // max = cmax(output as any); // isNaN(max) ? 'cmax' in output ? output['cmax'] : output.max : max;

        const scale = autoContrast && (!isNaN(max + min))
            ? (value: number) => 255 * (value - min) / (max - min)
            : (value: number) => value;

        const pixel = (value: number, scaled = scale(value)) => [scaled, scaled, scaled, 255];

        // Direct Method
        pixelData.fill(255);
        for (let i = 0, v; i < length && (i % 100 || !shouldAbort(this)); i++) {
            pixelData.fill(scale(output[i]), i * 4, i * 4 + 3);
        }
        // for (let i = 0, v; !shouldAbort(this) && i < length; i++) { // (!shouldAbort(this, i)) &&
        //     pixelData.set(pixel(output.get(length - i)), i * 4);
        // }

        // Using DataView
        // const view = new DataView(imageData.data.buffer);
        // for (let i = 0, v; i < length; i++) { // (!shouldAbort(this, i)) && !shouldAbort(this) &&
        //     view.setUint32(4 * i, (v = scale(output[length - i]) & 0xff) | (v << 8) | (v << 16) | (255 << 24), true); // tslint:disable-line:no-bitwise
        // }

        if (!shouldAbort(this)) {
            (context && context.putImageData(imageData, left > 0 ? left : 0, top > 0 ? top : 0));
            canvas && (canvas['processes'] = (canvas['processes'] || 1) - 1);
            if (typeof postrender === 'function') postrender.call(this, this);
        }
    }
}

export class FilterFFTProcess extends Process {
    static id = 'InverseFFT'; static instances = 0;

    input: { fftData: FFTData, filterData: FilterData }; output: FFTData;
    parameters: ImageProcess['Parameters'] & Partial<FFTParameters> & { filtered: Complex, mode: 'pass' | 'stop' | boolean };

    async process({ input, parameters, output, log }: this) {
        const { fftData, filterData } = input || {} as typeof input;
        const { data: filter, width = 0, height = 0 } = filterData, { length = width * height } = filter || {} as typeof filter;

        if (!filterData || !filter || shouldAbort(this)) return this.output = fftData;
        if (length !== fftData.length) return this.abort(`fftData length (${fftData.length}) is not equal to filter length (${length})!`), this.output = fftData;

        const { filtered = _fallbackValue, mode = 'pass', contrastConstant = _contrastConstant, maxMagnitude = _maxMagnitude, fallbackValue = _fallbackValue } = parameters || {} as typeof parameters;
        output = this.output = new Fourier.ComplexArray(fftData.length);
        initializeFFTData(output, { contrastConstant, maxMagnitude, fallbackValue });

        const dx = output['dx'] = floor(width / 2), dl = output['dl'] = floor(length / 2);
        // const unshifted = output.unshifted = input['shifted'] || ((i, x = i % width, l = i - x) => (l + ((dx - x) % width) + dl) % length);
        const unshifted = ((i, x = i % width, l = i - x) => (l - ((dx - x) % width) + dl) % length);

        const stopMode = (mode as any) === 'stop' || (mode as any) === false;
        const $in = input['buffer'] ? input : input['data'] && input['data']['buffer'] ? input['data'] : null;
        const $out = output['buffer'] ? output : output['data'] && output['data']['buffer'] ? output['data'] : null;

        const apply: (i: number) => any = $in && $out
            ? (i, i1 = i * 2, i2 = i1 + 1, h = filter[unshifted(i)]) => { stopMode && (h = 1 - h); $out[i1] = ($in[i1] || 0) * h, $out[i2] = ($in[i2] || 0) * h; }
            : (i, h = filter[unshifted(i)], { real, imag, magnitude } = fftData.get(i)) => { stopMode && (h = 1 - h), output.set(i, { real: real * h, imag: imag * h }); };

        for (let i = 0, v; i < length && (i % 100 || !shouldAbort(this)); i++) apply(i);
        output.max = input['max'], output.min = input['min'];
        return this.output = await output;
    }
}

export class GenerateFilterProcess extends Process {
    static id = 'GenerateFilterData'; static instances = 0;
    output: FilterData; parameters: ImageProcess['Parameters'] & { low?: number, high?: number; width: number, height: number; fill?: number; clear?: number; };

    pre({ parameters: { low, high, width, height } }: this) {
        this.output = { low, high, width, height } as FilterData;
    }

    async process({ input, parameters, output, log, parameters: { low, high, width, height, fill = 1, clear = 0 } }: this) {
        let data: FilterData['data'];
        data = output.data = (!width || !height) ? new Float32Array(0)
            : ((isNaN(low) || low <= 0) && (isNaN(high) || high >= Math.min(width, height))) ? new Float32Array(width * height).fill(fill)
                : Fourier.filter([width, height], Math.max(0, low || 0), Math.min(high || Infinity, width, height), { fill, clear });
        output.zeros = 'zeros' in data ? data.zeros : data.indexOf(clear) > -1;
        output.ones = 'ones' in data ? data.ones : data.indexOf(fill) > -1;
        return this.output = await output;
    }
}

export class RenderFilterProcess extends ImageProcess {
    static id = 'RenderFilter'; static instances = 0;
    static pixels = {
        filterFillPixel: [255, 255, 0, 127], filterClearPixel: [0, 0, 0, 127],
        fftClearPixel: [127, 127, 127, 255], fftVoidPixel: [0, 0, 0, 255],
    };

    input: { filterData: FilterData, fftData: FFTData };
    parameters: ImageProcess['parameters'] & {
        pixels?: Record<'filterFillPixel' | 'filterClearPixel' | 'fftClearPixel' | 'fftVoidPixel', [number, number, number, number]>;
        compositeContext?: CanvasRenderingContext2D;
    };
    compositeImageData?: ImageData; compositePixelData?: ImageData['data'];

    async process({ input, parameters, output, log, input: { filterData, fftData }, parameters: { context, compositeContext, top = 0, left = 0, width, height, pixels } }: this) {
        if (shouldAbort(this)) return output;
        const length = width * height; top = top > 0 ? top : 0; left = left > 0 ? left : 0;
        const { ones = true, zeros = true, data = new Uint8ClampedArray(length) } = filterData || {} as typeof filterData;
        const imageData = this.imageData = prepareImageData(context, { top, left, width, height });
        const pixelData = (imageData ? imageData.data : new Uint8ClampedArray(length * 4)).fill(255);
        const compositeImageData = this.compositeImageData = compositeContext && prepareImageData(compositeContext, { top, left, width, height });
        const compositeData = this.compositePixelData = (compositeImageData ? compositeImageData.data : new Uint8ClampedArray(length * 4)).fill(255);

        const { gray, rgb } = fftData || {} as typeof fftData;
        const { filterFillPixel, filterClearPixel, fftClearPixel, fftVoidPixel } = { ...RenderFilterProcess.pixels, ...pixels } as typeof pixels;
        const fft = gray ? (i, v = gray[i]) => [v, v, v, 255] : rgb ? (i, v = rgb(i)) => [v, v, v, 255] : () => fftClearPixel;
        // const filter = ones && zeros ? (i) => { pixelData.set(data[i] ? filterFillPixel : filterClearPixel, i * 4), compositeData && compositeData.set(data[i] ? fft(i) : fftVoidPixel, i * 4); }
        //     : ones ? (i) => { pixelData.set(filterFillPixel, i * 4), compositeData && compositeData.set(fft(i), i * 4); }
        //         : (i) => { pixelData.set(filterClearPixel, i * 4), compositeData && compositeData.set(fft(i), i * 4); };
        const filter = (i) => {
            const h = Number(data[i]) || 0, p = (h * 255) & 0xff, v = fft(i)[0] || 0;
            pixelData.set([filterFillPixel[0] * h, filterFillPixel[1] * h, filterFillPixel[2] * h, filterFillPixel[3]], i * 4);
            compositeData && compositeData.set([v * h, v * h, v * h, 255], i * 4);
        };
        for (let i = 0, v; i < length && (i % 100 || !shouldAbort(this)); i++) filter(i);
        if (!shouldAbort(this)) {
            context && context.putImageData(imageData, left, top);
            compositeContext && compositeContext.putImageData(compositeImageData, left, top);
        }
        return this.output = output;
    }
}

export class SimulateFilterProcess extends ImageProcess {
    static id = 'SimulateFilter'; static instances = 0;
    static pixels = RenderFilterProcess.pixels;
    static deferred = false;

    parameters: ImageProcess['parameters'] & { // GenerateFilterProcess['parameters'] & RenderFilterProcess['parameters']
        low?: number, high?: number; size?: number; // width: number, height: number; // fill?: number; clear?: number;
        pixels?: Record<'filterFillPixel' | 'filterClearPixel' | 'fftClearPixel' | 'fftVoidPixel', [number, number, number, number]>;
        filter?: FilterParameters; scale?: number; lineScale?: number; clientScale?: number;
    };

    async process({ input, parameters, output, log,
        parameters: { canvas, context = canvas.getContext('2d') },
        parameters: { top = 0, left = 0, width = canvas.width, height = canvas.height, pixels },
        parameters: { scale, lineScale: _lineScale, clientScale: _clientScale },
        parameters: { low: lowBand, high: highBand },
        // parameters: { filter = parseFilter({ lowBand, highBand, size: Math.min(width, height) }), size },
        parameters: { filter },
    }: this) {
        const contextScale = 1, lineScale = contextScale, transformScale = contextScale; // lineScale = lineScale;
        const filterScale = scale > 0 ? scale : 1;
        const { low, high, size, radius, sameBand, noHigh, noLow } = parseFilter({ lowBand, highBand, size: Math.min(width, height) });
        const { filterFillPixel, filterClearPixel, fftClearPixel, fftVoidPixel } = { ...RenderFilterProcess.pixels, ...pixels } as typeof pixels;
        const { beginPath, moveTo, lineTo, stroke, fill, fillRect, ellipse, save, restore, setTransform, clearRect, putImageData, setLineDash, set, get } = Bound(context);

        // Reset
        save(); setTransform(1, 0, 0, 1, 0, 0); clearRect(top, left, width, height);
        const offset = 0.5, alpha = 1;
        const xOffset = (width - width * filterScale) / 2 + offset * filterScale;
        const yOffset = (height - height * filterScale) / 2 + offset * filterScale;
        setTransform(filterScale, 0, 0, filterScale, xOffset, yOffset); set('globalAlpha', alpha);

        // console.log(`SimulateFilterProcess`, { scale, contextScale, filterScale });

        // context.save();
        if (sameBand || (noHigh && noLow)) return;
        const dx = (width - size) / 2, x = dx + size / 2;
        const dy = (height - size) / 2, y = dy + size / 2;
        const innerRadius = transformScale * (lowBand > 0 ? lowBand : NaN);
        const outerRadius = transformScale * (highBand > (lowBand || 0) ? highBand : NaN);
        const inner = !noLow && innerRadius > 0, outer = !noHigh && outerRadius > 0;
        if (!outer && !inner) return;

        (function outline() {
            const delta = Math.abs(outer ? (innerRadius || 0) - outerRadius : innerRadius || outerRadius);
            const centerRadius = (outer ? outerRadius - delta / 2 : inner ? innerRadius + delta / 2 : NaN);
            const centerCircumference = 2 * Math.PI * centerRadius;
            const center = delta > 2 && !isNaN(centerRadius); // outer && delta;
            const filterStyle = `rgba(255, 255, 0, 255)`; // rgba(${[0, 255, 255, 255].slice(0, 4).join(', ')})`;
            const guideStyle = `rgba(127, 255, 255, 255)`; // rgba(${[0, 255, 255, 255].slice(0, 4).join(', ')})`;
            const lineWidth = Math.max(lineScale / 4, Math.min(delta / 4 || lineScale, lineScale || 1));
            const dashSteps = 32, dashStep = centerCircumference / dashSteps, dashOffset = 0; // centerCircumference / 4; // centerCircumference / dashSteps * 2.5;
            let dashRatio = 1;
            save();

            // Clear
            clearRect(top, left, width, height);
            // context.fillStyle = 'rgba(0, 0, 0, 0.05)'; fillRect(top, left, width, height); // Dim Fill

            // Outer Boundary
            set('strokeStyle', filterStyle);
            dashRatio = outerRadius / centerRadius;
            const boundaryDash = dashStep * 3, boundaryGap = dashStep, boundaryOffset = dashOffset + (boundaryDash) / 2;
            beginPath(), ellipse(x, y, outerRadius, outerRadius, 0, 0, Math.PI * 4);
            setLineDash([boundaryDash * dashRatio, boundaryGap * dashRatio]); // setLineDash([boundaryGap * dashRatio / 2, boundaryDash * dashRatio / 2]); // setLineDash([dashStep / 2, dashStep * 1.5]);
            set('lineDashOffset', boundaryOffset * dashRatio); set('lineWidth', lineWidth);
            set('strokeStyle', filterStyle);
            outer && stroke();

            // Inner Boundary
            dashRatio = innerRadius / centerRadius;
            beginPath(), ellipse(x, y, innerRadius, innerRadius, 0, 0, Math.PI * 4);
            setLineDash([boundaryDash * dashRatio, boundaryGap * dashRatio]); // setLineDash([dashStep / 2, dashStep * 1.5]);
            set('lineDashOffset', boundaryOffset * dashRatio); set('lineWidth', lineWidth * 0.5);
            set('strokeStyle', filterStyle);
            inner && stroke();

            // Center
            dashRatio = 1;
            const centerDash = boundaryGap / 2, centerGap = boundaryDash + boundaryGap / 2;
            const centerOffset = dashOffset + centerDash / 2 + (centerGap + centerDash) / 2;
            const centerOutline = dashStep / 12;
            const centerColor = 'rgba(191, 255, 255, 1)';
            beginPath(), ellipse(x, y, centerRadius, centerRadius, 0, 0, Math.PI * 4);
            setLineDash([centerDash * dashRatio, centerGap * dashRatio]);
            set('lineDashOffset', centerOffset * dashRatio);
            set('strokeStyle', 'black'); set('lineWidth', lineWidth * 1.5);
            center && stroke();
            setLineDash([(centerDash - centerOutline) * dashRatio, (centerGap + centerOutline) * dashRatio]);
            set('lineDashOffset', (centerOffset - centerOutline / 2) * dashRatio);
            set('strokeStyle', centerColor); set('lineWidth', lineWidth * 1.25);
            center && stroke();

            // const centerDash = dashStep, centerGap = dashStep * 3;
            // const centerOffset = dashOffset + (centerDash) / 2 + (centerDash + centerGap) / 2;
            // const centerOutline = dashStep / 5;
            // setLineDash([(centerDash + centerOutline) * dashRatio, (centerGap - centerOutline) * dashRatio]); // setLineDash([dashStep, dashStep]);
            // set('strokeStyle', 'black'); set('lineWidth', lineWidth * 1.5);
            // set('lineDashOffset', (centerOffset + centerOutline / 2) * dashRatio);
            // beginPath(), ellipse(x, y, centerRadius, centerRadius, 0, 0, Math.PI * 4);
            // center && stroke();
            // setLineDash([centerDash * dashRatio, centerGap * dashRatio]); // setLineDash([dashStep, dashStep]);
            // set('strokeStyle', 'rgba(191, 255, 255, 0.75)'); set('lineWidth', lineWidth * 1.5);
            // set('lineDashOffset', centerOffset * dashRatio);
            // center && stroke();

            restore();
        })();

        // (function clipFill() {
        //     const filterStyle = `rgba(${[...filterFillPixel || [], 255, 255, 255, 255].slice(0, 4).join(', ')})`;
        //     set('fillStyle', filterStyle);
        //     outer
        //         ? context.ellipse(x, y, outerRadius, outerRadius, 0, 0, Math.PI * 4)
        //         : context.rect(top, left, width, height);
        //     context.fill();
        //     // context.save();
        //     context.beginPath();
        //     context.ellipse(x, y, innerRadius, innerRadius, 0, 0, Math.PI * 4);
        //     context.clip();
        //     context.clearRect(top, left, width, height);
        //     context.restore();
        // });

        context.restore(); // set('globalAlpha', globalAlpha); // context.translate(dt, dt);
        return this.output = output;
    }
}

export const toGrayIndex = (data: Uint8ClampedArray, index = 0, output = new TransientUint8ClampedArray(data.length / 4)) => {
    if (index = Math.abs((index || 0) % 4))
        for (let i = 0, source = data, length = Math.min(source.length, output.length * 4), v; i < length; i += 4)
            output[i / 4] = source[i + index];
    else
        for (let i = 0, source = data, length = Math.min(source.length, output.length * 4), v; i < length; i += 4)
            output[i / 4] = source[i];
    return output;
};

export const toGrayBits = (data: Uint8ClampedArray, bits = 0, output = new TransientUint8ClampedArray(data.length / 4)) => {
    // r = v >>> 24 & 0xFF, g = v >>> 16 & 0xFF, b = v >>> 8 & 0xFF, a = v & 0xFF; data[i] = v | (v << 8) | (v << 16) | (255 << 24)
    if (bits = (Math.abs(Math.round((bits <= 4 ? bits * 8 : bits) || 0) / 8) * 8) % 32)
        for (let i = 0, source = new Uint32Array(data.buffer), length = Math.min(source.length, output.length * 4), v; i < length; i++)
            output[i] = source[i] >> bits & 0xFF;
    else
        for (let i = 0, source = new Uint32Array(data.buffer), length = Math.min(source.length, output.length * 4), v; i < length; i++)
            output[i] = source[i] & 0xFF;
    return output;
};

export const fromGrayIndex = (data: TypedArray | number[], index = 0, slice = 3, output = new TransientUint8ClampedArray(data.length * 4)) => {
    if ((index = Math.abs((index || 0) % 4)) || ((slice = slice > 1 ? ~~slice : 1) > 1))
        for (let i = 0, source = data, length = Math.min(source.length, output.length / 4), v; i < length; i++)
            output.fill(source[i], i * 4 + index, i * 4 + index + slice);
    else
        for (let i = 0, source = data, length = Math.min(source.length, output.length / 4), v; i < length; i++)
            output[i * 4 + index] = source[i];
    return output;
};

export const fromGrayBits = (data: TypedArray | number[], mask = 0x00FFFFFF, filter = 0xFF000000, output: TypedArray = new Uint32Array(data.length)) => {
    // mask = ((mask + 0) & 0xFFFFFFFF) || 0xFFFFFF00; filter = ((filter + 0) & 0xFFFFFFFF) || (mask ^ 0x000000FF);
    const source = data, target = output instanceof Uint32Array ? output : new Uint32Array(output.buffer), length = Math.min(source.length, output.length);
    for (let i = 0, v; i < length; i++)
        v = source[i] & 0xFF, target[i] = ((v | v << 8 | v << 16 | v << 24) & mask) + filter; // v | v << 8 | v << 16 | 0xFF000000;
    return output instanceof TransientUint8ClampedArray ? output : new TransientUint8ClampedArray(output.buffer);
};

