import $global from './global-scope';

const main = typeof require === 'function' && require.main || undefined;

export const $require = (function $require(this: NodeModule, id: string, parent: NodeModule = this) {
    return typeof parent !== 'undefined' && parent.require
        ? parent.require(id)
        : typeof $global !== 'undefined' && $global['require']
            ? $global['require'](id)
            : eval(`require('${id}')`); // tslint:disable-line:no-eval
}).bind(main || module);
