/** Decorates a property making it configurable which is useful to override the default false when declaring a property as getter/setter */
function configurable(value: boolean): (target: any, propertyKey: string, descriptor: PropertyDescriptor) => void;
function configurable(target: any, propertyKey: string, descriptor: PropertyDescriptor): void;
function configurable(value: any) {
    value = arguments.length === 1 ? value : true;
    const decorate = (target: any, propertyKey: string, descriptor: PropertyDescriptor) => {
        descriptor.configurable = value === false ? false : true;
    };
    return (arguments.length === 1) && decorate || (decorate as any)(...arguments) || undefined;
}

