        // }

        // public template: Template;
        public root?: this;
        public $?: { [name: string]: HTMLElement };

        connectedCallback() {
            super.connectedCallback && super.connectedCallback.call(this);
            NerdyElement.prototype.connectedCallback.call(this);
        }

        disconnectedCallback() {
            super.disconnectedCallback && super.disconnectedCallback.call(this);
            NerdyElement.prototype.disconnectedCallback.call(this);
        }

        adoptedCallback() {
            super.adoptedCallback && super.adoptedCallback.call(this);
            NerdyElement.prototype.adoptedCallback.call(this);
        }

        attributeChangedCallback<name extends AttributeName, value extends AttributeValue>(attribute: name, previousValue: value, value: value) {
            super.attributeChangedCallback && super.attributeChangedCallback.call(this, attribute, previousValue, value);
            NerdyElement.prototype.attributeChangedCallback.call(this, attribute, previousValue, value);
        }

        protected _attachDom(dom) {
            const { root } = this.constructor as any as typeof NerdyElement;
            switch (root) {
                case 'light': return this.root = this, this.root.appendChild(dom);
                default: return super._attachDom(dom);
            }
            // const container = constructor.root === 'light' ? element : element.attachShadow({ mode: constructor.root === 'closed' ? 'closed' : 'open' });
        }

