export interface ProcessState<U = any, T = any, P = any> {
    id?: string;
    input?: T; parameters?: P; output?: U;
    state?: 'pending' | 'initializing' | 'ready' | 'executing' | 'completed' | 'failed' | 'aborted';
    next?: (process: this) => any;
    log?: Array<string | Error>;

    [name: string]: any;

    pre?(process?: this): void;
    post?(process?: this, result?: any): void;
    clean?(process?: this): void;
    abort?(reason?: false | string | Error, result?: any): false | string | Error;
    execute?(): Promise<this>;
}

export interface ProcessOptions<U = any, T = any, P = any> extends Partial<ProcessState<U, T, P>> { }

export class Process<U = any, T = any, P = any> implements ProcessState<U, T, P> { // implements Partial<PromiseLike<U>> {
    static id?: string;
    static instances = 0;
    static deferred = true;

    id?: string;
    input?: T; parameters?: P; output?: U;
    state?: 'pending' | 'initializing' | 'ready' | 'executing' | 'completed' | 'failed' | 'aborted' = 'pending';
    aborted = false; log: Array<string | Error> = []; next?: (process: this) => any;
    deferred?: boolean;
    [name: string]: any;

    constructor(process: { input?: T, parameters?: P, output?: U } & Partial<ProcessOptions<U, T, P>> = {} as any) {
        // Initialize (blocks premature execution)
        this.log.push(this.state = 'initializing');

        // Optionally initialize from process argument
        Object.assign(this, process);

        const constructor = new.target; //  this['constructor'];
        const instance = this.instance = (constructor.instances = (constructor.instances || 0) + 1);
        const id = this.id = `${this.id || constructor.id || 'Process'}[${instance}]`;

        this.deferred = !!('deferred' in process ? process.deferred : 'deferred' in constructor ? constructor.deferred : true);

        // Created localized execute (independent) and abort (prototypical) functions
        // const { execute } = this; // _abort // abort,
        const execute = this.execute, abort = this.abort = this.abort.bind(this); // , _abort = this['constructor'].prototype.abort;
        this.execute = (...args: any[]) => execute.call(this, ...args);
        // this.abort = (reason: string | Error, ...rest: any[]) => abort.call(this, reason, ...rest);
        // this.abort = (reason: string | Error, ...rest: any[]) => _abort.call(this, _abort === abort ? reason : abort.call(this, reason, ...rest), ...rest);
        this.log.push(this.state = 'ready');
        // const { log: { push } } = this; this.log.push = (... args: any[]) => (console.info(id, ... args), push.call(this.log, ... args));
    }

    // get _abort() { return Process.prototype.abort; }

    pre?(process: this): void;
    async process(process: this): Promise<U> { return this.output; }
    post?(process: this, result?: U): void;
    clean?(process: this): void;

    // abort(reason?: false | string | Error, result?: any): false | string | Error {
    abort(reason?: false | string | Error, type?: 'warn'): false | string | Error {
        if (reason === false || this.state !== 'executing') return false; // this.aborted !== false || // this.aborted === true ||
        if (this.aborted = true) {
            this.state = 'aborted';
            this.output && (this.output['outdated'] = true);
            this.log.push(reason ? `aborted: ${reason}` : 'aborted');
            reason && console[type] && setTimeout(() => (console[type] as any)(`${this.id || 'Process'}#abort: ${reason}`, this), 100);
        }
        return reason;
    }

    execute(): Promise<this> {
        const { id, deferred = true } = this; this.aborted = false; // console.log(`${id}#execute`); // console.group(`${id}#execute`);
        const defer = ((callback) => callback()), deferOptions = [];
        // const defer = deferred ? requestIdleCallback : (callback) => callback(), deferOptions = [{ timeout: 10 }]; // requestIdleCallback; // requestDeferCallback;
        const thisProcess = this;
        const { next, clean, abort } = this;
        let result: any = this.output, reason: any = false;
        const execute = (resolve, reject) => {
            const { input, parameters = {} } = thisProcess;
            // Delay until state is not "initializing"
            // if (this.state === 'initializing') return setTimeout(() => execute(resolve, reject), 50);
            // Abort if state is not "ready" knowing that it is also not "initializing"
            if (this.state !== 'ready') reject(`Cannot execute a process in the "${this.state}" state.`); // Error()
            // Capture the current pre, process, post, clean and abort functions
            const { pre, process, post } = this;
            // Set state to "executing" and reset completed/aborted
            this.log.push(this.state = 'executing'); this.aborted = false;
            // Local execution state
            // let result: any = this.output; // , reason: any = false, exception: Error;

            // Execute
            // try {

            defer(((
                /** @patch Safari 10.1.1 async arrow functions not equal to async functions ?! */
                async function execute(this: typeof thisProcess) {
                    // Execute pre callback to prepare the state of the process
                    if (!this.aborted && typeof pre === 'function') this.log.push('executing pre'), await pre.call(this, this);
                    // Execute process callback capturing output for optional post-processing (which may differ from this.output if handled by post)
                    if (!this.aborted && typeof process === 'function') this.log.push('executing process'), this.output = result = await process.call(this, this);
                    // Execute post passing the result (which must be identical to this.output afterward)
                    if (!this.aborted && typeof post === 'function') this.log.push('executing post'), await post.call(this, this, result);
                    this.log.push(this.state = 'completed'), resolve(this);
                    // if (this.next) this.next(this); // requestIdleCallback(() => this.next(this));
                    // if (typeof clean === 'function') clean.call(this, this); // this.log.push('executing clean'),
                    // (input || this.output) && console.debug(`${id}#execute`, { input, ...parameters, process: this });
                }).bind(thisProcess)), ...deferOptions);
            // } catch (e) {
            //     // Delegate decision for handling exceptions to the abort callback
            //     reason = abort(exception = e, result);
            //     if (reason !== false) return this.log.push(this.state = 'failed', reason, exception), reject(reason || exception);
            // } // finally {

            // /// Cleanup regardless of the outcome
            // /// if (typeof clean === 'function') clean.call(this, this); // this.log.push('executing clean'),
            // /// } // if (this.aborted) debugger; else console.log(this);
            // /// Reject only if reason is not false with a truthy reason or the exception as the reason
            // /// if (reason !== false) return this.log.push(this.state = 'failed', reason, exception), reject(reason || exception);
            // /// Resolve with this.output
            // /// this.log.push(this.state = 'completed'), resolve(this);
            // /// if (this.next) this.next(this); // requestIdleCallback(() => this.next(this));
            // /// }); // ();
        };
        const promise = new Promise<this>((resolve, reject) => execute.call(this, resolve, reject));
        (next || clean) && promise.then(_result => {
            next && next(thisProcess);
            clean && clean.call(thisProcess, thisProcess);
        });
        promise.catch(_reason => {
            thisProcess.aborted !== true && _reason && (reason = abort.call(thisProcess, _reason));
            clean && clean.call(thisProcess, thisProcess);
        });
        // promise.catch(_reason => _reason && (reason = thisProcess.abort(_reason)));
        // if (typeof this.next === 'function') promise.then(() => this.next(this));
        // if (typeof this.clean === 'function') promise.then(() => this.clean.call(this, this)), promise.catch(() => this.clean.call(this, this));
        return promise;
    }

}

