import { Components, NerdyElement, PolymerElement, CustomElement } from '~/framework';
import { notify, reflectToAttribute, Factory, KeyEvent, Section } from './common';
import BandInput from './band-input';

const bandInputSize = 5;
const bandInputStyle = `display: flex; flex: 1 1 auto; min-width: ${bandInputSize > 4 ? bandInputSize / 4 : 2}em; width: ${bandInputSize > 2 ? bandInputSize : 2}em; max-width: 100%; margin: 0.5em;`; // border: 1px solid blue; // 'flex: 1 0 50%; margin: 0;' min-width: 5em; max-width: 10em;
const bandInputGroupStyle = `display: flex; flex: 1 1 ${bandInputSize > 2 ? bandInputSize * 2 : 2}em; flex-flow: row wrap;`; // border: 1px solid red;

export declare type FilterControls = Section;
export const FilterControls = (({ addon = false, step = 1, atSuffix = 'suffix', numerify = (property, $nan = '⋯') => `[[asNumber(${property}, '${$nan}')]]` } = {}) => // <div flex-vertical>
    <div style='display: flex; flex-flow: row wrap-reverse;'>
        <div style='display: flex; flex: 1 1 25%; flex-flow: row wrap; place-content: center; place-items: center;'>
            {/* <ClarityIcon key='home' /> ⁣*/}
            <label style='display: flex; white-space: nowrap; justify-self: start; align-self: start; margin: 0.5rem; margin-right: 2rem;'>Filter</label>
            <div style='display: flex; flex: 1 1 auto; flex-flow: row wrap; place-content: space-between; place-items: center;'>
                <paper-toggle-button style='display: flex; flex: 1 1 5em; justify-self: center; align-self: center;' checked='{{bandPassMode}}' title='Toggle between band pass or stop filtering by simply inverting the pre-computed filter when multiplying it with the computed FFT data.'>[[_filterMode(bandPassMode)]]</paper-toggle-button>
                <paper-toggle-button style='display: flex; flex: 1 1 5em; justify-self: center; align-self: center;' checked='{{dynamicFilter}}' title='Enable or disable dynamically computed band when using images with supported resolution metadata.'>[[_filterMethod(dynamicFilter)]]</paper-toggle-button>
            </div>
        </div>
        <div style='display: flex; flex-wrap: wrap; flex: 1 1 75%; flex-flow: row wrap; place-content: space-between;'>
            <div style={bandInputGroupStyle}>
                <BandInput style={bandInputStyle} id='lowValueInput' label='[[_filterLabel("low")]]' value='{{bandFilter.low}}' min='0' step={step / 2}>
                    {addon && <span class='secondary' slot={atSuffix}>{numerify('bandFilter.lowValue')}</span>}
                </BandInput>
                <BandInput style={bandInputStyle} id='highValueInput' label='[[_filterLabel("high")]]' value='{{bandFilter.high}}' min='0' step={step / 2}>
                    {addon && <span class='secondary' slot={atSuffix}>{numerify('bandFilter.highValue')}</span>}
                </BandInput>
            </div>
            <div style={bandInputGroupStyle}>
                <BandInput style={bandInputStyle} id='bandValueInput' label='[[_filterLabel("band")]]' value='{{bandFilter.band}}' min='0' step={step}>
                    {addon && <span slot={atSuffix}>{numerify('bandFilter.bandValue')}</span>}
                </BandInput>
                <BandInput style={bandInputStyle} id='bandSizeInput' label='[[_filterLabel("size")]]' value='{{bandFilter.size}}' on-value-changed='_disableSymmetricFilter' min='0' step={step}>
                    {addon && <span slot={atSuffix}>{numerify('bandFilter.sizeValue')}</span>}
                </BandInput>
            </div>
        </div>
    </div>) as Factory<FilterControls, { attach(parent: PolymerElement): void; }>;

FilterControls.attach = (parent: PolymerElement) => {
    const inputHosts = [...parent.root.querySelectorAll('[band-input]') as any] as BandInput[];
    inputHosts.map((input) => BandInput.helper.initialize(input, parent));
};

export default FilterControls;
