import { Components, PolymerElement, StyleElement as Style, style } from '~/framework';
import { AppLocation, AppRoute } from 'modules/nerdy-polymer-elements';
import { Route, Routes } from '../app';
import { AppStyle } from '.';
// import NerdyAppStatus from './components/app-status';

const readOnly = true, notify = true;
const bound = { notify }; // readOnly

export class NerdyAppRouter extends PolymerElement {
    static tagName = 'nerdy-app-router';
    static properties = {
        page: { type: String, notify }, view: { type: String, notify }, // reflectToAttribute: true, observer: '_pageChanged'
        data: { notify }, route: { notify }, tail: { notify }, pattern: { notify },
        hash: { ...bound }, path: { ...bound }, query: { ...bound },
        useHash: { type: Boolean, value: () => window.location.protocol === 'file:' ? false : true },
    };

    static observers = ['_updatePage(data.page, routes)']; // , '_updatePath($.location.__path, $.location.__hash)'
    static root = 'light';

    static template = ({ }: NerdyAppRouter) =>
        <template>
            <AppLocation id='location' route='{{route}}' use-hash-as-path='[[useHash]]'></AppLocation>
            <AppRoute id='route' route='{{route}}' pattern='{{pattern}}' data='{{data}}' tail='{{tail}}'></AppRoute>
        </template> // __hash='{{hash}}' __path='{{path}}' __query='{{query}}'

    public $: { location: AppLocation; route: AppRoute; };

    public data: AppRoute['data'] = {};
    public route: Partial<AppRoute['route'] & AppLocation['route']> & { [name: string]: any } = {};
    public tail: AppRoute['tail'] = {};
    public pattern: AppRoute['pattern'] = '/:page';
    public routes: Routes;
    public view: string;
    public page: string;
    public hash: string;
    public path: string;
    public query: string;

    attached() {
        const appLocation = this.$.location, hash = appLocation && appLocation['__hash'] || window.location.hash || '';
        if (appLocation && appLocation.useHashAsPath && !/^\//.test(hash))
            appLocation['__hash'] = `/${hash}`; // console.log('NerdyRouter: %O', this, { hash, appLocation, location: { ... appLocation } });
    }

    _updatePage(page) {
        if (page === undefined) return; // Ignore Polymer initialization calls

        this.page = page; // = page && page.length ? page : 'entry';

        if (!this.routes || !this.routes['length']) return;

        const route = this.routes[page || 'entry'] || this.routes['fallback'];
        // this.route.current = route; // this.routes[page || 'entry'];
        this.set('route.current', route), this.set('route.name', route.name);
        this.set('view', route && (route.view || route.src) || undefined, undefined);

        // this.log(`_updatePage(${[...arguments].map(arg => typeof arg === 'string' ? `"${arg}"` : arg).join(', ')})`, { ['router.page']: this.page, ['router.view']: this.view, this: this });

        // // Close a non-persistent drawer when the page & route are changed.
        // if (!this.$.drawer.persistent) this.$.drawer.close();
    }

    // _updatePath(path, hash) { this.log(`_updatePath(${[...arguments].join(', ')})`, { this: this }); }

}

Components.define(NerdyAppRouter);

export default NerdyAppRouter;
