import { isFunction } from './type-checker';

// tslint:disable: unified-signatures ban-types align

export const OBJECT = Symbol('[[OBJECT]]'), ARRAY = Symbol('[[ARRAY]]'), OBJECTARRAY = Symbol('[[OBJECTARRAY]]');
export const ITERATOR = Symbol.iterator || Symbol('[[ITERATOR]]'), UNSCOPABLES = Symbol.unscopables || Symbol('[[UNSCOPABLES]]');
export const HANDLER = Symbol('[[HANDLER]]');

const { keys: keysOf, values: valuesOf } = Object;
const hasOwn = (object: object, property: PropertyKey): boolean => Object.prototype.hasOwnProperty.call(object, property);

// const preferSync = false;
const preferSync = ({ appVersion: version } = navigator) =>
    /Chrome/.test(version) ? (/(Chrome)\/(\d+)/.exec(version) || []).pop() < 55
        : /Safari/.test(version) ? (/(Safari)\/(\d+)/.exec(version) || []).pop() < 604
            : true;

export class ObjectArray<K extends object = object, T = valuesof<K>> extends (Array as any as typeof ImmutableArray) implements ImmutableArray<T> { // (Array as { new (... args: any[]): Partial<any[]>}) { // <T extends object>

    static scoped: PropertyKey[] = ['length', 'toJSON', OBJECT, ITERATOR, UNSCOPABLES];
    static inherited: PropertyKey[] = [...keysOf(Object.prototype), ...keysOf(Array.prototype)];
    static unsupported: PropertyKey[] = ['copyWithin', 'fill', 'push', 'pop', 'shift', 'unshift', 'sort', 'splice'];
    static reflected: Array<keyof ProxyHandler<object>> = ['apply', 'construct', 'has', 'getPrototypeOf', 'setPrototypeOf', 'preventExtensions', 'isExtensible'];
    static reserved: PropertyKey[];

    static [Symbol.hasInstance](instance) { return Array.isArray(instance) && instance[OBJECTARRAY] && instance[OBJECT]; } // && instance.constructor === ObjectArray

    static from(object: object) { // static from<T extends object, K extends keyof T, V extends T[K], U extends ObjectArray & T & V[]>(object: T): U { return new ObjectArray(object) as U; }
        return new ObjectArray(object);
    }

    constructor(object?: K) {
        if (Array.isArray(object = object[OBJECT] || object || {})) throw TypeError('ObjectArrays cannot be constructed from Array objects.');

        const instance = super(...valuesOf(object) as any[]) as any as ObjectArray & any[] & object;
        let array = instance[ARRAY] = Array.from(instance as any);

        const { prototype, reserved, unsupported, scoped, inherited, reflected } = ObjectArray;

        const properties = {
            constructor: ObjectArray,
            [UNSCOPABLES]: [].concat(object[UNSCOPABLES], array[UNSCOPABLES]),
            get [ITERATOR]() { return array[ITERATOR]; },
            get [OBJECT]() { return instance[OBJECT]; },
            set [OBJECT](object) { instance[OBJECT] = object; },
            get [OBJECTARRAY]() { return instance[OBJECTARRAY]; },
            set [OBJECTARRAY](objectArray) { objectArray[OBJECTARRAY] = objectArray; },
            get [ARRAY]() { return instance[ARRAY]; },
            set [ARRAY](array) { instance[ARRAY] = array; },
            get length() { return array.length; },
        };

        properties[OBJECT] = object, properties[OBJECTARRAY] = instance;

        const updateSync = () => {
            Array.prototype.splice.call(properties[ARRAY] = array = valuesOf(object), 0, instance.length, ...array);
        };
        const update = preferSync ? updateSync : async () => updateSync();
        const unsupportedMethod = () => { throw ReferenceError(`Cannot modify a read-only array.`); };
        const methodFor = (property) => properties[property] || (
            properties[property] = unsupported.indexOf(property) > -1 ? unsupportedMethod
                : (hasOwn(prototype, property) && typeof prototype[property] === 'function')
                    ? (...args) => prototype[property].call(instance, ...args)
                    : (...args) => instance[property].call(array, ...args)
        );

        const handler: ProxyHandler<this> = {
            ownKeys: (target) => ['length'].concat(Reflect.ownKeys(target) as string[], Reflect.ownKeys(object) as string[]),
            getOwnPropertyDescriptor: (target, property) => Object.getOwnPropertyDescriptor(object, property) || Object.getOwnPropertyDescriptor(array, property),
            get: (target, property) => (
                property in properties ? properties[property]
                    : property in target ? target[property]
                        : typeof property === 'symbol' ? (property in instance ? instance[property] : object[property])
                            : !isNaN(property) ? property < 0 ? array[array.length + Number(property)] : array[property]
                                : typeof instance[property] === 'function' ? methodFor(property)
                                    : object[property]
            ),
            set: (target, property, value) => {
                if (property in target) return Reflect.set(target, property, value);
                const propertyType = typeof property; // const index = typeof property !== 'symbol' && !isNaN(property) ? property : -1
                const readonly = reserved.indexOf(property) > -1 || (propertyType !== 'symbol' && !isNaN(property));
                if (readonly) return true;
                else if (propertyType === 'symbol') return properties[property] = value, true;

                const previouslyDefined = property in object, previousValue = object[property];
                const result = Reflect.set(object, property, value);
                return (!previouslyDefined || previousValue !== object[property]) && update(), result;
            },

            deleteProperty: (target, property) => {
                if (reserved.indexOf(property) > -1) return false;
                const previouslyDefined = property in object;
                const result = Reflect.deleteProperty(object, property);
                return previouslyDefined && !(property in object) && update(), result;
            },
        };

        for (const trap of reflected) handler[trap] = (target, ...args: any[]) => (Reflect[trap] as any)(object, ...args);

        const proxy = new Proxy(this, handler);

        return proxy;
    }

    remap<U>(callback: (this: ObjectArray | object, value: any, key: string, index: number, objectArray: this) => U, thisArg: ObjectArray | object = this): U[] {
        // if (Array.isArray(this)) return (this as any).map(...arguments) as any[];
        const object = this[OBJECT]; // , mapper = args[0], thisArg = args[1] || object || this;
        if (typeof callback !== 'function') throw TypeError('Cannot map an ObjectArray without a valid mapper function as the first argument');
        if (object) {
            const keys = keysOf(object), values: U[] = [];
            for (let i = 0; i < keys.length; i++) {
                const key = keys[i], value: U = object[key];
                const mapped: U = callback.call(thisArg || this, value, keys[i], i, this);
                // values[i] = callback.call(thisArg, value, keys[i], i, this);
            }
            return values;
        }
    }

    toJSON() { return this[OBJECT]; }

}

ObjectArray.reserved = [].concat(ObjectArray.scoped, ObjectArray.unsupported, ObjectArray.inherited, keysOf(ObjectArray.prototype));

export default ObjectArray;

(function Spec() {
    // tslint:disable:prefer-for-of

    const object = { a: 'A', b: 'B' };
    const objects = [1, 2, 3].map(() => ({ ...object }));
    const objectArray = new ObjectArray(object);
    const objectArrays = objects.map(object => new ObjectArray(object));

    delete objectArrays[0]['a'];
    objectArrays[1]['c'] = objectArrays[2]['c'] = 'C';
    delete objectArrays[1]['b'];
    objectArrays[2]['a'] = 'Å';

    const stringify = (object, ...args) => (JSON.stringify as any)(object, ...args); // .replace(/\"/g, quote);
    // const stringify = (object: object, ...args: any[]) => Object.setPrototypeOf({ [Symbol.toPrimitive]: () => JSON.stringify(object) }, object);
    const forOf = (subject, values: any[] = []) => { for (const i of subject) values.push(i); return values; };
    const forIn = (subject, values: object = {}) => { for (const k in subject) values[k] = subject[k]; return values; };
    const forAll = (subject, values: any[] = []) => { for (let i = 0; i < subject.length; i++) values.push(subject[i]); return values; };

    const spec = (subject, { separator = ',', quote = '' } = {}) => (subject = subject || {},
        // subject.reduce((results, value, index = 0) => ( results[`[${index}]`] = value, results),
        {
            ['[ object ]']: subject[OBJECTARRAY] || subject,
            // ['[ interface ]']: typeof subject !== 'object' ? typeof subject : subject[OBJECTARRAY] || subject[OBJECT] || subject,
            // typeof: typeof subject, isArray: Array.isArray(subject), isObjectArray: subject instanceof ObjectArray,
            // type: typeof subject !== 'object' ? typeof subject : !Array.isArray(subject) ? 'object' : !(subject instanceof ObjectArray) ? 'Array' : 'ObjectArray',
            keys: Array.from(keysOf(subject) || []).join(separator),
            ownKeys: Reflect.ownKeys(subject).join(separator),
            values: valuesOf(subject).join(separator),
            from: Array.from(subject).join(separator),
            join: subject.join(separator),
            JSON: JSON.stringify(subject),
            ['for … in']: stringify(forIn(subject, {})),
            ['for … of']: stringify(forOf(subject, [])),
            ['for …']: stringify(forAll(subject, [])),
            ...keysOf(subject).reduce((values, key, index) => (values[`[${key}]`] = subject[key], values[`[${index}]`] = subject[index], values), {}),
        });

    const results = [spec(objectArray), ...objectArrays.map(spec as any)];
    console.table(results);

});

export declare class ImmutableArray<T = any> extends Array<T> implements Partial<ReadonlyArray<T>> {
    constructor();
    constructor(...args: T[]);

    copyWithin(target: number, start: number, end?: number): never;
    copyWithin(...args: any[]): never;

    fill(value: number, start?: number, end?: number): this;
    fill(...args: any[]): never;

    push(...items: any[]): never;
    push(...args: any[]): never;

    pop(): never;
    pop(...args: any[]): never;

    shift(): never;
    shift(...args: any[]): never;

    unshift(...items: any[]): never;
    unshift(...args: any[]): never;

    sort(compareFn?: (a: number, b: number) => number): never;
    sort(...args: any[]): never;

    splice(start: number, deleteCount?: number): never;
    splice(start: number, deleteCount: number, ...items: any[]): never;
    splice(...args: any[]): never;
}

export declare type valuesof<T extends object = object> = T[keyof T];

