/// <reference path="types.d.ts" />

import { bind, Constructor } from './decorators';
import { isCustomTagName, isElement, isFunction, isString, defer, isBrowser } from './helpers';
import ElementsFactory, { ElementFactory, ElementFactoryMethod, CommentFactory, CommentFactoryMethod } from './elements-factory';
import TemplatesFactory, { } from './templates-factory';
import { getTagName, tagNames, classNames, Element } from './native-elements';

/**
 * NerdyElementsRegistry is a facade/singleton object for
 * runtime management of custom elements.
 */
export class ElementsRegistry {
    public constructors: Constructor[] = [];
    public tags: string[] = [];
    public elementFactory = new ElementsFactory(this);
    public templateFactory = new TemplatesFactory(this);
    public createElement: ElementsFactory.createElement = this.elementFactory.create; // public get createElement(): ElementsFactory.createElement { return this.elementFactory && this.elementFactory.create; }
    public createComment: ElementsFactory.createComment = this.elementFactory.createComment; // public get createComment(): ElementsFactory.createComment { return this.elementFactory && this.elementFactory.createComment; }
    public createTemplate: TemplatesFactory.create = this.templateFactory.create; // public get createTemplate(): TemplatesFactory.create { return this.templateFactory && this.templateFactory.create; }
    public defineTemplate: TemplatesFactory.define = this.templateFactory.define; // public get defineTemplate(): TemplatesFactory.define { return this.templateFactory && this.templateFactory.define; }

    _defined: Set<string | Constructor> = new Set();

    define(constructor: ElementConstructor<element>, options: Partial<ElementDefinitionOptions & { extends: Constructor }> = {}) {
        const { tagName } = constructor;

        if (this._defined.has(tagName)) throw ReferenceError(`Cannot define the ${tagName || '<unnamed>'} element since the tag name has already been defined;`);
        if (this._defined.has(constructor)) throw ReferenceError(`Cannot define the ${tagName || '<unnamed>'} element since the constructor has already been defined;`);

        this._defined.add(tagName), this._defined.add(constructor);

        const base = options && options.extends || constructor.extends;

        if (isFunction(base) && !options.extends) options.extends = ElementsRegistry.resolveTagName(base);
        // if (!options.extends) throw TypeError(`Cannot resolve base element's tagName!`);

        return this.register(tagName, constructor, options);
    }

    register(tagName: string, constructor: ElementConstructor<element>, options?: Partial<ElementDefinitionOptions>);
    register<Element extends element, Constructor extends ElementConstructor<Element>>(tagName: string, constructor: Constructor & any, options: Partial<ElementDefinitionOptions> = {}): Constructor {
        ElementsRegistry.validateTagName(tagName);
        ElementsRegistry.validateConstructor(constructor);
        this.registerTagName(tagName);
        this.registerConstructor(constructor, tagName);
        ElementsRegistry.registerCustomElement(tagName, constructor, { extends: 'div', ...options });
        return constructor;
    }

    registerTagName<Tag extends string>(tagName: Tag): Tag {
        if (this.tags.includes(tagName)) throw RangeError(`Tag name "${tagName}" is already in use.`);
        return (this.tags.push(tagName), tagName);
    }

    include<Constructor extends ElementConstructor<element>>(constructor: Constructor): boolean;
    include(tagName: string): boolean;
    include<Constructor extends ElementConstructor<element>>(entity: any): boolean {
        return isString(entity) ? false : this.constructors.includes(entity as Constructor);
    }

    registerConstructor<Constructor extends ElementConstructor<element>>(constructor: Constructor, tagName?: string): Constructor {
        const alreadyRegistered = this.include(constructor); // this.constructors.includes(constructor);
        this.constructors.push(constructor);
        if (alreadyRegistered) console.warn(`Constructor ${constructor.name} is now registered multiple times with different tags.`);
        if (isCustomTagName(tagName)) Object.defineProperty(constructor, 'name', { value: constructor.tagName = tagName });
        return constructor;
    }

}

export namespace ElementsRegistry {

    export function resolveTagName(entity: any) {
        return isString(entity) ? (isCustomTagName(entity) || tagNames.includes(entity))
            : !isFunction(entity) ? null :
                isCustomTagName(entity['tagName']) ? entity['tagName']
                    : isCustomTagName(entity['is']) ? entity['is']
                        : /HTML(\w+)Element/.test(entity.name) ? getTagName(entity as any)
                            : null;
    }

    export function validateTagName(tagName: any) {
        // if (!validateTagName['values']) validateTagName['values'] = {};
        // if (validateTagName['values'][tagName] === true) return true;
        if (validateTagName['lastValid'] === tagName) return true;
        if (!isCustomTagName(tagName)) throw TypeError(!isString(tagName)
            ? `Tag name of type "${typeof tagName}" is not allowed, expecting a string instead.`
            : `Tag name "${tagName}" is not a valid custom element tag (try adding a prefix to include at lease one dash in the name to conform to v1 specs).`);
        validateTagName['lastValid'] = tagName;
        // validateTagName['values'][tagName] = true;
    }

    export function validateConstructor(constructor: { new (...args: any[]): object } | ElementConstructor<element> | any) {
        if (validateConstructor['lastValid'] === constructor) return true;
        if (!(isFunction(constructor) && constructor.prototype instanceof Element)) throw TypeError(`Constructor of type ${typeof constructor} is not allowed, expecting a constructor function with a valid Element prototype.`);
        validateConstructor['lastValid'] = constructor;
    }

    export async function registerCustomElement(tagName: string, constructor: ElementConstructor<element>, options: ElementDefinitionOptions = {} as any) {
        if (!isBrowser) return false; // throw ReferenceError(`Cannot register custom element when executing in a non-browser context`);
        validateTagName(tagName), validateConstructor(constructor); // Limited memoization helps keep redundancy to a minimum
        if (isCustomTagName(options.extends)) await window.customElements.whenDefined(options.extends);
        if (typeof constructor['canRegister'] === 'function') constructor = await constructor['canRegister']();
        window.customElements.define(tagName, constructor as any, options);
        return constructor;
    }

}

const assert = console.assert;

export default ElementsRegistry;
