import { Components, PolymerElement, StyleElement as Style, style } from '~/framework';
import { AppStyle, AppStatus, AppRouter } from '.';
import { AppDrawer, AppDrawerLayout, AppHeader, AppHeaderLayout, AppToolbar, IronPages, IronSelector, PaperIconButton, IfTemplate as Conditional } from 'modules/nerdy-polymer-elements';

export class NerdyAppShell extends PolymerElement {
    // static root = 'light';
    static properties = {
        displayTitle: { type: String },
        sitename: { type: String, reflectToAttribute: true, notify: true },
        drawerless: { type: Boolean, reflectToAttribute: true },
    };

    static template = ({ }: NerdyAppShell) =>
        <template>
            <AppStyle></AppStyle>
            <AppDrawerLayout id='shell' fullbleed responsive-width='960px'>
                <Conditional if='{{!drawerless}}'>
                    <AppDrawer id='drawer' slot='drawer'>
                        <AppToolbar id='drawer-toolbar'><slot name="drawer-toolbar">Menu</slot></AppToolbar>
                        <slot name="drawer-content"></slot>
                    </AppDrawer>
                </Conditional>
                <AppHeaderLayout has-scrolling-region>
                    <AppHeader id='header' slot='header' x-condenses x-reveals effects='waterfall'>
                        <AppToolbar id='topbar'>
                            <PaperIconButton icon='my-icons:menu' drawer-toggle></PaperIconButton>
                            <div main-title><span>[[sitename]]</span><span>[[title]]</span></div>
                        </AppToolbar>
                        <slot name="main-header"></slot>
                    </AppHeader>
                    <slot name="main-content"></slot>
                </AppHeaderLayout>
            </AppDrawerLayout>
        </template>
    // <div main-title><slot name="topbar-title">[[sitename]][[title]]</slot></div>
    // <AppToolbar id='toolbar'><div main-title><slot name="main-toolbar-title">[[title]]</slot></div></AppToolbar>
    // <div main-title><slot name="main-toolbar-title">[[title]]</slot></div>
    // <slot name="main-toolbar"></slot>

    public sitename: string;
    public displayTitle: string;
    public drawerless: boolean;

    public $: { drawer: AppDrawer, header: AppHeader, toolbar: AppToolbar, 'drawer-toolbar': AppToolbar };

    public get $drawer() { return this.$ && this.$.drawer || null; }
    public get $header() { return this.$ && this.$.header || null; }
    public get $toolbar() { return this.$ && this.$.toolbar || null; }

    /**
     * @param {boolean | "force"} force close persistent drawer
     */
    closeDrawer(force?: boolean | 'force');
    closeDrawer(force: boolean | 'force' = false, { $: { drawer } } = this) {
        if (drawer && (!drawer.persistent || force) && drawer.close) drawer.close();
    }

}

Components.define(NerdyAppShell);

export default NerdyAppShell;
