// tslint:disable: align ban-types

import { isFunction, isObject } from './type-checker';

export class Spec<spec> {
    [name: string]: any;

    static merge = (result = {}, spec: any) => Object.assign(result || {}, isFunction(spec) ? spec(result) : isObject(spec) ? spec : {});

    constructor(spec: spec, ...specs: any[]) {
        spec = [].reduce.call(arguments, Spec.merge);

        if (arguments.length > 1 || typeof arguments[0] === 'function') return new Spec(spec);
        Object.assign(this, spec);
    }
}

export default Spec;
