/* FLEX */
export const flex: styling = { display: 'flex', flex: 1, flexDirection: 'column', flexBasis: 'auto' };

Object.seal(Object.setPrototypeOf(flex, {
    inline: Object.seal({ display: 'inline-flex' }),
    center: Object.seal(Object.setPrototypeOf({ ...flex, placeContent: 'center', justifyContent: 'center', alignContent: 'center' }, {
        content: Object.seal({ ...flex, placeContent: 'center', justifyContent: 'center', alignContent: 'center' }),
        items: Object.seal({ ...flex, placeItems: 'center', justifyItems: 'center', alignItems: 'center' }),
    })),
}));

export const vertical = Object.seal({ flexDirection: 'column' });
export const horizontal = Object.seal({ flexDirection: 'row' });

export const Flex: style = Object.setPrototypeOf({
    '[flex-horizontal]': { ...flex, justifyContent: 'space-between', flexDirection: 'row' },
    '[flex-vertical]': { ...flex, justifyContent: 'space-between', flexDirection: 'column' },
    '[flex-horizontal] > *, [flex-vertical] > *': { flexGrow: 1 },
    /** Horizontal */
    '[flex-horizontal] > *:not([hidden])': { margin: '0 var(--flex-padding, 0.5em)' },
    '[flex-horizontal] > *:not([hidden]):first-child': { marginLeft: 0 },
    '[flex-horizontal] > *:not([hidden]):last-child': { marginRight: 0 },
    /** Vertical */
    '[flex-vertical] > *:not([hidden])': { margin: 'var(--flex-padding, 0.5em) 0' },
    '[flex-vertical] > *:not([hidden]):first-child': { marginTop: 0 },
    '[flex-vertical] > *:not([hidden]):last-child': { marginBottom: 0 },
    /** Center */
    '[flex-center-items]': { ...flex['center']['items'] },
    '[flex-center-content]': { ...flex['center']['content'] },
    '[flex-center]': { ...flex['center']['items'], ...flex['center']['content'] },
}, flex); // as style & Partial<typeof flex>;  & Partial<typeof flex>

/* FLEX */
export const grid: styling = { display: 'grid' }; // grid: 1, gridDirection: 'column', gridBasis: 'auto'

Object.seal(Object.setPrototypeOf(grid, {
    inline: Object.seal({ display: 'inline-grid' }),
    center: Object.seal(Object.setPrototypeOf({ ...grid, placeContent: 'center', justifyContent: 'center', alignContent: 'center' }, {
        content: Object.seal({ ...grid, placeContent: 'center', justifyContent: 'center', alignContent: 'center' }),
        items: Object.seal({ ...grid, placeItems: 'center', justifyItems: 'center', alignItems: 'center' }),
    })),
}));

/* PATTERNS */

export const colorFill = (color = 'transparent', alpha = 1) => (
    `url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="1" height="1" style="opacity: ${alpha >= 0 ? alpha : 1}; fill: ${color};">
        <rect width="1" height="1" />
    </svg>')`
).replace(/\s*\n\s*/g, '');

export const $ColorFill = (
    `url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="10" height="10" style="opacity: var(--color-alpha, 1);">
        <rect x="0" y="0" width="50" height="50" fill="white" style="fill: var(--color-fill, transparent);" />
    </svg>')`
).replace(/\s*\n\s*/g, '');

export const $CheckerboardSize = `var(--checkerboard-size, 25px)`; // `var(--checkerboard-size, calc(${$CheckerboardSpace} + ${$CheckerboardSpace} + ${$CheckerboardSpace} + ${$CheckerboardSpace}))`;
export const $Checkerboard = { $CheckerboardSize };
export const $CheckerboardImage = (
    `url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100" style="opacity: 0.125;">
        <rect x="50" y="0" width="50" height="50" fill="black" />
        <rect x="0" y="50" width="50" height="50" fill="black" />
        <rect x="0" y="0" width="50" height="50" fill="white" />
        <rect x="50" y="50" width="50" height="50" fill="white" />
    </svg>')`
).replace(/\s*\n\s*/g, '');

export const Patterns: style = {
    checkerboard: {
        ...$Checkerboard,
        background: $CheckerboardImage,
        backgroundPosition: `50% 50%`, backgroundSize: `${$CheckerboardSize} ${$CheckerboardSize}`,
    },
};

/* COMMON */
export const rounded = Object.seal({ borderRadius: '0.5rem' });
export const padded = Object.seal({ padding: '0.25rem' });
export const contained = Object.seal({ objectFit: 'contain', maxWidth: '100%', maxHeight: '100%' });
export const unselectable = Object.seal({ userSelect: 'none', WebkitUserSelect: 'none' });
export const inert = Object.seal({ pointerEvents: 'none', ...unselectable });
export const ease = Object.seal({ transition: 'all 125ms ease-in-out' });
export const sticky = Object.seal({ position: 'sticky', top: '-1px', zIndex: 1 });
export const blurring = Object.seal({ backdropFilter: 'blur(5px)', WebkitBackdropFilter: 'blur(5px)' });

export const Paper: style = {
    '.primary': 'color: var(--primary-text-color); @apply --paper-font-body2' as any,
    '.secondary': 'color: var(--secondary-text-color); @apply --paper-font-body1' as any,
};

export const View: style = {
    ...globalStyle,
    ':root': {
        $Foreground: 'rgba(0, 0, 0, 0.95)', $Background: 'rgba(255, 255, 255, 0.85)',
        $PaperTabInk: 'var(--primary, gray)', $PaperTabsSelectionBarColor: 'var(--primary, gray)',
    },
    ':host': {
        ...flex['center'], flexDirection: 'column', ...ease,
    },
    ':host > #toolbar': {
        color: 'var(--foreground, #222)', backgroundColor: 'var(--background, #eee)',
    },
    ':host > #toolbar[blurring]': { ...blurring },
    ':host > #toolbar[sticky]': { ...sticky },
    ':host > #content': { ...flex['center'], flexDirection: 'column', ...padded }, // padding: '0 0.5rem'
    // '[sticky]': { position: '-webkit-sticky' },
    ...Flex,
};

Object.assign(View[':host'], View[':root']);

