import { Components, NerdyElement, PolymerElement, CustomElement } from '~/framework';
import { PaperButton as Button, PaperCard as Card, PaperInput as Input } from 'modules/nerdy-polymer-elements';
import { notify, reflectToAttribute, stop, factory, AbstractEvent as Event, KeyEvent, Factory } from './common';

/* BAND-INPUT-HELPER */
export class BandInputHelper {
    static events = ['keydown', 'keyup', 'keypress'];

    static initialize(element: BandInput, root?: NerdyElement) {
        const { nativeInput } = element.$ || {} as typeof element;
        if (!nativeInput) Input.whenDefined.then(() => BandInputHelper.initialize(element));
        else nativeInput['bandInputHandler'] = new BandInputHelper(nativeInput, element, root || element['domHost']);
        return element;
    }

    log?: NerdyElement['log'];
    downKey?: any; upKey?: any; updateTimeout?: any; defaultStep?: number = 1; defaultType?: BandInput['type'] = 'number';

    constructor(public element: HTMLInputElement, public host: BandInput = element['__dataHost'], public root?: NerdyElement) {
        const capture = false, { events = BandInputHelper.events } = this.constructor as typeof BandInputHelper;
        if (root && root.log) this.log = (root.log['@BandInput'] || root.log) as this['log'];
        if (!this.handle) this.handle = ((type: string, event: Event) => this.log['type'](event)) as any;
        for (const type of events) element.addEventListener(type, this.handle.bind(this, type), capture);
        if (this.handleFocused) host.addEventListener('focused-changed', () => this.handleFocused(), false); // () => { if (host) { host['alwaysFloatLabel'] = host.focused, element.type = host['type'] || 'number'; } }, false);
        if (host && 'keyboardEventMatchesKeys' in host) this.keyEventMatches = host.keyboardEventMatchesKeys; // .bind(host);
        if (host && host.$ && host.$.container && host.$.container.style) host.$.container.style.width = '100%';
    }

    keyEventMatches = (event: KeyEvent, eventString: string): boolean => null;

    handleFocused({ host, defaultType = 'number', host: { type = defaultType }, element } = this) {
        if (host.focused) {
            element.type = 'text', element.setAttribute('inputmode', 'numeric');
        } else {
            element.type = 'number';
            this.update(null, 0);
        }
        if (host) host['alwaysFloatLabel'] = host.focused;
    }

    handle(event: KeyEvent); handle(type: string, event: KeyEvent);
    handle(type: string | KeyEvent = null, event: KeyEvent = {} as KeyEvent) {
        if (typeof type === 'object') return this.handle(type['type'] || null, event = type as any);
        const defaultStep = 1, stepKey = (/Arrow(Up|Down)/.exec(event.key) || '')[1] as 'Up' | 'Down';
        return (stepKey) ? this.handleStep(event, stepKey) : event.key === '.' ? this.handleDecimal(event) : event.key === 'Backspace' ? this.handleDelete(event) : false;
    }

    update(value = null, timeout = 75) {
        clearTimeout(this.updateTimeout);
        const { element, host } = this;
        if (!element && !host) return;
        this.updateTimeout = setTimeout(() => {
            const newValue = value !== null ? value : (element || {} as any)['value'];
            host && host['updateValueAndPreserveCaret'] && host['updateValueAndPreserveCaret'](newValue);
        }, timeout > -1 ? timeout : 75);
    }

    handleStep(event: KeyEvent, stepKey: 'Up' | 'Down' = (/Arrow(Up|Down)/.exec(event.key) || '')[1] as 'Up' | 'Down', { defaultStep, host, element, element: { step = defaultStep, min = NaN, max = NaN, value = NaN } } = this) {
        const { type, ctrlKey, shiftKey, altKey, repeat } = event;
        const stepper = `step${stepKey}`, up = stepKey === 'Up', down = stepKey === 'Down';
        if (typeof element[stepper] === 'function') Event.stop(event); else return;
        const round = (value) => Math[up ? 'ceil' : down ? 'floor' : 'round'](value);
        let nextStep = Number(step) || defaultStep;
        value = Number(value || min) || 0;
        element.type = 'number';
        const state: any = this[`${stepKey}Key`] || (this[`${stepKey}Key`] = {});
        if (type === 'keyup') {
            nextStep = state.repeated ? 0 : defaultStep * (ctrlKey ? 0.25 : 1);
            state.repeats = null, state.repeated = null;
        } else if (type === 'keydown') {
            state.repeated = repeat, state.repeats = (state.repeats || 0) + 1;
            nextStep = state.repeated ? defaultStep * (ctrlKey ? 0.25 : shiftKey ? Math.min(5, state.repeats || 1) : 1) : 0;
        }
        element.step = nextStep || defaultStep as any;
        // if ((value / nextStep) % 1) element.value = round(value / nextStep) * nextStep as any;
        if ((value / nextStep) % 1) this.update(round(value / nextStep) * nextStep, 0);
        else if (nextStep > 0) element[stepper](), element.step = defaultStep as any, this.update(null, ctrlKey ? 0 : undefined); //  / 4
    }

    handleDecimal(event: KeyEvent, { element, element: { min = NaN, max = NaN, value = NaN } } = this) {
        element.type = 'text';
        if (event.key === '.' && element.value.indexOf('.') > -1) Event.stop(event); // , false;
        const [match, sign, integer = isNaN(min) ? Math.round(value as any) || 0 : min, decimal = 0] = /([\-\+]?)(\d*)(?:\.?)(\d*)/.exec(element.value) || ''; // , isNaN(min) ? Math.round(value as any) || 0 : min, 0];
        const newValue = `${sign}${integer || '0'}.${decimal || '0'}`;
        element.value = newValue.replace(/(\..*?)\..*?$/, '$1');
        element.setSelectionRange(newValue.indexOf('.') + 1, newValue.length);
    }

    handleDelete(event: KeyEvent, { element, element: { min = NaN, max = NaN, value = NaN } } = this) {
        element.type = 'text';
        const decimalPosition = element.value.indexOf('.');
        if (decimalPosition > -1) {
            const deltaStart = element.selectionStart - decimalPosition;
            if ((!deltaStart || deltaStart === 1) && element.selectionEnd > element.selectionStart) {
                Event.stop(event);
                const [match, sign, integer = isNaN(min) ? Math.round(value as any) || 0 : min, decimal = 0] = /([\-\+]?)(\d*)(?:\.?)(\d*)/.exec(value) || ''; // , isNaN(min) ? Math.round(value as any) || 0 : min, 0];
                const newValue = `${sign}${integer}`;
                element.value = newValue;
                element.selectionEnd = (element.selectionStart = Math.min(newValue.length, element.selectionStart)) + 1;
                this.update(Number(newValue));
            } else if (deltaStart > - 1) {
                Event.stop(event);
                const newValue = element.value.slice(0, element.selectionStart) + element.value.slice(element.selectionEnd);
                element.value = newValue.replace(/(\..*?)\..*?$/, '$1');
                element.selectionEnd = element.selectionStart = Math.min(newValue.length, element.selectionStart);
                setTimeout(() => this.handleDecimal(event), 0);
            }
        }
    }
}

/* BAND-INPUT ELEMENT */
export declare type BandInput = Input & Partial<HTMLInputElement>;
export const BandInput = factory({
    template: () => (<Input band-input type='number' prevent-invalid-input allowed-pattern='[\.0-9]' inputmode='numeric' /> as BandInput),
    helper: BandInputHelper,
});

export default BandInput;
