/// <reference types="src/globals" />
import { style, styling, StylesObject } from 'modules/nerdy-elements';

import { map, reduce } from './type-cast';

const { defineProperty, assign, getPrototypeOf, hasOwnProperty } = Object;

const selectorFunctions = new WeakMap();

export const merge = (style = {} as style, ... styles: style[]): style => {
    // tslint:disable-next-line:prefer-object-spread
    if (typeof style !== 'object') style = Object.assign({}, style) as style;

    let prototype: { [name: string]: Set<any> } = getPrototypeOf(style);
    if (prototype === Object.prototype) Object.setPrototypeOf(style, prototype = {});

    const mergeStyle = (target, source) => {
        if (typeof target === 'function') target = target();
        if (typeof target !== 'object') target = {};
        if (!source) return;
        if (typeof source === 'function') source = source();
        if (typeof source === 'object') Object.assign(target, source);
        return target;
    };

    const defineSelector = (selector) => !hasOwnProperty.call(style, selector) && defineProperty(style, selector, {
        set: (value) => prototype[selector]['this'] = value,
        get: () => {
            const own = prototype[selector]['this'];
            const merged = prototype[selector].values();
            const style = mergeStyle({}, own);
            for (const _style of merged) mergeStyle(style, _style);
            return style;
        },
    });

    for (const _style of styles) {
        if (typeof _style === 'object') {
            for (let selector in _style) {
                if (!(selector = typeof selector === 'string' && selector.trim())) continue;
                if (!(selector in prototype)) {
                    const styleValue = [style[selector]] || {}, styleSelector = () => styleValue;
                    prototype[selector] = new Set(), prototype[selector]['this'] = styleSelector;
                    delete style[selector];
                }
                if (selector in style) {
                    if (!selectorFunctions.has(_style)) selectorFunctions.set(_style, {});
                    const _styleSelectors = selectorFunctions.get(_style);
                    const _styleSelector = _styleSelectors[selector] = _styleSelectors[selector] || (() => _style['selector']);
                    prototype[selector].add(() => _style['selector'] );
                    defineSelector(selector);
                }
            }
        }
    }
    return style;
};

// tslint:disable: ban-types callable-types align

declare namespace Selectors {
    export interface Selectors extends Object { [name: string]: Object[keyof Object] | selector; }
    export interface Selector extends Partial<Selectors> { $: string; toString(): string; }
    export interface SelectorFactory<T = Selector> { (selectors?: selector, $?: string): T; }
    export type selector = string | Selector | Selectors;
    export class SelectorClassInterface implements Selector {
        static selector(selector: selector, fallback?: string): string;
        static selectors(selector: selector, fallback?: Partial<Selector>): Partial<Selector>;
        static define(selectors?: selector, $?: string): Partial<Selector>;
        // static define(selector: SelectorClassInterface, selectors?: selector, $?: string): Partial<Selector>;
        static define(...args: any[]): Partial<Selector>;
        $: string;
        constructor(selectors?: selector, $?: string);
        constructor(...args: any[]);
        toString(): string;
        [name: string]: Object[keyof Object] | selector;
    }
}

namespace Selectors {

    const SelectorClass = class Selector {

        static stringifier = 'toString';
        static selector = (selector: selector, fallback: string = ''): string => ((typeof selector === 'string' ? selector : selector['$']) || fallback) as string;
        static selectors = (selector: selector, fallback: object = {}): Selectors => (typeof selector === 'object' ? selector : fallback) as Selectors;
        static define(selectors: selector = {}, $: string = Selector.selector(selectors)) {
            const target = this !== Selector ? this : selectors;
            defineProperty(target, '$', { get: () => $, set: () => { }, configurable: true });
            reduce(selectors as Selectors, (object, item: selector, key: string) => (
                key === '$' || !isNaN(key) || !/(object|string)/.test(typeof selectors[key])
                    ? object : assign(object, { [key]: new Selector(Selector.selectors(item), Selector.selector(item, key).replace(/^(\…|\.\.\.)/, $)) })
            ), target);
            return target;
        }

        $: string;

        constructor(selectors: selector = {}, $: string = Selector.selector(selectors)) { Selector.define.call(this, ...arguments); }

        toString() { return this.$; }

    } as any as typeof SelectorClassInterface;

    export const Selector = function SelectorFactory(selectors?: selector, ...args: any[]) {
        return (selectors instanceof Selector) ? SelectorClass.define(...arguments) : new SelectorClass(...arguments);
    } as typeof SelectorClass & SelectorFactory<Selector>; // & { new (selectors?: selector, $?: string): Selector }

    Object.setPrototypeOf(Selector, SelectorClass); // Selector('a'); new Selector('a');
}

import Selector = Selectors.Selector;

export { Selector as Selector, Selector as Selectors };

export const normalizeProperty = (property) => typeof property === 'string' ? property.replace(/^\$/, '--').replace(/([a-z])([A-Z])/g, '$1-$2').toLocaleLowerCase() : '';

export namespace Variables {
    // tslint:disable-next-line:prefer-object-spread
    export const Var = Object.assign(function Var(this: (string & typeof Var) | void, name?: string, fallback?: string) {
        const args = arguments.length < 3 ? [...arguments] : [arguments[0], arguments[arguments.length - 1]].slice(0, Math.max(arguments.length, 2));
        if (typeof this !== 'symbol') {
            const boundVar = Var.bind(Symbol('var'), ...args); boundVar['toString'] = () => boundVar();
            return (args.length === 0)
                ? boundVar as string & ((name?: string, fallback?: string) => string) & typeof Var
                : boundVar as string & ((fallback?: string) => string) & typeof Var;
        }
        return args.length ? Var['stringify'](...args) : '';
    } as {
            new (): string & ((name?: string, fallback?: string) => string) & typeof Var;
            new (name: string, fallback?: string): string & ((fallback?: string) => string) & typeof Var;
            (name?: string, fallback?: string): string;
        }, {
            stringify: (name?: string, fallback?: string) => (`var(--${normalizeProperty(name).replace(/^($|--)/, '')}${fallback ? `, ${fallback}` : ''})`),
            mock: ($ = new Var() as any) => ({
                'length': $.length,
                '()': `${$()}`, '(a)': `${$("a")}`, '(a, b)': `${$("a", "b")}`, '(a, b, c)': `${$("a", "b", "c")}`,
                'new ()': `${new $()}`, 'new (AA)': `${new $("AA")}`, 'new (AA, BB)': `${new $("AA", "BB")}`, 'new (AA, BB, CC)': `${new $("AA", "BB", "CC")}`,
            }),
            mocks: () => ({ 'new()': Var['mock'](new Var()), 'new(A)': Var['mock'](new Var("A")), 'new(A, B)': Var['mock'](new Var("A", "B")), 'new(A, B, C)': Var['mock'](new Var("A", "B", "C")) }),
        }); // Var.prototype = { constructor: Var }; // console.table(Var['mocks']());
}

import Var = Variables.Var;
export { Var };

