import { $global } from './global-scope';
import { isFunction } from './type-checker';
import { resolve as resolvePath, relative as relativePath, dirname, join as joinPath, isAbsolute } from 'path';

export const mainPath = typeof require === 'function' && require.main && dirname(require.main.filename) || undefined,
      cwd = $global && $global.process && $global.process.cwd ? $global.process.cwd() : undefined,
      baseURI = '/';

export class Paths implements PathMap {
    entry?: string;
    absolute?: typeof resolvePath;
    join?: typeof resolvePath;
    relative?: (to: string) => string;
    resolve?: typeof resolvePath = resolvePath;
    filter?: (path: string) => string;

    [name: string]: Paths[keyof Paths] | PathMap | string;

    constructor(base = mainPath || cwd || baseURI, ...segments: string[]) {
        if (segments.length) base = resolvePath(base, ...segments);
        this.filter = (path: string) => path;
        Object.defineProperties(this, {
            absolute: { get: () => (...segments: string[]) => resolvePath(base, ...segments) },
            join: { get: () => (...segments: string[]) => joinPath(base, ...segments) },
            relative: { get: () => (to: string) => relativePath(base, to) },
            base: { get: () => base, set: (path: string) => base = path },
        });
    }
    
    // define(definitions?: PathMap & Partial<this>): this {
    //     // for (let path in definitions) definitions[path] = typeof path ===
    //     // return Object.create(this, definitions);
    //     return Object.setPrototypeOf(definitions, this);
    // }
}

export declare interface PathMap extends Partial<Paths> {
    absolute?: typeof resolvePath;
    relative?: (to: string) => string;
    resolve?: typeof resolvePath;
    [name: string]: string | PathMap | any;
}

export const paths = new Paths();
export const { absolute, relative, entry: basePath } = paths;

export { relative as relativePath, absolute as absolutePath, resolvePath, resolvePath as resolve, joinPath, joinPath as join };
