// tslint:disable: ban-types callable-types

export class Traces {
    private static _main = new Traces();
    static get main() { return this._main; }

    public sync: boolean = false;
    public get traces() { return [... this._traces]; }

    protected _traces: Trace.Stack = new Set();
    protected debugging: Debugging.FilterSet = new Set();
    protected _entries: number = 0;

    /** Creates a file-entry trace to keep track of module initialization sequence and runtime contexts */
    public entry(filename, ...content: any[]) {
        return (filename && typeof filename === 'string' &&
            (async () => (this.trace({ type: 'entry', index: (this._entries++), filename, content, debug: false }, await true)))()
        ), true;
        // const trace = { type: 'entry', index: (this._entries++), filename, content };
        // this.trace(trace);
        // if (this.sync === true) this.trace(trace, true);
        // else  (async () => (this.trace({ type: 'entry', index: (this._entries++), filename, content }, true), true))();
    }

    /** The delegate responsible for creating the actual traces */
    public trace(trace: Trace, sync = null) {
        if (!trace || !/(object|function)/.test(typeof trace)) return false;
        const type = trace.type = typeof trace.type === 'string' ? trace.type : 'trace';
        const debug = trace && (trace.debug === false ? false : trace.options && trace.options.debug);
        this._traces.add(trace);
        sync = sync === true || sync === false ? sync : trace['sync'] === true || trace['sync'] === false ? trace['sync'] : this.sync;
        return debug === false || (sync ? this.debug(trace, debug) : (async () => (await this.debug(trace, debug)))()) && true;
    }

    /** The delegate responsible for selectivly pushing traces to the console */
    public debug(trace: Partial<Trace> = {}, options: Partial<Debugging> = {}, filters: Debugging.Filters = options.filters || this.debugging) {
        if (!trace) return;

        const { force = false } =
            typeof options === 'object' ? options
                : typeof options === 'string' ? { force: /^\b(force|async|sync|)\b$/i.exec([options, 'sync'])[0] || false }
                    : { force: typeof options === 'boolean' ? options : false };

        const cue = trace['cue'] = `[$${trace.type}][@${trace.filename || '/'}]` + typeof trace['cue'] === 'string' ? trace['cue'] : '',
            filter = (filter: DebuggingFilter) => filter.test ? filter.test(cue)
                : typeof filter === 'string' ? cue.indexOf(filter) > -1
                    : typeof filter === 'function' ? !!filter(trace)
                        : false;

        // const mode: Debugging.Mode = (/(log|warn|info|error)/i.exec(` ${trace['level']} ${options.level} log `) || ['log'])[0] as Debugging.Mode;
        const mode: Debugging.Mode = /\b(log|warn|info|error)\b/i.exec<Debugging.Mode>([trace['level'], options.level, 'log'])[0];
        //     output = async () => (console[mode] as any)(... typeof trace === 'string'
        //         ? [trace]
        //         : [`${trace.type || ''} ${trace.filename || ''}`.trim() + ':', ...[].concat(trace.content || trace)],
        //         await true);

        // output(); // sync ? output() : (async () => output())();

        return (async () => ((console[mode] as any)(... typeof trace === 'string'
            ? [trace]
            : [`${trace.type || ''} ${trace.index > - 1 ? `[${trace.index}]` : ''} ${trace.filename || ''}`.trim().replace(/\s+/g, ' ') + ' — ', ...[].concat(trace.content || trace)],
        ), await true))();
    }
}

export const main = Traces.main;
export const entry: typeof main.entry = (...args: any[]) => (main.entry as any)(...args);

export default main;

