export declare interface Trace extends Trace.Body { type: string; options?: Trace.Options; index?: number; debug?: false; }
export declare namespace Trace {
    type Content = any[] | { [name: string]: any };
    interface Body extends Object { filename?: string; content?: Content; }
    interface Options extends Body { debug?: Debugging; level?: Debugging.Mode; }
    interface Stack extends Set<Trace> { }
    interface Queue extends Array<Partial<Trace>> { }
}

export declare type Debugging = {} & Debugging.Parameter;
export declare type DebuggingFilter = Debugging.FilterLike;
export declare namespace Debugging {
    interface Expression { test(cue: string): boolean; }
    interface Reducer { (trace: Partial<Trace>): boolean; }
    type Selector = string;
    type Filter = Selector | Expression | Reducer;
    type FilterLike = Filter & Partial<Selector & Expression & Reducer>;
    interface FilterSet extends Set<Filter> { }
    type Filters = FilterSet | Filter[];
    type Mode = 'log' | 'warn' | 'error' | 'info';
    type Parameter = ('force' | 'async' | 'sync' | boolean | Filter | FilterSet | Filter[]) & {
        selector?: string, sync?: boolean, async?: boolean,
        force?: 'force' | 'async' | 'sync' | boolean,
        level?: Mode
        filters?: Filters;
    };
}
