export const classNameMap = {} as any as NativeClassNameMap;

export const tagNames = Object.keys(tagNameMap) as NativeTagNames[];

for (const tagName of tagNames) {
    const className: string = `HTML${tagNameMap[tagName]}Element` as string;
    if (classNameMap[className]) continue;
    classNameMap[className] = tagName, tagNameMap[tagName] = className;
}

export const classNames = Object.keys(classNameMap) as NativeClassNames[];

export function getTagName(constructor: NativeElementConstructor<any>): NativeTag {
    const name: keyof HTMLElementTagNameMap = constructor.name as any;
    return classNameMap[name] as NativeTag;
}

export function getConstructor(tagName: NativeTag) { // : Constructor<any>
    if (tagName in tagNameMap) return window[tagNameMap[tagName]]; // return tagName in tagNameMap ? tagNameMap[tagName] : HTMLUnknownElement;
}

export const isNativeConstructor = <Prototype>(constructor: NativeElementConstructor<Prototype> | any): constructor is NativeConstructor<Prototype> => (window as any)[constructor.name] === constructor || classNames.includes(constructor.name as any);

// Isomorphic Fallback

export const BaseElement = (typeof Element !== 'undefined' ? Element : class Element { }) as typeof Element;
export const NativeElement = (typeof HTMLElement !== 'undefined' ? HTMLElement : class HTMLElement extends BaseElement { }) as typeof HTMLElement;

export { NativeElement as HTMLElement, BaseElement as Element };

export const CustomElement = NativeElement as CustomElement.Constructor<CustomElement.Prototype>;

export declare type NativeTag = NativeElements.Tag;
export declare type NativeTagNames = NativeElements.TagNames;
export declare type NativeElement<Tag extends NativeTagNames = NativeTagNames> = NativeElements.Element<Tag>;
export declare type NativeConstructor<Prototype extends NativeElement<NativeTagNames>> = NativeElements.Constructor<Prototype>;
export declare type NativeTagNameMap = NativeElements.TagNameMap;
export declare type NativeClassNameMap = NativeElements.ClassNameMap;
export declare type NativeClassNames = NativeElements.ClassNames;
export declare interface NativeElementConstructor<Prototype = HTMLElement> extends ElementConstructor<Prototype> {
    name: NativeClassNames;
}
