import { $global, isBrowser, $require, $entry, $traces } from 'modules/nerdy-utils';

// tslint:disable: forin member-ordering

$entry(__filename || 'nerdy-app/app.ts');

export class NerdyApp {

    private static _app: NerdyApp;

    public static get app(): NerdyApp { return this._app || (this._app = new this()); }

    ['constructor']: typeof NerdyApp;

    constructor() {
        this.initialize = this.initialize.bind(this), this.fail = this.fail.bind(this);
        // (async () => console.log('NerdyApp#new()', this))();
        this.initialize();
    }

    initialize() { }

    fail(...reasons: any[]) { (console.error as any)(...reasons); }

}

export default NerdyApp;
