import Components, { PolymerElement } from 'modules/nerdy-elements';

export const flexStyle = 'display: flex; flex: 1; flex-direction: column; justify-content: center; align-items: center;';
export const flexStyled = (element, style = flexStyle) => (
    element.setAttribute('style', (element.getAttribute('style') || '') + style), element
);

export const Error404View = class Error404View {
    constructor() {
        return flexStyled(<div>
            <h1>Error 404</h1>
            The page you are looking for cannot be found!
        </div>);
    }
} as typeof HTMLDivElement;

export default Error404View;
