export declare type AbstractEvent<T extends Event = CustomEvent> = T & { path?: HTMLElement[] };
export declare type KeyEvent<T extends Event = CustomEvent> = AbstractEvent<KeyboardEvent & T>;

export namespace AbstractEvent {
    export const stop = <E extends Event>(event: E): typeof event => (event.stopPropagation(), event.preventDefault(), event);
}

export import stop = AbstractEvent.stop;

export { AbstractEvent as Event };

export declare type Factory<T extends HTMLElement= HTMLElement, C extends {} = {}> = (() => T) & Partial<CustomElement.Constructor<T & CustomElement.Prototype>> & C & {
    (): Partial<JSX.Element & JSX.MappedProperties<HTMLElement> & T>;
    (...args: any[]): Partial<JSX.Element & JSX.MappedProperties<HTMLElement> & T>;
    prototype?: Partial<JSX.Element & JSX.MappedProperties<HTMLElement> & T>;
    // style?: StylesObject;
};

export function factory<T extends HTMLElement = T, F extends object & { template?(): T | Partial<HTMLElement> } = object & { template?(): T | Partial<HTMLElement> }>(factoryClass: F): Factory<T, F> {
    const { template, ...factory } = factoryClass as any;
    return Object.assign(typeof template === 'function' ? template : (() => template), { ...factory }) as Factory<T, F>; // tslint:disable-line:prefer-object-spread
}

export type Section = HTMLDivElement;
const section = document.createElement('div') as Section;
section.setAttribute('card-content', ''), section.classList.add('card-content');
export const Section = (() => section.cloneNode()) as Factory<Section>;
