import { uri } from 'modules/nerdy-utils';
import { getDescriptor, descriptor, readonlyValue } from 'modules/nerdy-utils/decorators';

export declare type ImageElementLike<P extends keyof HTMLImageElement = 'src'> = Partial<HTMLImageElement> & {[p in P]: HTMLImageElement[p]; };

export declare interface Data {
    event?: DragEvent; type?: 'drop' | 'dragover' | 'dragenter' | 'dragleave' | DragEvent['type'];
    types?: string[]; files?: File[]; src?: string; content?: any;
    typedData?: { [name: string]: any };
}

// tslint:disable-next-line:cyclomatic-complexity
export function parse(event: DragEvent, data: Data = {}): Data {
    if (!event || !event.dataTransfer) return data;
    const { type, dataTransfer, dataTransfer: { types: _types, files = [] } } = event; // (event || {}) as any;
    const types = _types || [];
    // const data: DragEventData = { type, event, types: [...types || []], files: Array.from(files) };
    data = Object.assign(data || {}, { type, event, types: [...types || []], files: Array.from(files) }); // tslint:disable-line
    data.typedData = data.typedData;
    if (type === 'drop') {
        const dataTypes = data['dataTypes'] = data.types.join(' ');
        const [textType] = /text\/(html|plain)\b/.exec(dataTypes) || types;
        const contentType = textType || 'text/plain';
        const content = data.typedData[contentType] || dataTransfer.getData(contentType); // event.dataTransfer.getData('text/html')
        // const dataTypes = data.types.join(' '), content = dataTransfer.getData(types[0] || 'text/plain'); // event.dataTransfer.getData('text/html')
        if (/text\/html/.test(dataTypes) && / src\=["]/.test(content)) data.src = (/(?: src\=["])(.*?)(?:["])/.exec(content) || '')[1];
        if (!data.src && /text\//.test(dataTypes) && /^(data:image|(https?|file)\:\/)\//.test(content)) data.src = content; // (/(?: src\=["])(.*?)(?:["])/.exec(content) || '')[1]; // /text\/plain/.test(dataTypes) &&
        if (data.src && uri.isDataURI(data.src)) {
            const file = uri.toFile(data.src);
            if (file) data.files = (data.files || []).push(file) as any;
        }
        data.content = content;
        data['textType'] = textType;
        // if (!data.content && !data.src && !data.typedData) {
        //     data.typedData = {};
        //     for (const type of data.types) data.typedData[type] = dataTransfer.getData(type);
        //     // console.log(data['typedData']);
        // }
        // if (types.find(type => /^image\/.*?/.test(type))) data
        // debugger;
    }
    return data;
}

export declare type FileParts = Array<ArrayBuffer | ArrayBufferView | Blob | string>;
export declare type FileURL = (string) | URL;
export declare type FileProperties = FilePropertyBag;

export class ImageFile extends File {
    readonly effectiveName: File['name'];

    @descriptor(getDescriptor(File.prototype, 'name'))
    readonly originalName: File['name'];

    get name() { return this.effectiveName || this.originalName; }
    set name(name) { readonlyValue(this, 'effectiveName', name || this.originalName); }

    constructor(file: File, filename?: string, properties?: FileProperties);
    constructor(url: FileURL, filename?: string, properties?: FileProperties);
    constructor(parts: FileParts, filename: string, properties?: FileProperties);
    constructor(contents: File | FileURL | FileParts, filename?: string, properties?: FileProperties) {
        if (typeof contents === 'object' && contents instanceof URL) contents = contents.href;
        if (typeof contents === 'string') {
            contents = uri.toFile(contents as string.URL);
        }
        if (typeof contents === 'object' && contents instanceof File) {
            const file = contents;
            if (!/^image\//.test(file.type)) throw TypeError(`Unexpected file type: "${file.type}" is not an image file type`);
            const imageFile = Object.setPrototypeOf(contents, ImageFile.prototype);
            // const names = new (class Names extends Array { } as any)( imageFile.name, imageFile.name = `Image - ${imageFile.name}`, imageFile.name = imageFile.originalName, imageFile.name = `Image - ${imageFile.name}`, imageFile.name ); // console.dir(names);
            return imageFile;
        }
        super(contents as FileParts, filename, properties);
    }
}

export function readImage(file: File);
export function readImage(file: File, image?: ImageElementLike<'src'>); // HTMLImageElement | {src: HTMLImageElement['src']});
export function readImage(file: File, image: ImageElementLike<'src'> = new Image()) {
    if (!file || !/^image\//.test(file.type)) return;
    const imageFile = new ImageFile(file);
    // console.log({ imageFile, isFile: imageFile instanceof File, isImageFile: imageFile instanceof ImageFile });
    const reader = new FileReader();
    reader.onload = ({ target: { result } }: any) => {
        if (/^data\:image\//.test(result)) {
            const { name = '' } = file, metadata: uri.Attributes = {};
            if (name && typeof name === 'string') metadata.name = name;
            result = uri.Attributes.append(result, metadata);
        }
        image.src = result;
    };
    reader.readAsDataURL(imageFile); // console.error(exception); debugger;
    return image;
}

import * as dropspace from './dropspace';

export default dropspace;
