declare type asyncCallbackHandle<T = any, S = {}> = T | (T & Partial<{ isAsyncCallbackHandle } & S>);
declare type idleCallbackHandle<T = any> = asyncCallbackHandle<T, { isIdleCallbackHandle }>;
declare type deferCallbackHandle<T = any> = asyncCallbackHandle<T, { isDeferCallbackHandle }>;
