export declare interface Reflection<T extends object> {
    get<K extends keyof T>(property: K): T[K];
    set<K extends keyof T>(property: K, value: any): boolean;
}

export const Reflection = <T extends object>(target: T, bindings = {} as any): Reflection<T> => ({
    set: Reflect.set.bind(Reflect, target),
    get: Reflect.get.bind(Reflect, target),
});

export const Bound = <T extends object>(target: T, bindings = Reflection(target) as any) => new Proxy(target, {
    get(target: T, property) {
        const value = Reflect.get(target, property);
        return bindings[property] || (typeof value === 'function' ? (bindings[property] = value.bind(target)) : value);
    },
}) as T & Reflection<T>;
