// tslint:disable:ban-types

export const { getOwnPropertyDescriptor, getOwnPropertyDescriptors, defineProperty } = Object;
export const isWritable = (object, property: PropertyKey, value?: any, { writable }: Partial<PropertyDescriptor> = Object.getOwnPropertyDescriptor(object, property) || {}) => !!writable;

export const readonlyValue = (object, property: PropertyKey, value: any, writable = isWritable(object, property)) => defineProperty(object, property, { configurable: true, value, writable });
export const getConstructor = ({ constructor: { prototype } }: Object = {}) => prototype;

export const getDescriptor = (target: Object, property: PropertyKey): PropertyDescriptor => (
    getOwnPropertyDescriptor(target, 'property') || getOwnPropertyDescriptor(getConstructor(target), property) || { configurable: true }
) as PropertyDescriptor;

export const descriptor = (_descriptor: PropertyDescriptor, inherit: 'inherit' | boolean = false): PropertyDecorator => (
    (target, property: PropertyKey, { base = inherit ? getDescriptor(target, property) : { configurable: true }, descriptor = { ...base, ..._descriptor, property } }: PropertyDescriptorMap = {}) => (
        // console.log('descriptor', { _descriptor, descriptor, target, property, spread: { ...base, ..._descriptor } }), // merged: mergeDescriptors(base, _descriptor, { property })
        descriptor
    )
);
