import { Components, PolymerElement, StyleElement as Style, style } from '~/framework';
import { Selector as $$, Selectors, Var } from 'modules/nerdy-utils/styles';
import { Constructor as QuasiConstructor, QuasiElementConstructor } from 'modules/nerdy-elements/core/quasi-elements';

// tslint:disable: prefer-object-spread

export const QuasiStyle = QuasiConstructor<HTMLStyleElement>('style') as typeof HTMLStyleElement;
export type QuasiStyle = HTMLStyleElement;

export class AppStyle extends QuasiStyle {
    constructor(style: string = String(AppStyle.style)) {
        super();
        this.appendChild(document.createTextNode(typeof style === 'string' && style || ""));
        // this.textContent = typeof style === 'string' && style || "";
        // this.textContent += '\n *[hidden] { display: none !important; }';
        // console.log(`AppStyle#constructor`, {this: this, style, text: this.textContent, clone: this.cloneNode(true)});
    }
}

export namespace AppStyle {
    export const colors = { primary: '#4285f4', secondary: 'black', contrast: 'white' };
    const { primary, secondary, contrast } = colors;

    export const variables = {
        $Primary: primary,
        $PrimaryContrast: contrast,
        $Secondary: secondary,
        $SecondaryContrast: contrast,
    };

    export const selectors = new Selectors({
        host: { $: ':host', drawerless: { $: ':host([drawerless])', toggle: '… [drawer-toggle]' } },
        shell: {
            $: ':host > #shell',
            toolbar: '… #topbar',
            // toolbar: {$: '… #topbar', title: {$: '… > #main-title', terms: '… > *'},
        },
        header: {
            $: 'app-header', iconButton: '… paper-icon-button',
            toolbar: '… app-toolbar',
            // toolbar: {$: '… app-toolbar', title: {$: '… > #main-title', terms: '… > *'},
        },
        drawer: {
            $: 'app-drawer',
            layout: { $: '…-layout', wide: { $: '…:not([narrow])', toggle: '… [drawer-toggle]' } },
            list: { $: '.drawer-list', links: { $: '… a', selected: { $: '….iron-selected' } } },
            // list: { $: '… .drawer-list', links: { $: '… a', selected: { $: '….iron-selected' } } },
        },
    });

    export const { host, shell, header, iconButton, drawer, drawerList } = selectors,
        $Primary = new Var('Primary', primary),
        $PrimaryContrast = new Var('PrimaryContrast', contrast),
        $Secondary = new Var('Secondary', secondary),
        $SecondaryContrast = new Var('SecondaryContrast', contrast);

    export const style: style = {
        // ...Styles.global,
        [host]: { ...variables, display: 'block' },
        [header]: { color: $PrimaryContrast, backgroundColor: $Primary },
        [shell.toolbar]: { height: '40px', fontSize: '75%', color: $PrimaryContrast, backgroundColor: $Primary },
        [header.toolbar + ' *[main-title] > *']: { paddingRight: '1em' },
        [header.iconButton]: { $PaperIconButtonInkColor: $PrimaryContrast },
        [drawer.list]: { margin: '0 20px' },
        [drawer.list.links]: { display: 'block', padding: '0 16px', color: $Secondary, textDecoration: 'none', lineHeight: '40px' },
        [drawer.list.links.selected]: { color: 'black', fontWeight: 'bold' },
        [host.drawerless.toggle]: { display: 'none' },
        [drawer.layout.wide.toggle]: { display: 'none' },
        [Symbol.toPrimitive]: (hint: string) => Style.generateStyleText(style as any),
        // ['*:not(:defined), *:not(:defined) *']: { opacity: '0 !important' }, // , *:not(:defined) *'
        // ['*:defined, *:not(:defined), *:not(:defined) *']: { transition: 'opacity 5000ms ease-in-out' },    // '[layer-group]': {
    } as style;

}

// export const { variables, selectors } = AppStyle;

export default AppStyle;
