import { Components, PolymerElement } from '~/framework';
import { Style, style, mixins } from '../components/styles';
import { PaperInput as Input, PaperInputContainer as InputContainer } from 'modules/nerdy-polymer-elements';
import NerdyImageField from '../components/nerdy-image-field';
import { uri } from 'modules/nerdy-utils';

const stopEvent = <E extends Event>(event: E): typeof event => (event.stopPropagation(), event.preventDefault(), event);
const notify = true, reflectToAttribute = true;

export class NerdyPaperImageField extends PolymerElement {
    // static root = 'light';
    static style: style = {
        // '#container': { maxHeight: '100%' },
        // ':host': { $MaxImageHeight: 'var(--max-image-height, 25vh)' },
        // '#input': { $MaxImageHeight: '25px' },
        '#caption': '@apply --paper-input-suffix' as any,
    };
    static properties = { value: { type: String, notify }, file: { type: File, notify } }; // reflectToAttribute
    static observers = ['_updateValue(value)'];
    static template = () => <template>
        <Style>{NerdyPaperImageField.style}</Style>
        <InputContainer id='container'>
            <label slot='label'>Image</label>
            <NerdyImageField slot='input' id='input' on-src-changed='_updateSrc' class='paper-input-input' file='{{file}}'></NerdyImageField>
            <span id='caption' slot="add-on">[[file.name]]</span>
        </InputContainer>
    </template>

    $: { container: InputContainer, input: NerdyImageField };
    value: NerdyImageField['src'] = '';

    constructor() {
        super();
    }

    _drop?(event: DragEvent);
    _drag?(event: DragEvent); _dragover?(event: DragEvent);
    _dragenter?(event: DragEvent); _dragleave?(event: DragEvent);
    _dragstart?(event: DragEvent); _dragend?(event: DragEvent);

    attached() {
        const { $ } = this, noop = () => { };
        for (const event of ['drop', 'drag', 'dragover', 'dragenter', 'dragleave', 'dragstart', 'dragend'])
            this[`_${event}`] = this[`_${event}`] || ((e: DragEvent, { input = {} } = $) => (input[`_${event}`] || noop).call(input, e) || false);
    }

    async getImage() {
        const { input } = this.$ || {} as this['$'];
        return input && input.getImage ? await input.getImage() : undefined;
    }
    // get getImage() { return () => this.$.input && this.$.input.getImage && this.$.input.getImage(); }  // : NerdyImageField['getImage']

    // = NerdyImageField.prototype.getImage; //  () { return this.$.input.getImage(); }

    _updateSrc(src: this['value'] = this.$.input.src) { this.$.container['_inputHasContent'] = !!(this.value = this.$.input.src); }
    _updateValue(value: this['value'] = this.value) { this.value = this.$.input.src = uri.isURL(value) ? value : ''; }
}

Components.define(NerdyPaperImageField);

export default NerdyPaperImageField;
