import { isObject } from './type-checker';

// tslint:disable: ban-types no-eval

export declare type Scope<Scope = object> = Scope;
export declare type Process = Scope<NodeJS.Global>;
export declare type Global = Scope<Window | Worker | Process>;
export declare type Lexical = Scope<Global | Object>;

const $this = eval('() => this')(); // tslint:disable-line: no-eval

export abstract class GlobalScope {
    static isWindow: boolean = !!('object' === typeof window && isObject(window.navigator));
    static notWindow: boolean = !GlobalScope.isWindow;
    static isWorker: boolean = GlobalScope.notWindow && !!('object' === typeof self && isObject(self.navigator));
    static notWorker: boolean = !GlobalScope.isWorker;
    static isBrowser: boolean = GlobalScope.isWindow || GlobalScope.isWorker;
    static notBrowser: boolean = !GlobalScope.isBrowser;
    static isFrontend: boolean = GlobalScope.isBrowser;
    static notFrontend: boolean = !GlobalScope.isFrontend;

    static isProcess: boolean = GlobalScope.notFrontend && !!('object' === typeof global && isObject(global.process));
    static notProcess: boolean = !GlobalScope.isProcess;
    static isBackend: boolean = GlobalScope.isProcess;
    static notBackend: boolean = !GlobalScope.isBackend;

    static isOther: boolean = GlobalScope.notBrowser && GlobalScope.notProcess;
    static notOther: boolean = !GlobalScope.isOther;

    static $window: Window = GlobalScope.isWindow ? (window as any) : undefined;
    static $worker: Worker = GlobalScope.isWorker ? (self as any) : undefined;
    static $process: Process = GlobalScope.isProcess ? (global as Process) : undefined;
    static $lexical: Lexical = $this; // eval('this');
    static $global = GlobalScope.$window || GlobalScope.$worker || GlobalScope.$process || GlobalScope.$lexical;
}

Object.setPrototypeOf(GlobalScope, GlobalScope.$global);

export declare type Isomorphic = Scope<Window & Worker & Process & Object & Function>;

export const $window: typeof GlobalScope & Window = GlobalScope.$window && GlobalScope as any;
export const $worker: typeof GlobalScope & Worker = GlobalScope.$worker && GlobalScope as any;
export const $process: typeof GlobalScope & Process = GlobalScope.$process && GlobalScope as any;
export const $lexical: typeof GlobalScope & Lexical = GlobalScope.$lexical && GlobalScope as any;
export const $global: typeof GlobalScope & Isomorphic = GlobalScope as any;

export const { isWindow, isWorker, isProcess, isOther, isBrowser, isBackend, isFrontend } = GlobalScope;
export const { notWindow, notWorker, notProcess, notOther, notBrowser, notBackend, notFrontend } = GlobalScope;

export default $global;
