import { bind } from './decorators';
import { isCustomTagName, isElement, isFunction, isString, isTemplateElement, isTemplateFactory } from './helpers';
import { ElementFactory, ElementFactoryMethod, CommentFactory, CommentFactoryMethod } from './elements-factory';

// tslint:disable: label-position ban-types unified-signatures interface-over-type-literal callable-types align no-empty-interface prefer-const

export const PROXY = Symbol('Binding::Proxy');
export const COMPONENT = Symbol('Binding::Component');
export const HOOKS = Symbol('Binding::Hooks');

export class BindingProxy {
    static get(proxy: BindingProxy | BindingHandler | object | undefined, model = {}): BindingProxy {
        if (typeof proxy === 'function' || typeof ((proxy || {}) as ProxyHandler<any>).get === 'function')
            return new BindingProxy(model, proxy);
        if (proxy instanceof BindingProxy) return proxy;
        return new BindingProxy(model);
    }

    // static bind(template?: HTMLTemplateElement) {
    //     return template;
    // }

    constructor(component: object);
    constructor(component: object, handlerConstructor: BindingHandler);
    constructor(component: object, handler: BindingHandler);
    constructor(component: object, handler: Partial<ProxyHandler<any>> & (BindingHandler | BindingHandlerConstructor) = NerdyBindingHandler) {
        if (component instanceof BindingProxy) return component;
        if (typeof handler === 'function') handler = new handler(component);
        return component[PROXY] = new Proxy(this, {
            get: (target: any, property: string, receiver) => handler.get(component, property, receiver),
        });
    }
}

export class BindingHook implements BindingReference {

    /* @internal */ public static spec;

    /**
     * Adds a binding into [[Hooks]] set and returns the
     * unique hook instance. When passed a string, a hook
     * object is automatically created to be used instead.
     */
    public static declare(hook: string | BindingHook): BindingHook { return isString(hook) ? new BindingHook(hook) : (BindingHook[HOOKS].add(hook), hook); }

    public toString?: () => string;
    public [Symbol.toStringTag]?: string;

    constructor(name: string) {
        this.toString = () => name, this[Symbol.toStringTag] = `«${name}»`;
        return BindingHook.declare(Object.create(this)); // Object.create(this);
    }

} BindingHook[HOOKS] = new WeakSet();

export class NerdyBindingHandler implements ProxyHandler<object> {
    constructor(component: object, hooks: { [name: string]: BindingHook } = {}) {
        this[COMPONENT] = component;
        this[HOOKS] = component.constructor['bindingHooks'] = hooks;
        this.get = (target: any, property: string | symbol, receiver): any =>
            property === PROXY ? receiver : property in this ? this[property] : this.getHook(property, component[property]);
        // property in component ? component[property] :
        component.constructor['bindingHandler'] = this;
    }

    bind?(template?: HTMLTemplateElement);

    get?(target: any, property: string | symbol, receiver): any;
    // get(target: any, property: string | symbol, receiver): any {
    //     return property === PROXY ? receiver : property in this ? this[property] : this.getHook(property);
    //     // return this[property] || (property === PROXY && receiver) || this.getHook(property);
    // }

    getHook(property, value?: any) {
        return this[HOOKS][property] || (this[HOOKS][property] = new BindingHook(property));
    }

}

const canQuerySelector = (object: any): object is { querySelector: HTMLElement['querySelector']; } => typeof object === 'object' && typeof object['querySelector'] === 'function';

@bind // Bind delegate methods to this factory instance
export class TemplatesFactory {

    static container: SelectorString = 'head';
    static templateElements = new Map<string, HTMLTemplateElement>();
    static normalizeId = (id: string) => id.toLowerCase().replace(/\-+template$/, '') + '-template';
    public container: Element | null; // document && document.querySelector('head') || null;

    get create(): TemplatesFactory['createTemplate'] { return this.createTemplate; }
    get define(): TemplatesFactory['defineTemplate'] { return this.defineTemplate; }

    constructor(public elementsFactory: ElementFactory & CommentFactory) {
        if (this.container === undefined || !(this.container instanceof Element))
            this.container = [this.container, this.constructor['container']].reduce((element, selector) =>
                element || element === null ? element : selector === null ? null
                    : (isString(selector) ? document.querySelector(selector) : isElement(selector) && selector) || element);
    }

    @bind public get createElement(): ElementFactoryMethod { return this.elementsFactory && this.elementsFactory.createElement; }

    @bind public get createComment(): CommentFactoryMethod { return this.elementsFactory && this.elementsFactory.createComment; }

    public createTemplateElement(...contents: element[]): TemplateElement;
    public createTemplateElement(properties: TemplateElementProperties, ...contents: element[]): TemplateElement;
    @bind public createTemplateElement(...contents: any[]): TemplateElement {
        const properties: TemplateElementProperties = contents.length === 0 || contents[0].cloneNode ? {} : contents.shift();
        return this.createElement(HTMLTemplateElement, properties, ...contents);
    }

    @bind public createTemplate<Prototype, Factory extends TemplateFactory<Prototype>, Template extends Factory | TemplateElement>(template: Template, proxy?, model?): TemplateElement {
        const element = (isTemplateElement(template) ? template
            : isTemplateFactory(template) ? this.createFromFactory(template, proxy)
                : this.createFromElements(isElement(template) ? template
                    : this.createComment(template)));
        return element[PROXY] = element[PROXY] || proxy, element as any;
    }

    @bind public createFromFactory<Prototype, Factory extends TemplateFactory<Prototype>>(factory: Factory, proxy?, model?): TemplateElement {
        proxy = BindingProxy.get(proxy, model) || {};
        let element = factory(proxy);
        element[PROXY] = element[PROXY] || proxy;
        if (typeof proxy.bind === 'function') proxy.bind(element);
        // if (typeof proxy.bind === 'function') element = proxy.bind(element) || element;
        return element;
    }

    @bind public createFromElements(...elements: element[]) {
        return this.createTemplateElement(...elements);
    }

    @bind public defineTemplate<Prototype, Factory extends TemplateFactory<Prototype>, Template extends Factory | TemplateElement>(id: string, template: Template, proxy?: any): TemplateElement {
        if (!isCustomTagName(id)) return this.createTemplateElement(template as element);

        // Normalize id to lower case with "-template" suffix
        id = TemplatesFactory.normalizeId(id);

        // Look for a predefined template element or create new template element
        const templateElement = TemplatesFactory.templateElements.get(id)
            || ([this.container && this.container.querySelector(`template[for=${id}], template#${id}`)].find(isTemplateElement) as TemplateElement)
            || this.createTemplate(template, proxy);

        // if (!templateElement.getAttribute('id')) templateElement.setAttribute('id', id);
        if (!templateElement.hasAttribute('for')) templateElement.setAttribute('for', id);

        // if (proxy) (templateElement as any).proxy = proxy;

        // Make sure template is in the right place
        TemplatesFactory.templateElements.set(id, templateElement);
        if (this.container && templateElement.parentElement !== this.container) {
            this.container.appendChild(templateElement);
            // console.log('defineTemplate::appendChild', { container: this.container, child: templateElement });
        }

        return templateElement; // console.log(templateElement.content.children);
    }

}

export namespace TemplatesFactory {
    export type create = TemplatesFactory['create'];
    export type define = TemplatesFactory['define'];
}

export namespace TemplatesFactory { }

export default TemplatesFactory;

export declare type SelectorString = string;

export declare interface BindingHandler extends ProxyHandler<any> { get?(target: any, property: string, receiver): string | any; }
export declare interface BindingHandlerConstructor { prototype: ProxyHandler<any>; new(component?: object): BindingHandler; }
export type TemplateElementProperties = Partial<TemplateElement> | ElementProperties;

