// tslint:disable: ban-types

const supportsSymbol = typeof Symbol === 'function' && typeof Symbol('') === 'symbol';

export declare type callable = Function; // tslint:disable-line:ban-types

export abstract class TypeChecker {
    static supportsSymbol = typeof Symbol === 'function' && typeof Symbol('') === 'symbol';

    static isUndefined<T extends undefined>(reference: T | any): reference is T {
        return reference === undefined;
    }

    static isNull(reference: any): reference is null {
        return reference === null;
    }

    static isString<T extends string>(reference: T | any): reference is T {
        return typeof reference === 'string';
    }

    static isNumber<T extends number>(reference: T | any): reference is T {
        return typeof reference === 'number'; // tslint:disable-line:use-isnan
    }

    static isBoolean<T extends boolean>(reference: T | any): reference is T {
        return reference === true || reference === false;
    }

    static isSymbol<T extends symbol>(reference: T | any): reference is T {
        return reference && typeof reference === 'symbol';
    }

    static isFunction<T extends callable>(reference: T | any): reference is T {
        return reference && typeof reference === 'function';
    }

    static isObject<T extends Object>(reference: T | any): reference is T {
        return reference && typeof reference === 'object';
    }

    static isObjectLike<T extends object>(reference: T | any): reference is T {
        return reference && typeof reference === 'object' || typeof reference === 'function'; //  ? reference !== null : isFunction(reference);
    }

    static isObjectLiteral<T extends Object>(reference: T | any): reference is T {
        return reference && TypeChecker.isObject(reference) && Object.getPrototypeOf(reference) === Object.prototype;
    }

    static isArray<T>(reference: T[] | any): reference is T[] {
        return reference && typeof reference === 'object' && Array.isArray(reference);
    }

    constructor() { return TypeChecker; }
}

const { isUndefined, isNull, isString, isNumber, isBoolean, isSymbol, isFunction, isObject, isObjectLike, isObjectLiteral, isArray } = TypeChecker;

export { isUndefined, isNull, isString, isNumber, isBoolean, isSymbol, isFunction, isObject, isObjectLike, isObjectLiteral, isArray };

export default TypeChecker;

