/// <reference path="types.d.ts" />

export { isBrowser } from 'modules/nerdy-utils';

// tslint:disable: ban-types unified-signatures prefer-const

export declare type CustomTagName = string;

export declare type TypeOfMap = {
    'null': null, 'undefined': undefined,
    'string': string, 'number': number, 'boolean': boolean, 'symbol': symbol,
    'object': Object, 'function': Function,
} & {
        [Name in keyof any]: Name;
    };

export declare type TypeOf<Type extends keyof TypeOfMap> = TypeOfMap[Type];

export const typeCheck = <type extends keyof TypeOfMap, Type extends object>(reference, type: type | { prototype: Type }): reference is TypeOfMap[type] | Type => (
    isString(type) ? (type === 'null' ? reference === type : typeof reference === type) :
        isFunction(type) ? (reference instanceof type) : call(type, 'test', reference)
);

export const isCustomTagName = (reference: string | any): reference is CustomTagName => /^\w+\-\w+(\-\w+)*$/.test(`${reference || ''}`) || null;

export const isElement = (reference: any): reference is element => (reference || null) instanceof HTMLElement || null;

export const isFunction = (reference: any): reference is Function => typeof reference === 'function' || null;

export const isString = (reference: any): reference is string => typeof reference === 'string' || null;

export const isTemplateElement = (reference: any): reference is TemplateElement => reference && typeof reference === 'object' && 'content' in reference && reference instanceof HTMLTemplateElement || null; //  instanceof HTMLTemplateElement

export const isTemplateFactory = (reference: any): reference is TemplateFactory<any> => isFunction(reference) || null;

export function call(callable: Function);
export function call(target: object, method: Function, ...args: any[]);
export function call(target, method: string, ...args: any[]); // & keyof typeof target
export function call(callable: Function) {
    if (arguments.length > 1) {
        let [target, method, ...args] = arguments;
        // const target = arguments[0] || {},  method = undefined,  args = Array.from(arguments).slice(2);
        return (isString(method) && isFunction((target || {})[method])) ? [(target || {})[method](...args)] :
            isFunction(method) ? [method.call(arguments[0] || null, ...args)] : false;
    } else return isFunction(callable) ? [callable()] : false;
}

export function defer(callback: () => any): number | NodeJS.Timer {
    return isFunction(requestAnimationFrame) ?
        requestAnimationFrame(callback) : setTimeout(callback, 0);
}
