const reflectToAttribute = true;

// tslint:disable: ban-types callable-types align
export class NerdyAppView extends PolymerElement {
    // static tagName = 'nerdy-app-view';
    // static root = 'light';
    static properties = {
        // page: { type: String, reflectToAttribute, observer: '_pageChanged' },
        route: { notify: true },
        view: { type: String, observer: '_viewChanged', notify: true }, // reflectToAttribute,
    };

    static template = ({ }: NerdyAppView) =>
        <template>
            <AppStyle></AppStyle>
            <AppShell id="shell" sitename="[[sitename]]" drawerless>
                <IQFFTDemo slot="main-content"></IQFFTDemo>
            </AppShell>
            {/*
                <AppShell id="shell" title="[[title]]" sitename="[[sitename]]" drawerless>
                    <IronSelector slot='drawer-content' selected='[[route.name]]' attr-for-selected='name' class='drawer-list' role='navigation'>
                        <Repeat items='{{state.routes}}' as='route'>
                            <a title='[[route.title]]' name='[[route.name]]' href='[[_href(route.href)]]' hidden='[[route.hidden]]'>{'[[route.title]]'}</a>
                        </Repeat>
                    </IronSelector>
                    <IronPages slot="main-content" id='pages' attr-for-selected='name' fallback-selection='fallback' role='main'>
                        <div name='fallback'><paper-spinner alt="Loading…" active></paper-spinner></div>
                    </IronPages>
                </AppShell>
                <AppRouter id='router' routes='{{state.routes}}' page='{{page}}' view='{{view}}' data='{{data}}' route='{{route}}' useHash='[[useHash]]' ></AppRouter>
            */}
        </template> // <AppToolbar class="tall">...</AppToolbar> // <slot name='main-header' slot='main-header'></slot>
    // <AppStatus id='status'>
    //     <status><label>routes:</label> <b>[[state.routes]]</b></status>
    //     <status><label>data:</label> <b>{`{page: [[data.page]]}`}</b></status>
    //     <status><label>page:</label> <b>[[page]]</b></status>
    //     <status><label>view:</label> <b>[[view]]</b></status>
    // </AppStatus>
    // <status><label>data:</label> <b>{`{page: [[data.page]]}`}</b></status>
    // <status><label>name:</label> <b>[[route.name]]</b></status>
    // <status><label>title:</label> <b>[[route.title]]</b></status>

    public $: NerdyAppView.Elements;

    public page: string = '';
    public view: string = '';
    public displayTitle: string = 'IQFFTDemo'; // 'NerdyApp';
    public sitename: string = 'NerdyApp: IQFFTDemo';
    public route: Route;
    // public component: HTMLElement;

    public get state() { return this._state; }
    public set state(state: any) { this._state = state; }
    public get component() { return this._component; }
    public set component(component) { this._activate(component); }

    protected _state: State;
    protected _resizing: { [name: string]: any } = {};
    protected _component: HTMLElement;
    // protected _resizeHandler: EventListener | boolean = false;

    public get $shell() { return this.$ && this.$.shell || null; }

    constructor() {
        super();
        if (typeof this.handleResize === 'function') {
            const { devicePixelRatio = 1, initialRatio = devicePixelRatio, screen = {} } = window || {} as any;
            const { width: initialWidth, availWidth = initialWidth, initialAvailWidth = availWidth } = screen || {} as any;
            this._resizing = { initialRatio, initialWidth, initialAvailWidth, initialScreen: { ...screen } };
        }
    }

    _href(path: string, useHash = this.state.useHash || false) {
        return (useHash ? '#' : '') + path.replace(/^\#/, '');
    }

    closeDrawer(force?: boolean | 'force') { this.$shell && this.$shell.closeDrawer(...arguments); }

    attached() {
        // if (typeof this.handleResize === 'function') window.addEventListener('resize', this.handleResize = this.handleResize.bind(this)), this.handleResize({} as any);
    }

    detatched() {
        // if (typeof this.handleResize === 'function') window.removeEventListener('resize', this.handleResize);
    }

    handleResize(event: Event) {
        this['_resizeDebugger'] = this['_resizeDebugger'] || ((event: Event) => {
            const { innerWidth: width, innerWidth, outerWidth } = window, { offsetWidth, clientWidth } = document.body;
            let scope: any = { windowWidth: width };
            if (offsetWidth !== width) scope = { ...scope, offsetWidth, clientWidth };
            if (offsetWidth !== clientWidth) scope = { ...scope, offsetWidth, clientWidth };
            if (innerWidth !== offsetWidth || innerWidth !== clientWidth) scope = { ...scope, innerWidth };
            const { zoom: htmlZoom, scale: htmlScale } = window.getComputedStyle(document.documentElement);
            const { zoom: bodyZoom, scale: bodyScale } = window.getComputedStyle(document.body);
            scope = { ...scope, html: [htmlZoom, htmlScale].join('|'), body: [bodyZoom, bodyScale].join('|') };
            // console.log(JSON.stringify({... scope, zoom, scale}).split('"').join('').split(',').join(' '), event);
            console.log(JSON.stringify(scope).replace(/([^\:\w]+\s*|\s+)/g, ' '), event);
        });

        const { initialAvailWidth, initialRatio, initialScreen, initialWidth } = this._resizing || {} as any;
        const { devicePixelRatio: currentRatio, screen: { width: currentWidth, availWidth: currentAvailWidth } } = window;
        console.log({ initialRatio, initialWidth, initialAvailWidth }, { currentRatio, currentWidth, currentAvailWidth });
    }

    _viewChanged(view: string) {
        if (!view || typeof view !== 'string') return;
        const container = this.$.pages;
        if (!container || !container.querySelector) return;
        clearTimeout(this['_viewChangeTimeout']);
        let component: HTMLElement = container.querySelector(`[name='${view}']`) as HTMLElement;
        if (!component) {
            const src = `../${view}`;
            const { default: View } = require(src); // FuseBox.import(src).then((exports) => console.log({ exports }));
            component = typeof View === 'function' ? <View name={view} > </View> : <div name={view}>Oops! Something went wrong or we're just too slow... Sorry, this page is broken.</div>;
            if (!component || !(component instanceof HTMLElement)) return;
            // if (component.hasAttribute('title')) {
            //     component.title = component.getAttribute('title') || component.title;
            //     component.removeAttribute('title');
            // }
            container.appendChild(component);
            this['_viewChangeTimeout'] = setTimeout(() => this._viewChanged(view), 100);
            this.log['#_viewChanged']({ view, View, container });
        } else {
            this.component = component;
            container['selected'] = view;
            this.displayTitle = component['shortTitle'] || this.get('route.current.shortTitle') || '';
        }

        this.closeDrawer();
    }

    _activate(component: HTMLElement & { activate?(container?: HTMLElement): any }) {
        if (!component) return;
        const _component = this._component, container = component['container'] = this.$shell || this;
        if (_component !== component) this._deactivate(_component);
        if (typeof component.activate === 'function') component.activate(container);
        this._component = component;
        // component.parentElement['selected'] = component['name'];
    }

    _deactivate(component: HTMLElement & { deactivate?(): any } = this._component) {
        if (component && typeof component.deactivate === 'function') component.deactivate();
    }

}

Components.define(NerdyAppView);

export declare namespace NerdyAppView {
    interface Elements {
        [name: string]: HTMLElement | CustomElement;
        // drawer: PolymerElements.AppDrawer;
        shell: AppShell;
        pages: IronPages;
        router: AppRouter;
    }
}

export default NerdyAppView;

