import { Components, PolymerElement } from '~/framework';
import { ImageField as ImageFieldStyle, Style, style } from '../components/styles';

// import { getDescriptor, descriptor, readonlyValue } from 'modules/nerdy-utils/decorators';
import { uri } from 'modules/nerdy-utils'; // import 'modules/nerdy-utils/uri';
import dropspace, { ImageFile } from '../modules/dropspace';

const stopEvent = <E extends Event>(event: E): typeof event => (event.stopPropagation(), event.preventDefault(), event);
const timeout = (callback: () => any, delay = 5000) => setTimeout(callback, delay);
const notify = true;

export class NerdyImageField extends PolymerElement {
    // static root = 'light';
    static style: style = {
        ...ImageFieldStyle,
        // '#preview': { $MaxImageHeight: 'var(--max-image-height, 25px)' },
        '#preview-image': {
            $MaxImageHeight: 'var(--max-image-height, 25vh)',
            maxHeight: 'var(--max-image-height, 25vh) !important',
        },
        '*[not-visible]': { visibility: 'hidden' },
    };
    static properties = {
        placeholder: { type: String },
        file: { type: File, notify },
        src: { type: String, notify, reflectToAttribute: true, observer: '_setSrc' },
    };

    static template = () =>
        <template>
            <Style>{NerdyImageField.style}</Style>
            <div id="dropspace" on-drop="_drop" on-dragover="_dragover" on-dragenter="_dragenter" on-dragleave="_dragleave">
                <div id="placeholder-label" class="label" hidden='[[src]]'><span>[[placeholder]]</span></div>
                <div id="preview" hidden='[[!src]]'>
                    <img id="preview-image" bind-src='[[src]]' on-dragstart="_dragstart" on-drag="_drag" draggable></img>
                </div>
            </div>
        </template>

    dragImage?: HTMLImageElement;
    src: HTMLImageElement['src'] = '';
    placeholder: string = 'Drop image here';
    file: Partial<File>;

    $: {
        'dropspace': HTMLDivElement,
        'placeholder-label': HTMLElement,
        'preview': HTMLElement,
        'preview-image': NerdyImageField['previewImage'],
    };

    get dropspace(): HTMLDivElement { return (this.$ || {})['dropspace']; }

    get previewImage(): HTMLImageElement { return (this.$ || {})['preview-image']; }
    set previewImage(image) { if (image instanceof HTMLImageElement) this.previewImage.src = image.src; }

    _lastDrag: { type?: string, timeout?: any } = {};
    _targets = new Set<HTMLElement>();

    constructor() { super(); this._dragstart = this._dragstart.bind(this); }

    attached() {
        const { dropspace, previewImage } = this;
        if (dropspace && previewImage) {
            dropspace.style.height = window.getComputedStyle(dropspace).height;
            previewImage.onload = () => (dropspace.style.height = 'auto', dropspace.style.height = window.getComputedStyle(dropspace) as any);
        }
    }

    async getImage() {
        const { previewImage } = this;
        return previewImage ? await new Promise<NerdyImageField['previewImage']>((resolve, reject) => {
            const image = previewImage.cloneNode(true) as this['previewImage'];
            if (image) image.onload = () => resolve(image);
        }) : undefined;
    }

    protected _dragover?(event: DragEvent); protected _drop?(event: DragEvent);
    protected _dragenter?(event: DragEvent); protected _dragleave?(event: DragEvent);
    protected _drag(event: DragEvent) { }

    protected _dragstart(event: DragEvent) {
        const { src, file } = this, { dataTransfer } = event || {} as DragEvent;
        const setPublic = false;
        if (src && dataTransfer) { // this.file && this.file.type) {
            if (!this.dragImage) this.dragImage = new Image();
            this.dragImage.src = this.previewImage.src;
            dataTransfer.setDragImage(this.dragImage, 0, 0);
            dataTransfer.effectAllowed = 'copy', dataTransfer.dropEffect = 'copy';
            const html = `<meta http-equiv="Content-Type" content="text/html;charset=UTF-8">${this.dragImage.outerHTML}`;
            dataTransfer.setData('text/html', html);
            dataTransfer.setData('text/plain', src);
            if (setPublic) {
                dataTransfer.setData('public.html', html); // setPublic &&
                dataTransfer.setData('public.utf8-plain-text', src); // setPublic &&
            }
            // event.preventDefault && event.preventDefault(), event.stopImmediatePropagation && event.stopImmediatePropagation(); // event.stopPropagation(); //
            // if (file) dataTransfer.setData('Files', file as any);
            // dataTransfer.setData('text/html', '<meta http-equiv="Content-Type" content="text/html;charset=UTF-8">' + this.dragImage.outerHTML);
        }
        // if (!(event instanceof Event)) return true;

        this._parseDragEvent(event);
        // event.stopImmediatePropagation && event.stopImmediatePropagation();
        return true;
    }

    // tslint:disable-next-line:cyclomatic-complexity
    protected _parseDragEvent(event: DragEvent = {} as any) {
        const data: dropspace.Data = {} as any, { type: eventType, dataTransfer = null } = event || { type: undefined };
        if (dataTransfer) dataTransfer.effectAllowed = 'copy', dataTransfer.dropEffect = 'copy';
        if (eventType === 'drop' && dataTransfer) {
            const allowedTypes = ['text/html', 'image/tiff', 'image/png', 'text/plain', 'public.html', 'public.url', 'public.html'];
            const typedData = data.typedData = {};
            const types = [...dataTransfer.types || []].reverse();
            //     const imageTypes: string[] = types.filter(type => /^(image)\//.test(type));
            //     const textTypes: string[] = types.filter(type => /^(text)\//.test(type));
            //     const publicTypes: string[] = types.filter(type => /^(public)\.(url)/.test(type));
            //     const allowedTypes: string[] = [...publicTypes, 'application/x-webarchive', 'com.apple.webarchive']; //  [...imageTypes, ...textTypes, ...publicTypes]; // [... dataTransfer.types || []].filter(type => /^(text|image|public)[\/\.](html|plain|png|tiff|url|utf)/.test(type));
            //     debugger;
            for (const type of allowedTypes) { // [... dataTransfer.types || []] as string[]) {
                types.includes(type) && (typedData[type] = dataTransfer.getData(type)); // 'text');
            }
        }
        dropspace.parse(event, data);
        const { type, files = [], src } = data;
        // if (type !== 'drag' && type !== 'dragstart') stopEvent(event);
        const $dropspace = event['dropspace'] || this.dropspace;
        let force = false;
        switch (type) {
            case 'drop':
                $dropspace.classList.remove('dragover');
                this.file = null;
                if (files.length) this._setFile(files[0]);
                else if (src) this._setSrc(src);
                else force = true; // this._logDragEvent(data, 'force'); // debugger;
                // (files.length ? this._setFile(files[0]) : this._setSrc(src)) || this._logDragEvent(data, 'force');
                break;
            case 'dragenter':
                $dropspace.classList.add('dragover');
                break;
            case 'dragend':
                $dropspace.classList.remove('dragover');
                this.dropspace.classList.remove('dragover');
                this['_draggingFrom'] = false;
            case 'dragleave':
                this.dropspace.classList.remove('dragover');
                $dropspace.classList.remove('dragover'); // !this['_draggingFrom'] &&
                break;
            case 'dragstart':
            case 'drag':
                $dropspace.classList.add('dragover'); // || this._logDragEvent(data);
                break;
            case 'dragover':
                this.dropspace.classList.remove('dragover');
                $dropspace.classList.add('dragover');
                break;
            default: return false;
        }
        if (!(event instanceof Event)) return;
        if (force || eventType === 'drop') this._logDragEvent(data, force);
        if (type !== 'drag' && type !== 'dragstart') event.preventDefault(); // stopEvent(event);
        return true;
    }

    protected _logDragEvent(data: dropspace.Data = {}, force: 'force' | boolean = false) {
        const { type, types = [], files, event } = data, { _lastDrag: last } = this;
        if (!force && (!type || type === last.type)) return;
        clearTimeout(last.timeout), last.timeout = timeout(() => (delete last.type, delete last.timeout));
        // const scope = { field: this, types: types.join(', '), files, data };
        // const dataType = (types || [])[0];
        // if (/text/.test(dataType)) {
        //     const text = scope['text'] = event.dataTransfer.getData(dataType);
        //     if (dataType === 'text/html') {
        //         const src = scope['src'] = (/(?: src\=["])(.*?)(?:["])/.exec(text) || '')[1];
        //     }
        //     // scope['items'] = Array.from(event.dataTransfer.items).map(item => (item['file'] = item.getAsFile(), item));
        // }
        // console.trace(`_${last.type = type}`, scope);
        console.log(data); // '%s %o',
    }

    protected _setSrc(src: string) {
        // this.log('_setSrc', src.truncate(25));
        if (!this.file && typeof src === 'string') {
            if (uri.isDataURI(src)) {
                const attributes = uri.Attributes.parse(src);
                const { type = 'url/data', name = `arbitrary ${/^image/.test(type) ? type.split('/').resverse().join(' ') : type.split('/')[0]}` } = attributes;
                // const file = this.file = { name, type };
                const file = this.file = new dropspace.ImageFile(src); // console.log({ file }); // if (file) return dropspace.readImage(this.file = file, this);
                // this.log('_setSrc', { attributes, type, name, file });
            }
        }
        if (src || this.src !== src) this.src = src;
        return src;
    }

    protected _setFile(file: File) {
        if (file && typeof file === 'object') return this.src = '', dropspace.readImage(this.file = file, this), true;
    }
}

for (const method of ['_drop', '_dragover', '_dragenter', '_dragleave'])
    NerdyImageField.prototype[method] = NerdyImageField.prototype['_parseDragEvent'];

Components.define(NerdyImageField);

export default NerdyImageField;
export { default as PaperImageField } from './nerdy-paper-image-field';

