const { round } = Math;
export let canvasSize: number = 2 ** 11;

/* CANVAS SIZES */
const { hardwareConcurrency = 1 } = typeof navigator !== 'undefined' ? navigator : {} as Navigator;
const { height: screenHeight, width: screenWidth } = typeof screen !== 'undefined' ? screen : {} as Window['screen'], screenSize = Math.max(screenWidth || 480, screenHeight || 480);
const canvasSizes = [128, 256];
(hardwareConcurrency > 2 || 512 <= screenSize) && (
    canvasSizes.push(512),
    (hardwareConcurrency > 3 || 1024 <= screenSize) && canvasSizes.push(1024),
    hardwareConcurrency > 4 && canvasSizes.push(2048)
);

