import { Components, PolymerElement } from '~/framework';
import { Style, style, flex, ease, horizontal } from '../components/styles';
import { round, floor, ceil, diagonal, Bound, Reflection, imul, max, min } from 'modules/nerdy-utils';

const notify = true, reflectToAttribute = true;

export class NerdyImageGrid extends PolymerElement {
    static style: style = {
        // '#preview': { $MaxImageHeight: 'var(--max-image-height, 25px)' },
        ':root': { $Foreground: 'rgba(31, 31, 31, 0.5)', $Background: 'transparent' }, // as styling,
        ':host': { ...flex['center'], ...ease },
        '*': { ...ease },
        '#container': {
            ...flex['center'], ...horizontal,
            minWidth: '5em', minHeight: '5em', height: 'auto',
        },
        '#container > *': {
            position: 'absolute', display: 'block',
            top: 0, left: 0, width: '100%', height: '100%',
        },
        '#container > :first-child': { position: 'relative' },
        '#image': {
            $MaxImageHeight: 'var(--max-image-height, 100vh)',
            maxHeight: 'var(--max-image-height, 100vh) !important',
        },
        '*[not-visible]': { visibility: 'hidden' },
    };
    static properties = {
        src: { type: String, notify, reflectToAttribute }, // observer: '_setSrc'
        rows: { type: Number, notify, reflectToAttribute },
        columns: { type: Number, notify, reflectToAttribute },
    };

    static template = () =>
        <template>
            <Style>{NerdyImageGrid.style}</Style>
            <div id="container">
                {/* <img id="image" bind-src='[[src]]'></img> */}
                <canvas id="canvas" style='display: none; pointer-events: none;'></canvas>
                <div id="grid"></div>
            </div>
        </template>

    public image = new Image(); public src: string;
    public rows: number = 10; public columns: number = 10;
    _context: CanvasRenderingContext2D;

    get container() { return this.$.container as HTMLDivElement; }
    get canvas() { return this.$.canvas as HTMLCanvasElement; }
    get context() { return this._context || (this._context = this.canvas.getContext('2d')); }

    constructor() {
        super();
        this.image.onload = () => this.slice;
    }

    slice() {
        const {image, canvas, context} = this;

    }
}

Components.define(NerdyImageGrid);

export default NerdyImageGrid;
