import { getConstructor } from './native-elements';

/**
 * QuasiElements
 *
 * Quasi Elements retain the original constructor when cloned
 * so if needed, must redefine cloneNode:
 *
 *  class XElement {
 *      cloneNode(deep: boolean) {
 *          return Object.setPrototypeOf(super.cloneNode(deep), Template.prototype);
 *      }
 *  }
 *
 *  const t = new XElement(), t2 = t.cloneNode(true); console.log({t, t2});
 *
 */

export interface QuasiElementConstructor<T = HTMLElement, P extends HTMLElement = T & HTMLElement> extends Constructor<HTMLElement> { // CustomElement.Constructor<CustomElement.Prototype & T> {
    tagName: string;
    isDefined?: boolean;
    whenDefined?: Promise<T>;
    prototype: P;
    constructor: QuasiElementConstructor<P>;
    new(): P;
    new(tagName: string, options?: ElementDefinitionOptions): P;
}

export const whenDefined = <T = HTMLElement>(tagName: string, prototype?: T | HTMLElement) => new Promise<T>((resolve, reject) => {
    if (/\-/.test(tagName)) return window.customElements.whenDefined(tagName).then(() => resolve(window.customElements.get(tagName)));

    const constructor = tagName && ((prototype && prototype.constructor) || getConstructor(tagName as any) || document.createElement(tagName)['constructor']); // || HTMLUnknownElement;
    constructor ? resolve(constructor) : reject(ReferenceError(`Cannot resolve a constructor for '${tagName}' element tag.`));
});

export const Constructor = <T = HTMLElement>(tagName: string, prototype?: T | HTMLElement, options: ElementDefinitionOptions = {} as any): QuasiElementConstructor<T> => {
    tagName = tagName.replace(/([a-z](?=[A-Z])|\D(?=\d)|[A-Z](?=[A-Z][a-z]))/g, '$1-').toLowerCase();
    const constructor = class {
        static tagName = tagName;
        static options = options;
        static isDefined = false;
        static whenDefined = whenDefined<T>(tagName, prototype).then(constructor => (constructor['isDefined'] = true, constructor));
        static [Symbol.hasInstance](object: any) { return object instanceof HTMLElement && object.localName === this.tagName; }

        constructor();
        constructor(tagName = constructor.tagName, options = constructor.options) {
            const element = document.createElement(tagName);
            if (element.constructor === HTMLElement) element.setAttribute('not-defined', ''), constructor.whenDefined.then(() => (element.removeAttribute('not-defined'), element.setAttribute('has-been-defined', '')));
            else element.setAttribute('is-defined', '');
            return element;
        }
    };

    Object.setPrototypeOf(constructor.prototype, (prototype || HTMLElement.prototype) as object);

    return constructor as any as QuasiElementConstructor<T>;
};

export class Template extends (Constructor<HTMLTemplateElement>('template', HTMLTemplateElement.prototype) as typeof HTMLTemplateElement) { // implements Partial<HTMLTemplateElement> {
    static isTemplate = (object: any): object is HTMLTemplateElement => object && object instanceof HTMLTemplateElement;

    constructor() { super(); Object.setPrototypeOf(this, Template.prototype); }

    append(content: Node | ArrayLike<Node> | HTMLTemplateElement | DocumentFragment) {
        if ((content || 0)['length'] > 0) for (const child of Array.from(content as NodeList)) this.content.appendChild(child); // Array.from(content as NodeList).forEach((node) => this.content.appendChild(node));
        else if (typeof content === 'object') content instanceof HTMLTemplateElement ? this.append(content.content.childNodes)
            : content instanceof DocumentFragment ? this.append(content.childNodes)
                : content instanceof Node && this.append([content]);
    }

    clear() { for (const child of Array.from(this.content.childNodes)) this.content.removeChild(child); }

    replace(content: Node | ArrayLike<Node> | HTMLTemplateElement | DocumentFragment) { this.clear(), this.append(content); }

}

export const Canvas = Constructor<Canvas>('canvas');
export type Canvas = HTMLCanvasElement;
