import { Components } from '../core';
import { NerdyElement } from './nerdy-element';
import Styles, { StylesObject, StyleDeclaration, styling, style } from '../styles';

import { $entry } from 'modules/nerdy-utils'; $entry(__filename || 'nerdy-elements/components/style-element.tsx');

export { StylesObject, StyleDeclaration, styling, style };

export declare type HTMLElementStyle = HTMLElement['style'];

const styleObjects: HTMLElementStyle[] = [],
    createStyleObject = () => document.createElement('element').style,
    requestStyleObject = (): HTMLElementStyle => styleObjects.pop() || createStyleObject(),
    returnStyleObject = (object: HTMLElementStyle) => styleObjects.push(object);

export { createStyleObject };

export const parseValue: (value: any) => string = (value, type = typeof value) => (
    `${type === 'string' || type === 'number' ? value : type === 'function' ? value()
        : type === 'object' && Array.isArray(value) ? value.map(parseValue).join(', ') : ''}`
);

export const normalizeProperty = (property) => property.replace(/^\$/, '--').replace(/([a-z]|^)([A-Z])/g, '$1-$2').toLocaleLowerCase();

export const setProperty = (styleObject: HTMLElementStyle, property: string, value?: any, priority: boolean | string = /\!important/.test(value) ? 'important' : '') => (
    property === 'content' && (value = `${value || ''}`.trim().replace(/^"?(.*?)"?$/, '"$1"')),
    property in styleObject || priority === true || priority === 'important'
        ? styleObject.setProperty(property = normalizeProperty(property), !priority ? value : `${value}`.replace(/\!important/, '').trim(), priority === true || priority === 'important' ? 'important' : '')
        : (styleObject[property] = value),
    property
);

export const generateStyleText = (styles: StylesObject | string = {}) => {
    const strings: string[] = [];
    if (typeof styles === 'object') {
        const object: HTMLElementStyle = requestStyleObject();
        for (const selector in styles) {
            const style = styles[selector], properties = []; // const { $ = '', ...style }
            // selector = selector.replace(/\$/g, ':');
            if (typeof style === 'object') {
                APPLY: for (const property in style) properties.push(setProperty(object, property, parseValue(style[property])));
                strings.push(`${selector} { ${`${object.cssText}`.trim()} }`); // ${$ || ''}
                RESET: for (const property of properties) object.removeProperty(property); // /^--/.test(property) ? object[property] = '' : object.removeProperty(property);
            } else if (typeof style === 'string') strings.push(`${selector} { ${(style as string).trim()} }`); // ${$ || ''}
        }
        returnStyleObject(object);
    } else if (typeof styles === 'string') return styles;
    // let string = strings.join('\n');
    // string = string.replace(/position\: sticky;/g, 'position\: -webkit-sticky; position\: sticky;');
    return strings.join('\n'); // Only return fully reset styleObject to pool (avoid try-catch mess)!
};

export abstract class AbstractNerdyStyle extends HTMLStyleElement {
    static normalizeProperty = normalizeProperty;
    static generateStyleText = generateStyleText;
    static parseValue = parseValue;
    static setProperty = setProperty;
    appendObject(object: any): any {
        if (object instanceof HTMLElement) return this.appendChild(object);
        if (object instanceof Text) return this.appendChild(object);
        // const { textContent = '' } = this;
        const text = generateStyleText(object as StylesObject).trim() || ''; // (typeof object === 'string' && object) || (typeof object === 'object' && generateStyleText(object as StylesObject)) || undefined;
        if (text) this.appendChild(document.createTextNode(text));
        // if (!text || textContent.indexOf(text) > -1) return;
        // // if (typeof object === 'string' && textContent.indexOf(object) > -1) return;
        // this.textContent = [textContent.trim() || '', text].join('\n\n').trim();
        // // this.textContent = !text || typeof text !== 'string' ? textContent : [textContent || '', text].join('\n\n').trim();
        // // const scope = { object, text, inital: textContent, final: this.textContent };
        // // (this['[APPENDS]'] = this['[APPENDS]'] || []).push(scope);
        // console.log('AbstractNerdyStyle.appendObject()', this.textContent);
        // if (typeof text === 'string') this.textContent = [...[this.textContent] || [], text].join('\n\n').trim();
    }
}

const prototype = { appendObject: AbstractNerdyStyle.prototype.appendObject };

const NerdyStyle = class NerdyStyle implements Partial<AbstractNerdyStyle> { // implements Partial<HTMLStyleElement>,
    constructor() {
        // tslint:disable-next-line: prefer-object-spread
        const element = Object.assign(document.createElement('style') as HTMLStyleElement, prototype);
        // console.log('NerdyStyle#constructor()', { element, prototype });
        return element;
    }
    // appendObject(object: any) {
    //     // console.log(this,)
    //     if (object instanceof HTMLElement) return this['appendChild'] && this['appendChild'](object);
    //     // else AbstractNerdyStyle.prototype.appendObject.call(this, object);
    // }
} as any as typeof HTMLStyleElement & { prototype: HTMLStyleElement & AbstractNerdyStyle } & typeof AbstractNerdyStyle;

// TODO: Use Object.setPrototypeOf instead for NerdyStyle#constructor
Object.setPrototypeOf(NerdyStyle, AbstractNerdyStyle);
