
export declare type Method = 'log' | 'error' | 'warn' | 'info';
export declare type Methods = 'log' & 'error' & 'warn' & 'info';
export declare interface StyleLoggerOptions<M extends Method = Methods> {
    type?: M; args?: any[];
    prefix?: string; source?: object;
    format?: string; style?: string;
    color?: string; background?: string;
    padding?: string | number; fontSize?: string | number;
}

declare global {
    type logger = Method;
    type Logger<M extends Method = Methods, T = object> = InstanceLogger<M> & {[Key in keyof T]?: InstanceLogger<M> };
    type LoggerOptions<M extends Method = Methods> = StyleLoggerOptions<M>;
}

export const styles = {
    background: 'lightgray', backgrounds: { log: 'lightgray', info: '#9CF', warn: '#CC3', error: 'red' },
    color: 'black', colors: { log: 'white', info: 'white', warn: 'white', error: 'white' },
    padding: '0.125em 0.25em',
    fontSize: '90%',
};

export declare interface LoggerFunction { (message?: any, ...optionalParams: any[]): void; } // tslint:disable-line:callable-types

export declare interface StyleLogger<M extends Method = Methods> extends LoggerFunction { loggers?: Map<string, StyleLogger>; args?: any[]; } // options?: Partial<StyleLoggerOptions<M>>;

export declare interface InstanceLogger<M extends Method = Methods> extends StyleLogger<M> {
    (message?: any, ...optionalParams: any[]): void;
    loggers?: StyleLogger['loggers'] & any; args?: StyleLogger['args'] | & any;
    [name: string]: StyleLogger<M>;
}

const errorFormat = /Chrome/.test(navigator.appVersion) ? '%o' : /Safari/.test(navigator.appVersion) ? '– %s' : '%s';

export function createStyledLogger<M extends Method>(options: StyleLoggerOptions<M> = {}, ...args: any[]) {
    const {
        type = 'log', prefix = '',
        color = styles.colors[type] || styles.color,
        background = styles.backgrounds[type] || styles.background,
        padding = styles.padding, fontSize = styles.fontSize,
        style = `color:${color};background:${background};padding:${padding};font-size:${fontSize};font-family:sans-serif;margin-right: 0.5em;`,
        source = {},
        format = ((style && prefix ? `%c${prefix}%c ` : style ? `%c ` : ' ') + (type === 'error' ? errorFormat : '')).trim(), //  + ' '
    } = options;
    const method = console[type], bindings: any[] = [format || null, style || null, style ? '' : null].filter(arg => typeof arg === 'string');
    const logger: StyleLogger<typeof type> = method.bind(console, ...bindings, ...args), loggers = logger.loggers = new Map();
    const sublogger = (id: string, postfix?: string) => !id ? undefined : loggers.get(id) || (
        loggers.set(id, createStyledLogger({
            ...options, prefix: (
                postfix = postfix || typeof source[id] === 'function' ? `#${id}` : `${id}`
            ) ? (/^[a-z0-9\_]/i.test(postfix) ? `${prefix}:${postfix}`
                : `${prefix}${postfix}`) : prefix,
        }, ...args || [])).get(id)
    );
    const proxy = new Proxy(logger, {
        get(target, property: PropertyKey) {
            return logger[property] !== undefined ? logger[property] : (logger[property] = sublogger(property as string));
        },
    });
    return proxy;
}

export default createStyledLogger;
